export default {"path":"/XtableDemo/edit/Insert","name":"XtableDemo.edit.Insert","head":{"label":"XtableDemo.edit.Insert.label","description":"XtableDemo.edit.Insert.description"},"samples":[{"id":"XtableDemo.edit.Insert.sample1","label":"XtableDemo.edit.Insert.sample1.label","description":"XtableDemo.edit.Insert.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent()\">1</vue-button>\n    <vue-button @click=\"insertEvent(tableData[2])\">3 Sex </vue-button>\n    <vue-button @click=\"insertEvent(-1)\"></vue-button>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  show-overflow\n  ref=\"xTable\"\n  max-height=\"400\"\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable :edit-render=\"{name: 'input', defaultValue: ''}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'select', options: sexList}\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" sortable :edit-render=\"{name: 'input', defaultValue: 18}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      sexList: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 4)\n    this.findSexList()\n  },\n\n  methods: {\n    findSexList () {\n      return Vue.http.get('/api/conf/sex/list').then(data => {\n        this.sexList = data.data\n      })\n    },\n    insertEvent (row) {\n      let record = {\n        sex: '1'\n      }\n      this.$refs.xTable.insertAt(record, row)\n        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'sex'))\n    },\n    getInsertEvent () {\n      let insertRecords = this.$refs.xTable.getInsertRecords()\n      this.$alert({ message: insertRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent()\">1</vue-button>\n        <vue-button @click=\"insertEvent(tableData[2])\">3 Sex </vue-button>\n        <vue-button @click=\"insertEvent(-1)\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      show-overflow\n      ref=\"xTable\"\n      max-height=\"400\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable :edit-render=\"{name: 'input', defaultValue: ''}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'select', options: sexList}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" sortable :edit-render=\"{name: 'input', defaultValue: 18}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      sexList: [],

    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 4)
    this.findSexList()
  },

  methods: {
    findSexList () {
      return Vue.http.get('/api/conf/sex/list').then(data => {
        this.sexList = data.data
      })
    },
    insertEvent (row) {
      let record = {
        sex: '1'
      }
      this.$refs.xTable.insertAt(record, row)
        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'sex'))
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    }
  }
}}],"styles":""}