export default {"path":"/XtableDemo/advanced/Page","name":"XtableDemo.advanced.Page","head":{"label":"XtableDemo.advanced.Page.label","description":"XtableDemo.advanced.Page.description"},"samples":[{"id":"XtableDemo.advanced.Page.sample1","label":"XtableDemo.advanced.Page.sample1.label","description":"XtableDemo.advanced.Page.sample1.description","code":"<vue-xtable\n  border\n  show-overflow\n  height=\"200\"\n  row-id=\"id\"\n  :loading=\"loading\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column type=\"index\" title=\"\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"rate\" title=\"Rate\"></vue-xtable-column>\n</vue-xtable>\n\n<vue-xtable-pager\n  border\n  :loading=\"loading\"\n  :current-page=\"tablePage.currentPage\"\n  :page-size=\"tablePage.pageSize\"\n  :total=\"tablePage.totalResult\"\n  :layouts=\"['PrevPage', 'JumpNumber', 'NextPage', 'FullJump', 'Sizes', 'Total']\"\n  @page-change=\"handlePageChange\">\n</vue-xtable-pager>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableData: [],\n      tablePage: {\n        currentPage: 1,\n        pageSize: 10,\n        totalResult: 0\n      },\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      Vue.http.get(`/api/user/page/list/${this.tablePage.pageSize}/${this.tablePage.currentPage}`, this.formData).then(response => {\n        let { page, result } = response.data\n        this.tableData = result\n        this.tablePage.totalResult = page.totalResult\n        this.loading = false\n      }).catch(e => {\n        this.loading = false\n      })\n    },\n    handlePageChange ({ currentPage, pageSize }) {\n      this.tablePage.currentPage = currentPage\n      this.tablePage.pageSize = pageSize\n      this.findList()\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      show-overflow\n      height=\"200\"\n      row-id=\"id\"\n      :loading=\"loading\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" title=\"\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"rate\" title=\"Rate\"></vue-xtable-column>\n    </vue-xtable>\n\n    <vue-xtable-pager\n      border\n      :loading=\"loading\"\n      :current-page=\"tablePage.currentPage\"\n      :page-size=\"tablePage.pageSize\"\n      :total=\"tablePage.totalResult\"\n      :layouts=\"['PrevPage', 'JumpNumber', 'NextPage', 'FullJump', 'Sizes', 'Total']\"\n      @page-change=\"handlePageChange\">\n    </vue-xtable-pager></div>","parameter": {
