export default {"path":"/XtableDemo/edit/Status","name":"XtableDemo.edit.Status","head":{"label":"XtableDemo.edit.Status.label","description":"XtableDemo.edit.Status.description"},"samples":[{"id":"XtableDemo.edit.Status.sample1","label":"XtableDemo.edit.Status.sample1.label","description":"XtableDemo.edit.Status.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent\"></vue-button>\n    <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n    <vue-button @click=\"getRemoveEvent\"></vue-button>\n    <vue-button @click=\"getUpdateEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  show-overflow\n  ref=\"xTable\"\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'cell', showStatus: true}\">\n  <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date3\" title=\"Date\" formatter=\"formatDate\"></vue-xtable-column>\n  <vue-xtable-column title=\"\" width=\"200\">\n    <template v-slot=\"scope\">\n      <template v-if=\"!scope.row.date3\">\n        <vue-button @click=\"saveEvent2(scope.row)\" :loading=\"scope.row.loading\"></vue-button>\n      </template>\n      <template v-else-if=\"scope.rowIndex % 2 === 0\">\n        <vue-button @click=\"saveEvent(scope.row)\" :loading=\"scope.row.loading\"></vue-button>\n      </template>\n      <template v-else>\n        <vue-button type=\"primary\" @click=\"saveEvent(scope.row, 'name')\" :loading=\"scope.row.loading\"> Name </vue-button>\n      </template>\n    </template>\n  </vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n  },\n\n  methods: {\n    insertEvent () {\n      this.$refs.xTable.insert()\n        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'name'))\n    },\n    saveEvent (row, field) {\n      let xTable = this.$refs.xTable\n      if (xTable.isUpdateByRow(row)) {\n        row.loading = true\n        this.submitSave(row).then(data => {\n          //  record \n          xTable.reloadRow(row, null, field)\n          this.$notify.success({ message:''});\n          row.loading = false\n        })\n      } else {\n        this.$notify.info({ message:''});\n      }\n    },\n    saveEvent2 (row, field) {\n      let xTable = this.$refs.xTable\n      if (xTable.isUpdateByRow(row)) {\n        row.loading = true\n        this.submitSave(row).then(data => {\n          // \n          xTable.reloadRow(row, data, field)\n          this.$notify.success({ message:''});\n          row.loading = false\n        })\n      } else {\n        this.$notify.info({ message:''});\n      }\n    },\n    submitSave (row) {\n      return new Promise(resolve => {\n        let rest = {\n          date3: VueUtil.formatDate(new Date())\n        }\n        if (row.name) {\n          rest.name = row.name\n        }\n        if (row.sex) {\n          rest.sex = row.sex\n        }\n        setTimeout(() => resolve(rest), 500)\n      })\n    },\n    getInsertEvent () {\n      let insertRecords = this.$refs.xTable.getInsertRecords()\n      this.$alert({ message: insertRecords.length })\n    },\n    getRemoveEvent () {\n      let removeRecords = this.$refs.xTable.getRemoveRecords()\n      this.$alert({ message: removeRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTable.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent\"></vue-button>\n        <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      show-overflow\n      ref=\"xTable\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'cell', showStatus: true}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date3\" title=\"Date\" formatter=\"formatDate\"></vue-xtable-column>\n      <vue-xtable-column title=\"\" width=\"200\">\n        <template v-slot=\"scope\">\n          <template v-if=\"!scope.row.date3\">\n            <vue-button @click=\"saveEvent2(scope.row)\" :loading=\"scope.row.loading\"></vue-button>\n          </template>\n          <template v-else-if=\"scope.rowIndex % 2 === 0\">\n            <vue-button @click=\"saveEvent(scope.row)\" :loading=\"scope.row.loading\"></vue-button>\n          </template>\n          <template v-else>\n            <vue-button type=\"primary\" @click=\"saveEvent(scope.row, 'name')\" :loading=\"scope.row.loading\"> Name </vue-button>\n          </template>\n        </template>\n      </vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)
  },

  methods: {
    insertEvent () {
      this.$refs.xTable.insert()
        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'name'))
    },
    saveEvent (row, field) {
      let xTable = this.$refs.xTable
      if (xTable.isUpdateByRow(row)) {
        row.loading = true
        this.submitSave(row).then(data => {
          //  record 
          xTable.reloadRow(row, null, field)
          this.$notify.success({ message:''});
          row.loading = false
        })
      } else {
        this.$notify.info({ message:''});
      }
    },
    saveEvent2 (row, field) {
      let xTable = this.$refs.xTable
      if (xTable.isUpdateByRow(row)) {
        row.loading = true
        this.submitSave(row).then(data => {
          // 
          xTable.reloadRow(row, data, field)
          this.$notify.success({ message:''});
          row.loading = false
        })
      } else {
        this.$notify.info({ message:''});
      }
    },
    submitSave (row) {
      return new Promise(resolve => {
        let rest = {
          date3: VueUtil.formatDate(new Date())
        }
        if (row.name) {
          rest.name = row.name
        }
        if (row.sex) {
          rest.sex = row.sex
        }
        setTimeout(() => resolve(rest), 500)
      })
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    },
    getRemoveEvent () {
      let removeRecords = this.$refs.xTable.getRemoveRecords()
      this.$alert({ message: removeRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTable.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    }
  }
}}],"styles":""}