    parameter: {
      data: function () {
        return {
          tableData: []
        }
      },
      methods: {
        getData: function () {
          var tableData = this.tableData;
          var i = 100;
          while (i--) {
            var column = {};
            for (var j = 1; j <= 5; j++) {
              column['column' + j] = (Math.random() * 100).toFixed(2);
            }
            tableData.push(column);
          }
        },
        aggregate: function (item) {
          var result;
          switch (item) {
            case 1:
              result = 'sum';
              break;
            case 2:
              result = 'average';
              break;
            case 3:
              result = 'min';
              break;
            case 4:
              result = 'max';
              break;
            case 5:
              result = 'count';
              break;
          }
          return result;
        }
      },
      mounted: function () {
        this.getData();
      }
    },
