export default {"path":"/XtableDemo/scroll/CellValid","name":"XtableDemo.scroll.CellValid","head":{"label":"XtableDemo.scroll.CellValid.label","description":"XtableDemo.scroll.CellValid.description"},"samples":[{"id":"XtableDemo.scroll.CellValid.sample1","label":"XtableDemo.scroll.CellValid.sample1.label","description":"XtableDemo.scroll.CellValid.sample1.description","code":"<vue-toolbar :refresh=\"{query: findList}\">\n  <template v-slot:buttons>\n    <vue-dropdown>\n      <vue-button><i class=\"vue-icon-arrow-down vue-icon--right\"></i></vue-button>\n      <vue-dropdown-menu slot=\"dropdown\">\n        <vue-dropdown-item @click.native=\"validEvent\"></vue-dropdown-item>\n        <vue-dropdown-item @click.native=\"fullValidEvent\"></vue-dropdown-item>\n        <vue-dropdown-item @click.native=\"selectValidEvent\"></vue-dropdown-item>\n      </vue-dropdown-menu>\n    </vue-dropdown>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  resizable\n  show-overflow\n  ref=\"xTable\"\n  height=\"300\"\n  :loading=\"loading\"\n  :edit-rules=\"validRules\"\n  :edit-config=\"{trigger: 'click', mode: 'cell', showStatus: true}\"\n  :checkbox-config=\"{checkField: 'checked'}\">\n  <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"rate\" title=\"Rate\" width=\"200\"></vue-xtable-column>\n  <vue-xtable-column field=\"region\" title=\"Region\" width=\"200\"></vue-xtable-column>\n  <vue-xtable-column field=\"time\" title=\"Time\" width=\"200\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" width=\"300\" show-overflow></vue-xtable-column>\n  <vue-xtable-column field=\"updateTime\" title=\"UpdateTime\" width=\"200\"></vue-xtable-column>\n  <vue-xtable-column field=\"createTime\" title=\"CreateTime\" width=\"200\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      validRules: {\n        name: [\n          { required: true, message: '' },\n          { min: 3, max: 50, message: ' 3  50 ' }\n        ],\n        sex: [\n          { required: true, message: '' }\n        ]\n      },\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      return new Promise(resolve => {\n        setTimeout(() => {\n          let tableData = window.MOCK_DATA_LIST.slice(0, 1000)\n          //  vue  vue \n          if (this.$refs.xTable) {\n            this.$refs.xTable.loadData(tableData)\n          }\n          resolve()\n          this.loading = false\n        }, 300)\n      })\n    },\n    validEvent () {\n      this.$refs.xTable.validate(valid => {\n        if (valid) {\n          this.$notify.success({ message:''});\n        } else {\n          this.$notify.error({ message:''});\n        }\n      })\n    },\n    fullValidEvent () {\n      this.$refs.xTable.fullValidate((valid, errMap) => {\n        if (valid) {\n          this.$notify.success({ message:''});\n        } else {\n          let msgList = []\n          Object.values(errMap).forEach(errList => {\n            errList.forEach(params => {\n              let { rowIndex, column, rules } = params\n              rules.forEach(rule => {\n                msgList.push(` ${rowIndex}  ${column.title} ${rule.message}`)\n              })\n            })\n          })\n          var h = this.$createElement;\n          this.$alert({\n            type: \"error\",\n            message: h('div',[msgList.map(function (msg) {\n              return h(\"div\", [msg]);\n            })])\n          });\n        }\n      })\n    },\n    selectValidEvent () {\n      let selectRecords = this.$refs.xTable.getSelectRecords()\n      if (selectRecords.length > 0) {\n        this.$refs.xTable.validate(selectRecords, valid => {\n          if (valid) {\n            this.$notify.success({ message:''});\n          } else {\n            this.$notify.error({ message:''});\n          }\n        })\n      } else {\n        this.$notify.warning({ message:''});\n      }\n    },\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :refresh=\"{query: findList}\">\n      <template v-slot:buttons>\n        <vue-dropdown>\n          <vue-button><i class=\"vue-icon-arrow-down vue-icon--right\"></i></vue-button>\n          <vue-dropdown-menu slot=\"dropdown\">\n            <vue-dropdown-item @click.native=\"validEvent\"></vue-dropdown-item>\n            <vue-dropdown-item @click.native=\"fullValidEvent\"></vue-dropdown-item>\n            <vue-dropdown-item @click.native=\"selectValidEvent\"></vue-dropdown-item>\n          </vue-dropdown-menu>\n        </vue-dropdown>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      show-overflow\n      ref=\"xTable\"\n      height=\"300\"\n      :loading=\"loading\"\n      :edit-rules=\"validRules\"\n      :edit-config=\"{trigger: 'click', mode: 'cell', showStatus: true}\"\n      :checkbox-config=\"{checkField: 'checked'}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"rate\" title=\"Rate\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"region\" title=\"Region\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"time\" title=\"Time\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" width=\"300\" show-overflow></vue-xtable-column>\n      <vue-xtable-column field=\"updateTime\" title=\"UpdateTime\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"createTime\" title=\"CreateTime\" width=\"200\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      validRules: {
        name: [
          { required: true, message: '' },
          { min: 3, max: 50, message: ' 3  50 ' }
        ],
        sex: [
          { required: true, message: '' }
        ]
      },

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      this.loading = true
      return new Promise(resolve => {
        setTimeout(() => {
          let tableData = window.MOCK_DATA_LIST.slice(0, 1000)
          //  vue  vue 
          if (this.$refs.xTable) {
            this.$refs.xTable.loadData(tableData)
          }
          resolve()
          this.loading = false
        }, 300)
      })
    },
    validEvent () {
      this.$refs.xTable.validate(valid => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          this.$notify.error({ message:''});
        }
      })
    },
    fullValidEvent () {
      this.$refs.xTable.fullValidate((valid, errMap) => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          let msgList = []
          Object.values(errMap).forEach(errList => {
            errList.forEach(params => {
              let { rowIndex, column, rules } = params
              rules.forEach(rule => {
                msgList.push(` ${rowIndex}  ${column.title} ${rule.message}`)
              })
            })
          })
          var h = this.$createElement;
          this.$alert({
            type: "error",
            message: h('div',[msgList.map(function (msg) {
              return h("div", [msg]);
            })])
          });
        }
      })
    },
    selectValidEvent () {
      let selectRecords = this.$refs.xTable.getSelectRecords()
      if (selectRecords.length > 0) {
        this.$refs.xTable.validate(selectRecords, valid => {
          if (valid) {
            this.$notify.success({ message:''});
          } else {
            this.$notify.error({ message:''});
          }
        })
      } else {
        this.$notify.warning({ message:''});
      }
    },
  }
}}],"styles":""}