 [item.id]\n        if (!item.children || !item.children.length) {\n          let row = { }\n          item.keys.forEach((key, index) => {\n            let level = index + 1\n            let obj = keyMap[key]\n            row[`check_${level}`] = false\n            row[`id_${level}`] = obj.id\n            row[`name_${level}`] = obj.name\n          })\n          list.push(row)\n        }\n      }, options)\n      this.keyMap = keyMap\n      this.tableData = list\n    },\n    // \n    rowspanMethod ({ row, $rowIndex, column, data }) {\n      let fields = ['name_1', 'name_2', 'name_3']\n      let cellValue = row[column.property]\n      if (cellValue && VueUtil.includes(fields, column.property)) {\n        let prevRow = data[$rowIndex - 1]\n        let nextRow = data[$rowIndex + 1]\n        if (prevRow && prevRow[column.property] === cellValue) {\n          return { rowspan: 0, colspan: 0 }\n        } else {\n          let countRowspan = 1\n          while (nextRow && nextRow[column.property] === cellValue) {\n            nextRow = data[++countRowspan + $rowIndex]\n          }\n          if (countRowspan > 1) {\n            return { rowspan: countRowspan, colspan: 1 }\n          }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      height=\"600\"\n      :span-method=\"rowspanMethod\"\n      :data=\"tableData\">\n      <vue-xtable-column field=\"name_1\" title=\"\"></vue-xtable-column>\n      <vue-xtable-column field=\"name_2\" title=\"\"></vue-xtable-column>\n      <vue-xtable-column field=\"name_3\" title=\"\">\n        <template v-slot=\"scope\">\n          <vue-checkbox v-model=\"scope.row.check_3\" @change=\"check3ChangeEvent(row)\">{{ scope.row.name_3 }}</vue-checkbox>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"name_4\" title=\"\">\n        <template v-slot=\"scope\">\n          <vue-checkbox v-model=\"scope.row.check_4\" @change=\"check4ChangeEvent(row)\">{{ s