n    <vue-xtable\n      border\n      height=\"400\"\n      :loading=\"loading\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"\"></vue-xtable-column>\n      <vue-xtable-column field=\"role\" title=\"Role\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"\"></vue-xtable-column>\n      <vue-xtable-column field=\"rate\" title=\"Rate\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
