export default {"path":"/XtableDemo/grid/Dynamic","name":"XtableDemo.grid.Dynamic","head":{"label":"XtableDemo.grid.Dynamic.label","description":"XtableDemo.grid.Dynamic.description"},"samples":[{"id":"XtableDemo.grid.Dynamic.sample1","label":"XtableDemo.grid.Dynamic.sample1.label","description":"XtableDemo.grid.Dynamic.sample1.description","code":"<vue-xgrid\nborder\nresizable\nshow-overflow\nheight=\"306\"\n:pager-config=\"tablePage\"\n:proxy-config=\"tableProxy\"\n:columns=\"tableColumn\"\n:toolbar=\"tableToolbar\"\n:edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tablePage: {\n        pageSize: 5,\n        pageSizes: [5, 10, 15, 20, 50]\n      },\n      tableProxy: {\n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          query: ({ page }) => Vue.http.get(`/api/column/page/list/${page.pageSize}/${page.currentPage}`, { sort: 'seq', order: 'asc' }),\n          delete: ({ body }) => Vue.http.post('/api/column/save', body),\n          save: ({ body }) => Vue.http.post('/api/column/save', body)\n        }\n      },\n      tableToolbar: {\n        buttons: [\n          { code: 'reload', name: '' },\n          { code: 'insert_actived', name: '' },\n          { code: 'mark_cancel', name: '/' },\n          { code: 'save', name: '' },\n          { code: 'export', name: '.csv' }\n        ],\n        refresh: true,\n        setting: true\n      },\n      tableColumn: [\n        { type: 'checkbox', width: 50 },\n        { field: 'seq', title: '', width: 80, editRender: { name: 'input' } },\n        { field: 'key', title: '', width: 100, editRender: { name: 'input' } },\n        { field: 'name', title: '', width: 100, editRender: { name: 'input' } },\n        { field: 'type', title: '', width: 100, editRender: { name: 'input' } },\n        { field: 'width', title: '', width: 100, editRender: { name: 'input' } },\n     