}},{"id":"XtableDemo.grid.Dynamic.sample2","label":"XtableDemo.grid.Dynamic.sample2.label","description":"XtableDemo.grid.Dynamic.sample2.description","code":"<vue-xgrid\nborder\nshow-overflow\nheight=\"290\"\n:loading=\"loading2\"\n:pager-config=\"tablePage2\"\n:proxy-config=\"tableProxy2\"\n:columns=\"tableColumn2\"\n:toolbar=\"tableToolbar2\"\n:edit-rules=\"validRules2\"\n:edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n@toolbar-button-click=\"toolbarButtonClickEvent\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading2: false,\n      tablePage2: {\n        pageSize: 10\n      },\n      tableProxy2: {\n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),\n          delete: ({ body }) => Vue.http.post('/api/user/save', body),\n          save: ({ body }) => Vue.http.post('/api/user/save', body)\n        }\n      },\n      tableToolbar2: {\n        buttons: [\n          { code: 'reloadColumn', name: '' },\n          { code: 'reload', name: '' },\n          { code: 'insert_actived', name: '' },\n          { code: 'mark_cancel', name: '/' },\n          { code: 'save', name: '' },\n          { code: 'export', name: '.csv' }\n        ],\n        refresh: true,\n        setting: true\n      },\n      validRules2: null,\n      tableColumn2: [],\n\n    }\n  },\n  created () {\n    this.findColumn()\n  },\n  methods: {\n    findColumn () {\n      this.loading2 = true\n      Vue.http.get('/api/column/list', { sort: 'seq', order: 'asc' }).then(data => {\n        let validRules = {}\n        this.tableColumn2 = data.body.map(item => {\n          let config = {\n            title: item.name,\n            width: item.width\n          }\n          if (item.type) {\n            Object.assign(config, {\n              type: item.type\n            })\n          } else {\n            Object.assign(config, {\n              slots: {\n                default: ({ row, column }) => {\n                  // \n                  if (item.link) {\n                    return [\n                      <a class=\"link\" href={ item.link }>{ row[column.property] }</a>\n                    ]\n                  }\n                  // \n                  return [\n                    <span>{ row[column.property] }</span>\n                  ]\n                }\n              }\n            })\n            // \n            if (item.required) {\n              validRules[item.key] = [\n                { required: true, message: `${item.name}` }\n              ]\n            }\n            if (item.validator) {\n              if (validRules[item.key]) {\n                validRules[item.key].push({ pattern: new RegExp(item.validator), message: item.validMsg || `${item.name}` })\n              } else {\n                validRules[item.key] = [\n                  { pattern: new RegExp(item.validator), message: item.validMsg || `${item.name}` }\n                ]\n              }\n            }\n            if (item.key) {\n              config.field = item.key\n            }\n            if (item.isEdit) {\n              config.editRender = { name: 'input' }\n            }\n          }\n          return config\n        })\n        this.validRules2 = validRules\n        this.loading2 = false\n      })\n    },\n    toolbarButtonClickEvent ({ code }, event) {\n      switch (code) {\n        case 'reloadColumn':\n          this.findColumn()\n          break\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      show-overflow\n      height=\"290\"\n      :loading=\"loading2\"\n      :pager-config=\"tablePage2\"\n      :proxy-config=\"tableProxy2\"\n      :columns=\"tableColumn2\"\n      :toolbar=\"tableToolbar2\"\n      :edit-rules=\"validRules2\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      @toolbar-button-click=\"toolbarButtonClickEvent\"></vue-xgrid></div>","parameter": {
  data () {
    return {
      loading2: false,
      tablePage2: {
        pageSize: 10
      },
      tableProxy2: {
        props: {
          result: 'data.result',
          total: 'data.page.total'
        },
        ajax: {
          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),
          delete: ({ body }) => Vue.http.post('/api/user/save', body),
          save: ({ body }) => Vue.http.post('/api/user/save', body)
        }
      },
      tableToolbar2: {
        buttons: [
          { code: 'reloadColumn', name: '' },
          { code: 'reload', name: '' },
          { code: 'insert_actived', name: '' },
          { code: 'mark_cancel', name: '/' },
          { code: 'save', name: '' },
          { code: 'export', name: '.csv' }
        ],
        refresh: true,
        setting: true
      },
      validRules2: null,
      tableColumn2: [],

    }
  },
  created () {
    this.findColumn()
  },
  methods: {
    findColumn () {
      this.loading2 = true
      Vue.http.get('/api/column/list', { sort: 'seq', order: 'asc' }).then(data => {
        let validRules = {}
        this.tableColumn2 = data.body.map(item => {
          let config = {
            title: item.name,
            width: item.width
          }
          if (item.type) {
            Object.assign(config, {
              type: item.type
            })
          } else {
            Object.assign(config, {
              slots: {
                default: ({ row, column }) => {
                  // 
                  if (item.link) {
                    return [
                      <a class="link" href={ item.link }>{ row[column.property] }</a>
                    ]
                  }
                  // 
                  return [
                    <span>{ row[column.property] }</span>
                  ]
                }
              }
            })
            // 
            if (item.required) {
              validRules[item.key] = [
                { required: true, message: `${item.name}` }
              ]
            }
            if (item.validator) {
              if (validRules[item.key]) {
                validRules[item.key].push({ pattern: new RegExp(item.validator), message: item.validMsg || `${item.name}` })
              } else {
                validRules[item.key] = [
                  { pattern: new RegExp(item.validator), message: item.validMsg || `${item.name}` }
                ]
              }
            }
            if (item.key) {
              config.field = item.key
            }
            if (item.isEdit) {
              config.editRender = { name: 'input' }
            }
          }
          return config
        })
        this.validRules2 = validRules
        this.loading2 = false
      })
    },
    toolbarButtonClickEvent ({ code }, event) {
      switch (code) {
        case 'reloadColumn':
          this.findColumn()
          break
      }
    }
  }
}}],"styles":""}