 })\n    }\n  }\n})\n</script>\n\n<style>\n.radio-table.vue-xtable .vxe-body--row.row-checked {\n  background-color: #fbf8ec;\n}\n</style>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"$refs.xTable1.setRadioRow(tableData[1])\"></vue-button>\n        <vue-button @click=\"clearRadioRowEevnt\"></vue-button>\n        <vue-button @click=\"getRadioEvent1\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      ref=\"xTable1\"\n      class=\"radio-table\"\n      height=\"300\"\n      :data=\"tableData\"\n      :row-class-name=\"rowClassName\"\n      @cell-click=\"cellClickEvent\"\n      @radio-change=\"radioChangeEvent\">\n      <vue-xtable-column type=\"radio\" width=\"60\">\n        <template v-slot:header>\n          <vue-button type=\"text\" @click=\"clearRadioRowEevnt\" :disabled=\"!selectRow\"></vue-button>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
