export default {"path":"/XtableDemo/tree/Menu","name":"XtableDemo.tree.Menu","head":{"label":"XtableDemo.tree.Menu.label","description":"XtableDemo.tree.Menu.description"},"samples":[{"id":"XtableDemo.tree.Menu.sample1","label":"XtableDemo.tree.Menu.sample1.label","description":"XtableDemo.tree.Menu.sample1.description","code":"<vue-toolbar :data=\"tableData\" setting>\n  <template v-slot:buttons>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  resizable\n  ref=\"xTree\"\n  :tree-config=\"treeConfig\"\n  :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, visibleMethod:visibleMethod}\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n  :data=\"tableData\"\n  @context-menu-click=\"contextMenuClickEvent\">\n  <vue-xtable-column type=\"checkbox\" width=\"120\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      treeConfig: {\n        children: 'children'\n      },\n      headerMenus: [\n        [\n          {\n            code: 'hideColumn',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'showAllColumn',\n            name: '',\n            disabled: false\n          }\n        ]\n      ],\n      bodyMenus: [\n        [\n          {\n            code: 'insertAt',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'expand',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'contract',\n            name: '',\n            disabled: false\n          }\n        ]\n      ],\n\n    }\n  },\n  created () {\n    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)\n  },\n\n  methods: {\n    insertAtEvent (row, column) {\n      let xTree = this.$refs.xTree\n      xTree.createRow({\n        name: '',\n        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),\n        isNew: true\n      }).then(newRow => {\n        // \n        let rowNode = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)\n        if (rowNode) {\n          rowNode.items.splice(rowNode.index, 0, newRow)\n          xTree.refreshData().then(() => xTree.setActiveCell(newRow, column.property))\n        }\n      })\n    },\n    getInsertEvent () {\n      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)\n      this.$alert({ message: insertRecords.length })\n    },\n    visibleMethod  ({ row, type }) {\n      let xTree = this.$refs.xTree\n      let treeConfig = this.treeConfig\n      if (type === 'body') {\n        this.bodyMenus.forEach(list => {\n          list.forEach(item => {\n            if (VueUtil.includes(['expand', 'contract'], item.code)) {\n              if (row[treeConfig.children] && row[treeConfig.children].length) {\n                let isExpand = xTree.isTreeExpandByRow(row)\n                item.disabled = VueUtil.includes(['expand'], item.code) ? isExpand : !isExpand\n              } else {\n                item.disabled = true\n              }\n            }\n          })\n        })\n      }\n      return true\n    },\n    contextMenuClickEvent ({ menu, row, column }) {\n      let xTree = this.$refs.xTree\n      switch (menu.code) {\n        case 'hideColumn':\n          xTree.hideColumn(column)\n          break\n        case 'showAllColumn':\n          xTree.resetCustoms()\n          break\n        case 'insertAt':\n          this.insertAtEvent(row, column)\n          break\n        case 'expand':\n          xTree.setTreeExpansion(row, true)\n          break\n        case 'contract':\n          xTree.setTreeExpansion(row, false)\n          break\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :data=\"tableData\" setting>\n      <template v-slot:buttons>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      resizable\n      ref=\"xTree\"\n      :tree-config=\"treeConfig\"\n      :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, visibleMethod:visibleMethod}\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      :data=\"tableData\"\n      @context-menu-click=\"contextMenuClickEvent\">\n      <vue-xtable-column type=\"checkbox\" width=\"120\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      treeConfig: {
        children: 'children'
      },
      headerMenus: [
        [
          {
            code: 'hideColumn',
            name: '',
            disabled: false
          },
          {
            code: 'showAllColumn',
            name: '',
            disabled: false
          }
        ]
      ],
      bodyMenus: [
        [
          {
            code: 'insertAt',
            name: '',
            disabled: false
          },
          {
            code: 'expand',
            name: '',
            disabled: false
          },
          {
            code: 'contract',
            name: '',
            disabled: false
          }
        ]
      ],

    }
  },
  created () {
    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)
  },

  methods: {
    insertAtEvent (row, column) {
      let xTree = this.$refs.xTree
      xTree.createRow({
        name: '',
        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),
        isNew: true
      }).then(newRow => {
        // 
        let rowNode = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)
        if (rowNode) {
          rowNode.items.splice(rowNode.index, 0, newRow)
          xTree.refreshData().then(() => xTree.setActiveCell(newRow, column.property))
        }
      })
    },
    getInsertEvent () {
      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)
      this.$alert({ message: insertRecords.length })
    },
    visibleMethod  ({ row, type }) {
      let xTree = this.$refs.xTree
      let treeConfig = this.treeConfig
      if (type === 'body') {
        this.bodyMenus.forEach(list => {
          list.forEach(item => {
            if (VueUtil.includes(['expand', 'contract'], item.code)) {
              if (row[treeConfig.children] && row[treeConfig.children].length) {
                let isExpand = xTree.isTreeExpandByRow(row)
                item.disabled = VueUtil.includes(['expand'], item.code) ? isExpand : !isExpand
              } else {
                item.disabled = true
              }
            }
          })
        })
      }
      return true
    },
    contextMenuClickEvent ({ menu, row, column }) {
      let xTree = this.$refs.xTree
      switch (menu.code) {
        case 'hideColumn':
          xTree.hideColumn(column)
          break
        case 'showAllColumn':
          xTree.resetCustoms()
          break
        case 'insertAt':
          this.insertAtEvent(row, column)
          break
        case 'expand':
          xTree.setTreeExpansion(row, true)
          break
        case 'contract':
          xTree.setTreeExpansion(row, false)
          break
      }
    }
  }
}}],"styles":""}