export default {"path":"/XtableDemo/scroll/ScrollCols","name":"XtableDemo.scroll.ScrollCols","head":{"label":"XtableDemo.scroll.ScrollCols.label","description":"XtableDemo.scroll.ScrollCols.description"},"samples":[{"id":"XtableDemo.scroll.ScrollCols.sample1","label":"XtableDemo.scroll.ScrollCols.sample1.label","description":"XtableDemo.scroll.ScrollCols.sample1.description","code":"<vue-xgrid\n  border\n  show-overflow\n  show-header-overflow\n  ref=\"xGrid\"\n  height=\"300\"\n  :loading=\"loading\"\n  :toolbar=\"tableToolbar\"\n  :checkbox-config=\"{checkField: 'checked', labelField: 'nickname'}\">\n  <template v-slot:buttons>\n    <vue-button @click=\"loadColumnAndData(10000, 10000)\">1w1w</vue-button>\n  </template>\n</vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableToolbar: {},\n    }\n  },\n  created () {\n    this.loadColumnAndData(200, 600)\n  },\n\n  methods: {\n    loadColumnAndData (colSize, rowSize) {\n      this.loading = true\n      Promise.all([\n        this.loadColumn(colSize),\n        this.loadList(rowSize)\n      ]).then(() => {\n        this.loading = false\n      })\n    },\n    loadColumn (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          //  vue \n          let tableColumn = window.MOCK_COLUMN_LIST.slice(0, size)\n          console.log(this.$refs)\n          this.$refs.xGrid.loadColumn(tableColumn).then(() => {\n            resolve()\n          })\n        }, 300)\n      })\n    },\n    loadList (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          //  vue \n          let tableData = window.MOCK_DATA_LIST.slice(0, size)\n          this.$refs.xGrid.reloadData(tableData).then(() => {\n            resolve()\n          })\n        }, 300)\n      })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      show-overflow\n      show-header-overflow\n      ref=\"xGrid\"\n      height=\"300\"\n      :loading=\"loading\"\n      :toolbar=\"tableToolbar\"\n      :checkbox-config=\"{checkField: 'checked', labelField: 'nickname'}\">\n      <template v-slot:buttons>\n        <vue-button @click=\"loadColumnAndData(10000, 10000)\">1w1w</vue-button>\n      </template>\n    </vue-xgrid></div>","parameter": {
  data () {
    return {
      loading: false,
      tableToolbar: {},
    }
  },
  created () {
    this.loadColumnAndData(200, 600)
  },

  methods: {
    loadColumnAndData (colSize, rowSize) {
      this.loading = true
      Promise.all([
        this.loadColumn(colSize),
        this.loadList(rowSize)
      ]).then(() => {
        this.loading = false
      })
    },
    loadColumn (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          //  vue 
          let tableColumn = window.MOCK_COLUMN_LIST.slice(0, size)
          console.log(this.$refs)
          this.$refs.xGrid.loadColumn(tableColumn).then(() => {
            resolve()
          })
        }, 300)
      })
    },
    loadList (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          //  vue 
          let tableData = window.MOCK_DATA_LIST.slice(0, size)
          this.$refs.xGrid.reloadData(tableData).then(() => {
            resolve()
          })
        }, 300)
      })
    }
  }
}}],"styles":""}