export default {"path":"/XtableDemo/advanced/ManualFilter","name":"XtableDemo.advanced.ManualFilter","head":{"label":"XtableDemo.advanced.ManualFilter.label","description":"XtableDemo.advanced.ManualFilter.description"},"samples":[{"id":"XtableDemo.advanced.ManualFilter.sample1","label":"XtableDemo.advanced.ManualFilter.sample1.label","description":"XtableDemo.advanced.ManualFilter.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"filterNameEvent\"> Name</vue-button>\n    <vue-button @click=\"filterAgeEvent\"> Age</vue-button>\n    <vue-button @click=\"updateNameFilterEvent\"> Name </vue-button>\n    <vue-button @click=\"$refs.xTable.clearFilter('age')\"> Age </vue-button>\n    <vue-button @click=\"$refs.xTable.clearFilter()\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  highlight-hover-row\n  ref=\"xTable\"\n  height=\"400\"\n  :loading=\"loading\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable :filters=\"nameFilterList\" :filter-method=\"filterNameMethod\"></vue-xtable-column>\n  <vue-xtable-column field=\"role\" title=\"Role\" sortable :filters=\"[{ data: '' }]\" :filter-method=\"filterRoleMethod\">\n    <template v-slot:filter=\"scope\">\n      <select class=\"my-select\" v-model=\"option.data\" v-for=\"(option, index) in scope.column.filters\" :key=\"index\" @change=\"scope.context.changeOption($event, !!option.data, option)\">\n        <option v-for=\"(label, cIndex) in roleList\" :key=\"cIndex\" :value=\"label\">{{ label }}</option>\n      </select>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" sortable :filter-multiple=\"false\" :filters=\"[{label: 'Man', value: '1'}, {label: 'Woman', value: '0'}]\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" :filters=\"[{ data: '' }]\" :filter-method=\"filterAgeMethod\">\n    <template v-slot:filter=\"scope\">\n      <template v-for=\"(option, index) in scope.column.filters\">\n        <input class=\"my-input\" type=\"type\" :key=\"index\" v-model=\"option.data\" @input=\"scope.context.changeOption($event, !!option.data, option)\" @keyup.enter=\"scope.context.confirmFilter()\" placeholder=\"\">\n      </template>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"time\" title=\"Time\" sortable></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableData: [],\n      roleList: ['', '', '', ''],\n      nameFilterList: [\n        { label: 'id10', value: 10 },\n        { label: 'id40', value: 40 }\n      ],\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      return new Promise(resolve => {\n        setTimeout(() => {\n          this.tableData = window.MOCK_DATA_LIST.slice(0, 50)\n          this.loading = false\n          resolve()\n        }, 300)\n      })\n    },\n    filterNameMethod ({ value, row, column }) {\n      return row.id >= value\n    },\n    filterRoleMethod ({ option, row }) {\n      return row.role === option.data\n    },\n    filterAgeMethod ({ option, row }) {\n      return row.age === Number(option.data)\n    },\n    updateNameFilterEvent () {\n      let xTable = this.$refs.xTable\n      xTable.filter('name', options => {\n        // \n        return [\n          {\n            label: 'id10',\n            value: 10\n          },\n          {\n            label: 'id20',\n            value: 20\n          },\n          {\n            label: 'id30',\n            value: 30,\n            checked: true // \n          },\n          {\n            label: 'id40',\n            value: 40\n          }\n        ]\n      }).then(() => {\n        //  updateData \n        xTable.updateData()\n      })\n    },\n    filterNameEvent () {\n      let xTable = this.$refs.xTable\n      xTable.filter('name')\n        .then(options => {\n          // \n          if (options.length) {\n            let option = options[1]\n            option.checked = true\n          }\n        })\n        .then(() => {\n          //  updateData \n          xTable.updateData()\n        })\n    },\n    filterAgeEvent () {\n      let xTable = this.$refs.xTable\n      xTable.filter('age')\n        .then(options => {\n          // \n          if (options.length) {\n            let option = options[0]\n            option.data = '26'\n            option.checked = true\n          }\n        })\n        .then(() => {\n          //  updateData \n          xTable.updateData()\n        })\n    }\n  }\n})\n</script>\n\n<style>\n.my-select {\n  width: 100px;\n  height: 32px;\n}\n.my-input {\n  width: 140px;\n  height: 32px;\n}\n</style>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"filterNameEvent\"> Name</vue-button>\n        <vue-button @click=\"filterAgeEvent\"> Age</vue-button>\n        <vue-button @click=\"updateNameFilterEvent\"> Name </vue-button>\n        <vue-button @click=\"$refs.xTable.clearFilter('age')\"> Age </vue-button>\n        <vue-button @click=\"$refs.xTable.clearFilter()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      highlight-hover-row\n      ref=\"xTable\"\n      height=\"400\"\n      :loading=\"loading\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable :filters=\"nameFilterList\" :filter-method=\"filterNameMethod\"></vue-xtable-column>\n      <vue-xtable-column field=\"role\" title=\"Role\" sortable :filters=\"[{ data: '' }]\" :filter-method=\"filterRoleMethod\">\n        <template v-slot:filter=\"scope\">\n          <select class=\"my-select\" v-model=\"option.data\" v-for=\"(option, index) in scope.column.filters\" :key=\"index\" @change=\"scope.context.changeOption($event, !!option.data, option)\">\n            <option v-for=\"(label, cIndex) in roleList\" :key=\"cIndex\" :value=\"label\">{{ label }}</option>\n          </select>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" sortable :filter-multiple=\"false\" :filters=\"[{label: 'Man', value: '1'}, {label: 'Woman', value: '0'}]\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" :filters=\"[{ data: '' }]\" :filter-method=\"filterAgeMethod\">\n        <template v-slot:filter=\"scope\">\n          <template v-for=\"(option, index) in scope.column.filters\">\n            <input class=\"my-input\" type=\"type\" :key=\"index\" v-model=\"option.data\" @input=\"scope.context.changeOption($event, !!option.data, option)\" @keyup.enter=\"scope.context.confirmFilter()\" placeholder=\"\">\n          </template>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"time\" title=\"Time\" sortable></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      tableData: [],
      roleList: ['', '', '', ''],
      nameFilterList: [
        { label: 'id10', value: 10 },
        { label: 'id40', value: 40 }
      ],

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      this.loading = true
      return new Promise(resolve => {
        setTimeout(() => {
          this.tableData = window.MOCK_DATA_LIST.slice(0, 50)
          this.loading = false
          resolve()
        }, 300)
      })
    },
    filterNameMethod ({ value, row, column }) {
      return row.id >= value
    },
    filterRoleMethod ({ option, row }) {
      return row.role === option.data
    },
    filterAgeMethod ({ option, row }) {
      return row.age === Number(option.data)
    },
    updateNameFilterEvent () {
      let xTable = this.$refs.xTable
      xTable.filter('name', options => {
        // 
        return [
          {
            label: 'id10',
            value: 10
          },
          {
            label: 'id20',
            value: 20
          },
          {
            label: 'id30',
            value: 30,
            checked: true // 
          },
          {
            label: 'id40',
            value: 40
          }
        ]
      }).then(() => {
        //  updateData 
        xTable.updateData()
      })
    },
    filterNameEvent () {
      let xTable = this.$refs.xTable
      xTable.filter('name')
        .then(options => {
          // 
          if (options.length) {
            let option = options[1]
            option.checked = true
          }
        })
        .then(() => {
          //  updateData 
          xTable.updateData()
        })
    },
    filterAgeEvent () {
      let xTable = this.$refs.xTable
      xTable.filter('age')
        .then(options => {
          // 
          if (options.length) {
            let option = options[0]
            option.data = '26'
            option.checked = true
          }
        })
        .then(() => {
          //  updateData 
          xTable.updateData()
        })
    }
  }
}}],"styles":".my-select {\n  width: 100px;\n  height: 32px;\n}\n.my-input {\n  width: 140px;\n  height: 32px;\n}"}