export default {"path":"/XtableDemo/grid/FullQuery","name":"XtableDemo.grid.FullQuery","head":{"label":"XtableDemo.grid.FullQuery.label","description":"XtableDemo.grid.FullQuery.description"},"samples":[{"id":"XtableDemo.grid.FullQuery.sample1","label":"XtableDemo.grid.FullQuery.sample1.label","description":"XtableDemo.grid.FullQuery.sample1.description","code":"<vue-xgrid\nref=\"xGrid\"\nborder\nresizable\nremote-filter\nheight=\"548\"\nrow-id=\"id\"\n:pager-config=\"{pageSize: 15}\"\n:columns=\"tableColumn\"\n:proxy-config=\"tableProxy\"\n:checkbox-config=\"{reserve: true}\"\n@select-change=\"selectChangeEvent\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableProxy: {\n        index: true, // \n        sort: true, // \n        filter: true, // \n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          query: ({ page, sort, filters }) => {\n            // \n            let formData = {\n              sort: sort.field,\n              order: sort.order\n            }\n            // \n            filters.forEach(({ column, field, values }) => {\n              formData[field] = values.join(',')\n            })\n            return Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`, formData)\n          }\n        }\n      },\n      tableColumn: [\n        { type: 'checkbox', width: 50, fixed: 'left' },\n        { type: 'index', width: 60, fixed: 'left' },\n        { field: 'id', title: 'ID', width: 100, remoteSort: true },\n        { field: 'name', title: 'Name', width: 300, remoteSort: true },\n        { field: 'nickname', title: 'Nickname', remoteSort: true, width: 300 },\n        { field: 'age', title: 'Age', remoteSort: true, width: 100 },\n        {\n          field: 'role',\n          title: 'Role',\n          remoteSort: true,\n          width: 200,\n          filters: [\n            { label: '', value: '' },\n            { label: '', value: '' },\n            { label: '', value: '' }\n          ],\n          filterMultiple: false\n        },\n        { field: 'describe', title: 'Describe', width: 300, showOverflow: true }\n      ],\n\n    }\n  },\n  methods: {\n    selectChangeEvent ({ checked, row, reserves }) {\n      console.log(checked ? '' : '')\n      console.log('', reserves)\n    },\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      ref=\"xGrid\"\n      border\n      resizable\n      remote-filter\n      height=\"548\"\n      row-id=\"id\"\n      :pager-config=\"{pageSize: 15}\"\n      :columns=\"tableColumn\"\n      :proxy-config=\"tableProxy\"\n      :checkbox-config=\"{reserve: true}\"\n      @select-change=\"selectChangeEvent\"></vue-xgrid></div>","parameter": {
  data () {
    return {
      tableProxy: {
        index: true, // 
        sort: true, // 
        filter: true, // 
        props: {
          result: 'data.result',
          total: 'data.page.total'
        },
        ajax: {
          query: ({ page, sort, filters }) => {
            // 
            let formData = {
              sort: sort.field,
              order: sort.order
            }
            // 
            filters.forEach(({ column, field, values }) => {
              formData[field] = values.join(',')
            })
            return Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`, formData)
          }
        }
      },
      tableColumn: [
        { type: 'checkbox', width: 50, fixed: 'left' },
        { type: 'index', width: 60, fixed: 'left' },
        { field: 'id', title: 'ID', width: 100, remoteSort: true },
        { field: 'name', title: 'Name', width: 300, remoteSort: true },
        { field: 'nickname', title: 'Nickname', remoteSort: true, width: 300 },
        { field: 'age', title: 'Age', remoteSort: true, width: 100 },
        {
          field: 'role',
          title: 'Role',
          remoteSort: true,
          width: 200,
          filters: [
            { label: '', value: '' },
            { label: '', value: '' },
            { label: '', value: '' }
          ],
          filterMultiple: false
        },
        { field: 'describe', title: 'Describe', width: 300, showOverflow: true }
      ],

    }
  },
  methods: {
    selectChangeEvent ({ checked, row, reserves }) {
      console.log(checked ? '' : '')
      console.log('', reserves)
    },
  }
}}],"styles":""}