export default {"path":"/XtableDemo/edit/Footer","name":"XtableDemo.edit.Footer","head":{"label":"XtableDemo.edit.Footer.label","description":"XtableDemo.edit.Footer.description"},"samples":[{"id":"XtableDemo.edit.Footer.sample1","label":"XtableDemo.edit.Footer.sample1.label","description":"XtableDemo.edit.Footer.sample1.description","code":"<vue-toolbar export>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent\"></vue-button>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  show-footer\n  show-overflow\n  highlight-hover-row\n  ref=\"xTable\"\n  height=\"400\"\n  class=\"editable-footer\"\n  :footer-method=\"footerMethod\"\n  :footer-cell-class-name=\"footerCellClassName\"\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'row'}\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 20)\n    this.tableData = list\n  },\n\n  methods: {\n    footerCellClassName ({ $rowIndex, column, columnIndex }) {\n      if (columnIndex === 0) {\n        if ($rowIndex === 0) {\n          return 'col-blue'\n        } else {\n          return 'col-red'\n        }\n      }\n    },\n    footerMethod ({ columns, data }) {\n      return [\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age'], column.property)) {\n            return VueUtil.mean(data.map(data => data[column.property]))\n          }\n          return null\n        }),\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age'], column.property)) {\n            return VueUtil.sum(data.map( data => data[column.property]))\n          }\n          return null\n        })\n      ]\n    },\n    insertEvent () {\n      let record = {\n        name: 'New name'\n      }\n      this.$refs.xTable.insert(record).then(({ row }) => this.$refs.xTable.setActiveCell(row, 'age'))\n    },\n    getInsertEvent () {\n      let insertRecords = this.$refs.xTable.getInsertRecords()\n      this.$alert({ message: insertRecords.length })\n    }\n  }\n})\n</script>\n\n<style>\n.editable-footer .vxe-footer--column.col-blue {\n  background-color: #2db7f5;\n  color: #fff;\n}\n.editable-footer .vxe-footer--column.col-red {\n  background-color: red;\n  color: #fff;\n}\n</style>","template":"<div class=\"source\">    <vue-toolbar export>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      show-footer\n      show-overflow\n      highlight-hover-row\n      ref=\"xTable\"\n      height=\"400\"\n      class=\"editable-footer\"\n      :footer-method=\"footerMethod\"\n      :footer-cell-class-name=\"footerCellClassName\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'row'}\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 20)
    this.tableData = list
  },

  methods: {
    footerCellClassName ({ $rowIndex, column, columnIndex }) {
      if (columnIndex === 0) {
        if ($rowIndex === 0) {
          return 'col-blue'
        } else {
          return 'col-red'
        }
      }
    },
    footerMethod ({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          if (VueUtil.includes(['age'], column.property)) {
            return VueUtil.mean(data.map(data => data[column.property]))
          }
          return null
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          if (VueUtil.includes(['age'], column.property)) {
            return VueUtil.sum(data.map( data => data[column.property]))
          }
          return null
        })
      ]
    },
    insertEvent () {
      let record = {
        name: 'New name'
      }
      this.$refs.xTable.insert(record).then(({ row }) => this.$refs.xTable.setActiveCell(row, 'age'))
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    }
  }
}}],"styles":".editable-footer .vxe-footer--column.col-blue {\n  background-color: #2db7f5;\n  color: #fff;\n}\n.editable-footer .vxe-footer--column.col-red {\n  background-color: red;\n  color: #fff;\n}"}