export default {"path":"/XtableDemo/tree/GroupBy","name":"XtableDemo.tree.GroupBy","head":{"label":"XtableDemo.tree.GroupBy.label","description":"XtableDemo.tree.GroupBy.description"},"samples":[{"id":"XtableDemo.tree.GroupBy.sample1","label":"XtableDemo.tree.GroupBy.sample1.label","description":"XtableDemo.tree.GroupBy.sample1.description","code":"<vue-xtable\n  border\n  resizable\n  :loading=\"loading\"\n  :tree-config=\"tableTreeConfig\"\n  :span-method=\"colspanMethod\"\n  :data=\"tableData\">\n  <vue-xtable-column field=\"name\" title=\"Name\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"num\" title=\"Num\"></vue-xtable-column>\n  <vue-xtable-column field=\"level\" title=\"Level\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"rate\" title=\"Rate\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableData: [],\n      tableTreeConfig: {\n        children: 'children',\n        trigger: 'row', // \n        expandAll: false // \n      },\n\n    }\n  },\n  created () {\n    this.loading = true\n    setTimeout(() => {\n      let list = [\n        { gKey: 'Group 1', name: 'test7', num: 23, level: 9, age: 24, rate: 4 },\n        { gKey: 'Group 1', name: 'test6', num: 63, level: 14, age: 20, rate: 3 },\n        { gKey: 'Group 1', name: 'test51', num: 84.8, level: 21, age: 19, rate: 5 },\n        { gKey: 'Group 1', name: 'test32', num: 63, level: 11, age: 21, rate: 1 },\n        { gKey: 'Group 2', name: 'test15', num: 5.9, level: 13, age: 32, rate: 1 },\n        { gKey: 'Group 2', name: 'test44', num: 23, level: 9, age: 29, rate: 4 },\n        { gKey: 'Group 2', name: 'test44', num: 41, level: 22, age: 28, rate: 5 },\n        { gKey: 'Group 2', name: 'test84', num: 63.9, level: 18, age: 24, rate: 2 },\n        { gKey: 'Group 2', name: 'test91', num: 12, level: 16, age: 27, rate: 5 },\n        { gKey: 'Group 3', name: 'test6', num: 33.6, level: 3, age: 22, rate: 2 },\n        { gKey: 'Group 3', name: 'test2', num: 23, level: 5, age: 25, rate: 3 },\n        { gKey: 'Group 3', name: 'test42', num: 66.8, level: 17, age: 35, rate: 4 }\n      ]\n      // \n      let groupMap = VueUtil.groupBy(list, 'gKey')\n      let groupList = []\n      // \n      VueUtil.each(groupMap, (list, gKey) => {\n        let children = list || []\n        if (children) {\n          let total = {\n            name: '',\n            num: `:${VueUtil.sum(children.map( children => children.num))}`,\n            level: `:${VueUtil.sum(children.map( children => children.level))}`,\n            age: `:${parseInt(VueUtil.mean(children.map( children => children.age)))}`,\n            rate: `:${VueUtil.sum(children.map( children => children.rate))}`\n          }\n          children.push(total)\n        }\n        groupList.push({ name: gKey, children })\n      })\n      this.tableData = groupList\n      this.loading = false\n    }, 300)\n  },\n\n  methods: {\n    colspanMethod ({ row, rowIndex, column, columnIndex, data, level }) {\n      // \n      if (level === 0) {\n        if (columnIndex === 0) {\n          return {\n            rowspan: 1,\n            colspan: 5\n          }\n        } else if (columnIndex < 5) {\n          return {\n            rowspan: 0,\n            colspan: 0\n          }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      resizable\n      :loading=\"loading\"\n      :tree-config=\"tableTreeConfig\"\n      :span-method=\"colspanMethod\"\n      :data=\"tableData\">\n      <vue-xtable-column field=\"name\" title=\"Name\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"num\" title=\"Num\"></vue-xtable-column>\n      <vue-xtable-column field=\"level\" title=\"Level\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"rate\" title=\"Rate\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      tableData: [],
      tableTreeConfig: {
        children: 'children',
        trigger: 'row', // 
        expandAll: false // 
      },

    }
  },
  created () {
    this.loading = true
    setTimeout(() => {
      let list = [
        { gKey: 'Group 1', name: 'test7', num: 23, level: 9, age: 24, rate: 4 },
        { gKey: 'Group 1', name: 'test6', num: 63, level: 14, age: 20, rate: 3 },
        { gKey: 'Group 1', name: 'test51', num: 84.8, level: 21, age: 19, rate: 5 },
        { gKey: 'Group 1', name: 'test32', num: 63, level: 11, age: 21, rate: 1 },
        { gKey: 'Group 2', name: 'test15', num: 5.9, level: 13, age: 32, rate: 1 },
        { gKey: 'Group 2', name: 'test44', num: 23, level: 9, age: 29, rate: 4 },
        { gKey: 'Group 2', name: 'test44', num: 41, level: 22, age: 28, rate: 5 },
        { gKey: 'Group 2', name: 'test84', num: 63.9, level: 18, age: 24, rate: 2 },
        { gKey: 'Group 2', name: 'test91', num: 12, level: 16, age: 27, rate: 5 },
        { gKey: 'Group 3', name: 'test6', num: 33.6, level: 3, age: 22, rate: 2 },
        { gKey: 'Group 3', name: 'test2', num: 23, level: 5, age: 25, rate: 3 },
        { gKey: 'Group 3', name: 'test42', num: 66.8, level: 17, age: 35, rate: 4 }
      ]
      // 
      let groupMap = VueUtil.groupBy(list, 'gKey')
      let groupList = []
      // 
      VueUtil.each(groupMap, (list, gKey) => {
        let children = list || []
        if (children) {
          let total = {
            name: '',
            num: `:${VueUtil.sum(children.map( children => children.num))}`,
            level: `:${VueUtil.sum(children.map( children => children.level))}`,
            age: `:${parseInt(VueUtil.mean(children.map( children => children.age)))}`,
            rate: `:${VueUtil.sum(children.map( children => children.rate))}`
          }
          children.push(total)
        }
        groupList.push({ name: gKey, children })
      })
      this.tableData = groupList
      this.loading = false
    }, 300)
  },

  methods: {
    colspanMethod ({ row, rowIndex, column, columnIndex, data, level }) {
      // 
      if (level === 0) {
        if (columnIndex === 0) {
          return {
            rowspan: 1,
            colspan: 5
          }
        } else if (columnIndex < 5) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    }
  }
}}],"styles":""}