export default {"path":"/XtableDemo/edit/Span","name":"XtableDemo.edit.Span","head":{"label":"XtableDemo.edit.Span.label","description":"XtableDemo.edit.Span.description"},"samples":[{"id":"XtableDemo.edit.Span.sample1","label":"XtableDemo.edit.Span.sample1.label","description":"XtableDemo.edit.Span.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent\"></vue-button>\n    <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  ref=\"xTable\"\n  height=\"500\"\n  :loading=\"loading\"\n  :span-method=\"rowspanMethod\"\n  :data=\"tableData\"\n  :edit-rules=\"validRules\"\n  :mouse-config=\"{selected: true}\"\n  :keyboard-config=\"{isArrow: true, isDel: true, isTab: true, isEdit: true}\"\n  :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n  <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"key\" title=\"Key\" :edit-render=\"{name: 'input', props: {placeholder: ''}}\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Translate\" :edit-render=\"{name: 'input', props: {placeholder: ''}}\"></vue-xtable-column>\n  <vue-xtable-column field=\"language\" title=\"Language\" :edit-render=\"{name: 'select', options: optionList}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableData: [],\n      optionList: [\n        { label: '----', value: null },\n        { label: '', value: 'zh_CN' },\n        { label: 'English', value: 'en_US' },\n        { label: 'Espaol', value: 'es' },\n        { label: 'Franais', value: 'fr_FR' }\n      ],\n      validRules: {\n        key: [\n          { required: true, message: '' }\n        ],\n        name: [\n          { required: true, message: '' }\n        ],\n        language: [\n          { required: true, message: '' }\n        ]\n      },\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      Vue.http.get('/api/i18n/list', { sort: 'key', order: 'asc' }).then(data => {\n        this.tableData = data.data\n        this.loading = false\n      })\n    },\n    insertEvent () {\n      let record = {}\n      this.$refs.xTable.insert(record)\n        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'key'))\n    },\n    // \n    rowspanMethod ({ row, $rowIndex, column, data }) {\n      let fields = ['key']\n      let cellValue = row[column.property]\n      if (cellValue && VueUtil.includes(fields, column.property)) {\n        let prevRow = data[$rowIndex - 1]\n        let nextRow = data[$rowIndex + 1]\n        if (prevRow && prevRow[column.property] === cellValue) {\n          return { rowspan: 0, colspan: 0 }\n        } else {\n          let countRowspan = 1\n          while (nextRow && nextRow[column.property] === cellValue) {\n            nextRow = data[++countRowspan + $rowIndex]\n          }\n          if (countRowspan > 1) {\n            return { rowspan: countRowspan, colspan: 1 }\n          }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent\"></vue-button>\n        <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      ref=\"xTable\"\n      height=\"500\"\n      :loading=\"loading\"\n      :span-method=\"rowspanMethod\"\n      :data=\"tableData\"\n      :edit-rules=\"validRules\"\n      :mouse-config=\"{selected: true}\"\n      :keyboard-config=\"{isArrow: true, isDel: true, isTab: true, isEdit: true}\"\n      :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"key\" title=\"Key\" :edit-render=\"{name: 'input', props: {placeholder: ''}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Translate\" :edit-render=\"{name: 'input', props: {placeholder: ''}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"language\" title=\"Language\" :edit-render=\"{name: 'select', options: optionList}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      tableData: [],
      optionList: [
        { label: '----', value: null },
        { label: '', value: 'zh_CN' },
        { label: 'English', value: 'en_US' },
        { label: 'Espaol', value: 'es' },
        { label: 'Franais', value: 'fr_FR' }
      ],
      validRules: {
        key: [
          { required: true, message: '' }
        ],
        name: [
          { required: true, message: '' }
        ],
        language: [
          { required: true, message: '' }
        ]
      },

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      this.loading = true
      Vue.http.get('/api/i18n/list', { sort: 'key', order: 'asc' }).then(data => {
        this.tableData = data.data
        this.loading = false
      })
    },
    insertEvent () {
      let record = {}
      this.$refs.xTable.insert(record)
        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'key'))
    },
    // 
    rowspanMethod ({ row, $rowIndex, column, data }) {
      let fields = ['key']
      let cellValue = row[column.property]
      if (cellValue && VueUtil.includes(fields, column.property)) {
        let prevRow = data[$rowIndex - 1]
        let nextRow = data[$rowIndex + 1]
        if (prevRow && prevRow[column.property] === cellValue) {
          return { rowspan: 0, colspan: 0 }
        } else {
          let countRowspan = 1
          while (nextRow && nextRow[column.property] === cellValue) {
            nextRow = data[++countRowspan + $rowIndex]
          }
          if (countRowspan > 1) {
            return { rowspan: countRowspan, colspan: 1 }
          }
        }
      }
    }
  }
}}],"styles":""}