export default {"path":"/XtableDemo/edit/CascadingSelect","name":"XtableDemo.edit.CascadingSelect","head":{"label":"XtableDemo.edit.CascadingSelect.label","description":"XtableDemo.edit.CascadingSelect.description"},"samples":[{"id":"XtableDemo.edit.CascadingSelect.sample1","label":"XtableDemo.edit.CascadingSelect.sample1.label","description":"XtableDemo.edit.CascadingSelect.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent()\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  resizable\n  ref=\"xTable\"\n  max-height=\"400\"\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'row'}\"\n  @edit-actived=\"editActivedEvent\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"attr3\" title=\"Project type\" :edit-render=\"{name: 'select', options: ptypeList, events: {change: ptypeChangeEvent}}\"></vue-xtable-column>\n  <vue-xtable-column field=\"attr4\" title=\"Project name\" :formatter=\"formatPanmeLabel\" :edit-render=\"{}\">\n    <template v-slot:edit=\"scope\">\n      <select class=\"vxe-default-select\" v-model=\"scope.row.attr4\">\n        <option v-for=\"item in pnameList\" :key=\"item.value\" :value=\"item.value\">{{ item.label }}</option>\n      </select>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"date12\" title=\"Date\" :edit-render=\"{name: 'input', attrs: {type: 'date'}}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      ptypeList: [\n        {\n          label: '',\n          value: ''\n        },\n        {\n          label: '1',\n          value: '1'\n        },\n        {\n          label: '2',\n          value: '2'\n        },\n        {\n          label: '3',\n          value: '3'\n        }\n      ],\n      pnameList: [],\n      cachePnameList: [],\n\n    }\n  },\n  created () {\n    this.$nextTick(() => {\n      this.tableData = window.MOCK_DATA_LIST.slice(0, 4)\n    })\n  },\n\n  methods: {\n    insertEvent () {\n      let record = {\n\n      }\n      this.$refs.xTable.insert(record)\n    },\n    // \n    formatPanmeLabel ({ cellValue, row }) {\n      let ptype = row.attr3\n      let ptypeItem = VueUtil.find(this.cachePnameList, item => item.ptype === ptype)\n      if (ptypeItem && ptypeItem.pnameList) {\n        let pnameItem = VueUtil.find(ptypeItem.pnameList, item => item.value === cellValue)\n        if (pnameItem) {\n          return pnameItem.label\n        }\n      }\n      return ''\n    },\n    // \n    updatePnameList (row) {\n      let ptype = row.attr3\n      let pnameList = [\n        {\n          label: '',\n          value: ''\n        }\n      ]\n      if (ptype) {\n        let item = VueUtil.find(this.cachePnameList, item => item.ptype === ptype)\n        if (item) {\n          pnameList = item.pnameList\n        } else {\n          // \n          Array.from(new Array(Math.floor(Math.random()*(8-3+1)+3))).forEach((item, index) => {\n            pnameList.push({\n              label: `${ptype}-${index}`,\n              value: `${ptype}_${index}`\n            })\n          })\n          this.cachePnameList.push({ ptype, pnameList })\n        }\n      }\n      this.pnameList = pnameList\n    },\n    ptypeChangeEvent ({ row }, evnt) {\n      //  select  v-model \n      row.attr3 = evnt.target.value\n      // \n      row.attr4 = ''\n      this.updatePnameList(row)\n    },\n    editActivedEvent ({ row }) {\n      this.updatePnameList(row)\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      ref=\"xTable\"\n      max-height=\"400\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'row'}\"\n      @edit-actived=\"editActivedEvent\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr3\" title=\"Project type\" :edit-render=\"{name: 'select', options: ptypeList, events: {change: ptypeChangeEvent}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr4\" title=\"Project name\" :formatter=\"formatPanmeLabel\" :edit-render=\"{}\">\n        <template v-slot:edit=\"scope\">\n          <select class=\"vxe-default-select\" v-model=\"scope.row.attr4\">\n            <option v-for=\"item in pnameList\" :key=\"item.value\" :value=\"item.value\">{{ item.label }}</option>\n          </select>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"date12\" title=\"Date\" :edit-render=\"{name: 'input', attrs: {type: 'date'}}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      ptypeList: [
        {
          label: '',
          value: ''
        },
        {
          label: '1',
          value: '1'
        },
        {
          label: '2',
          value: '2'
        },
        {
          label: '3',
          value: '3'
        }
      ],
      pnameList: [],
      cachePnameList: [],

    }
  },
  created () {
    this.$nextTick(() => {
      this.tableData = window.MOCK_DATA_LIST.slice(0, 4)
    })
  },

  methods: {
    insertEvent () {
      let record = {

      }
      this.$refs.xTable.insert(record)
    },
    // 
    formatPanmeLabel ({ cellValue, row }) {
      let ptype = row.attr3
      let ptypeItem = VueUtil.find(this.cachePnameList, item => item.ptype === ptype)
      if (ptypeItem && ptypeItem.pnameList) {
        let pnameItem = VueUtil.find(ptypeItem.pnameList, item => item.value === cellValue)
        if (pnameItem) {
          return pnameItem.label
        }
      }
      return ''
    },
    // 
    updatePnameList (row) {
      let ptype = row.attr3
      let pnameList = [
        {
          label: '',
          value: ''
        }
      ]
      if (ptype) {
        let item = VueUtil.find(this.cachePnameList, item => item.ptype === ptype)
        if (item) {
          pnameList = item.pnameList
        } else {
          // 
          Array.from(new Array(Math.floor(Math.random()*(8-3+1)+3))).forEach((item, index) => {
            pnameList.push({
              label: `${ptype}-${index}`,
              value: `${ptype}_${index}`
            })
          })
          this.cachePnameList.push({ ptype, pnameList })
        }
      }
      this.pnameList = pnameList
    },
    ptypeChangeEvent ({ row }, evnt) {
      //  select  v-model 
      row.attr3 = evnt.target.value
      // 
      row.attr4 = ''
      this.updatePnameList(row)
    },
    editActivedEvent ({ row }) {
      this.updatePnameList(row)
    }
  }
}}],"styles":""}