export default {"path":"/XtableDemo/scroll/ScrollFullCols","name":"XtableDemo.scroll.ScrollFullCols","head":{"label":"XtableDemo.scroll.ScrollFullCols.label","description":"XtableDemo.scroll.ScrollFullCols.description"},"samples":[{"id":"XtableDemo.scroll.ScrollFullCols.sample1","label":"XtableDemo.scroll.ScrollFullCols.sample1.label","description":"XtableDemo.scroll.ScrollFullCols.sample1.description","code":"<vue-xgrid\n  border\n  show-overflow\n  show-header-overflow\n  highlight-hover-row\n  highlight-current-row\n  ref=\"xGrid\"\n  height=\"600\"\n  :loading=\"loading\"\n  :toolbar=\"tableToolbar\"\n  :checkbox-config=\"{checkField: 'checked', labelField: 'nickname'}\">\n  <template v-slot:buttons>\n    <vue-button @click=\"loadColumnAndData(10000, 100000)\">1w10w</vue-button>\n    <vue-button @click=\"$refs.xGrid.toggleRowSelection($refs.xGrid.getData(1))\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.setSelection([$refs.xGrid.getData(2), $refs.xGrid.getData(3)], true)\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.setAllSelection(true)\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.clearSelection()\"></vue-button>\n    <vue-button @click=\"getSelectEvent\"></vue-button>\n  </template>\n</vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableToolbar: {},\n\n    }\n  },\n  created () {\n    this.loadColumnAndData(200, 600)\n  },\n\n  methods: {\n    loadColumnAndData (colSize, rowSize) {\n      this.loading = true\n      Promise.all([\n        this.loadColumn(colSize),\n        this.loadList(rowSize)\n      ]).then(() => {\n        this.loading = false\n      })\n    },\n    loadColumn (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          //  vue \n          let tableColumn = window.MOCK_COLUMN_LIST.slice(0, size)\n          this.$refs.xGrid.loadColumn(tableColumn).then(() => {\n            resolve()\n          })\n        }, 300)\n      })\n    },\n    loadList (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          //  vue \n          let tableData = window.MOCK_DATA_LIST.slice(0, size)\n          this.$refs.xGrid.reloadData(tableData).then(() => {\n            resolve()\n          })\n        }, 300)\n      })\n    },\n    getSelectEvent () {\n      let selectRecords = this.$refs.xGrid.getSelectRecords()\n      this.$alert({ message: selectRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      show-overflow\n      show-header-overflow\n      highlight-hover-row\n      highlight-current-row\n      ref=\"xGrid\"\n      height=\"600\"\n      :loading=\"loading\"\n      :toolbar=\"tableToolbar\"\n      :checkbox-config=\"{checkField: 'checked', labelField: 'nickname'}\">\n      <template v-slot:buttons>\n        <vue-button @click=\"loadColumnAndData(10000, 100000)\">1w10w</vue-button>\n        <vue-button @click=\"$refs.xGrid.toggleRowSelection($refs.xGrid.getData(1))\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.setSelection([$refs.xGrid.getData(2), $refs.xGrid.getData(3)], true)\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.setAllSelection(true)\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.clearSelection()\"></vue-button>\n        <vue-button @click=\"getSelectEvent\"></vue-button>\n      </template>\n    </vue-xgrid></div>","parameter": {
  data () {
    return {
      loading: false,
      tableToolbar: {},

    }
  },
  created () {
    this.loadColumnAndData(200, 600)
  },

  methods: {
    loadColumnAndData (colSize, rowSize) {
      this.loading = true
      Promise.all([
        this.loadColumn(colSize),
        this.loadList(rowSize)
      ]).then(() => {
        this.loading = false
      })
    },
    loadColumn (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          //  vue 
          let tableColumn = window.MOCK_COLUMN_LIST.slice(0, size)
          this.$refs.xGrid.loadColumn(tableColumn).then(() => {
            resolve()
          })
        }, 300)
      })
    },
    loadList (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          //  vue 
          let tableData = window.MOCK_DATA_LIST.slice(0, size)
          this.$refs.xGrid.reloadData(tableData).then(() => {
            resolve()
          })
        }, 300)
      })
    },
    getSelectEvent () {
      let selectRecords = this.$refs.xGrid.getSelectRecords()
      this.$alert({ message: selectRecords.length })
    }
  }
}}],"styles":""}