export default {"path":"/XtableDemo/grid/Toolbar","name":"XtableDemo.grid.Toolbar","head":{"label":"XtableDemo.grid.Toolbar.label","description":"XtableDemo.grid.Toolbar.description"},"samples":[{"id":"XtableDemo.grid.Toolbar.sample1","label":"XtableDemo.grid.Toolbar.sample1.label","description":"XtableDemo.grid.Toolbar.sample1.description","code":"<vue-xgrid\nborder\nresizable\nheight=\"530\"\n:pager-config=\"tablePage\"\n:proxy-config=\"tableProxy\"\n:columns=\"tableColumn\"\n:toolbar=\"tableToolbar\"\n:edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n@toolbar-button-click=\"toolbarButtonClickEvent\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tablePage: {\n        pageSize: 15\n      },\n      tableProxy: {\n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          // page  { pageSize, currentPage }\n          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),\n          // body  { removeRecords }\n          delete: ({ body }) => Vue.http.post('/api/user/save', body),\n          // body  { insertRecords, updateRecords, removeRecords, pendingRecords }\n          save: ({ body }) => Vue.http.post('/api/user/save', body)\n        }\n      },\n      tableToolbar: {\n        id: 'toolbar_demo_1',\n        buttons: [\n          { code: 'insert_actived', name: '' },\n          {\n            code: 'mark_cancel',\n            name: '',\n            dropdowns: [\n              { code: 'delete_selection', name: '' },\n              { code: 'remove_selection', name: '' }\n            ]\n          },\n          { code: 'save', name: '' },\n          {\n            name: '',\n            dropdowns: [\n              { code: 'open_import', name: '' },\n              { code: 'open_export', name: '' }\n            ]\n          },\n          { code: 'exportData111', name: '' }\n        ],\n        export: true,\n        refresh: true,\n        resizable: {\n          storage: true\n        },\n        setting: {\n          storage: true\n        }\n      },\n      tableColumn: [\n        { type: 'checkbox', width: 50 },\n        { type: 'index', width: 60 },\n        { field: 'name', title: 'Name', editRender: { name: 'input' } },\n        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },\n        { field: 'role', title: 'Role', editRender: { name: 'input' } },\n        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }\n      ],\n\n    }\n  },\n\n  methods: {\n    toolbarButtonClickEvent ({ code }, event) {\n      switch (code) {\n        case 'myBtn':\n          this.$alert({ message: code })\n          break\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      height=\"530\"\n      :pager-config=\"tablePage\"\n      :proxy-config=\"tableProxy\"\n      :columns=\"tableColumn\"\n      :toolbar=\"tableToolbar\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      @toolbar-button-click=\"toolbarButtonClickEvent\"></vue-xgrid></div>","parameter": {
  data () {
    return {
      tablePage: {
        pageSize: 15
      },
      tableProxy: {
        props: {
          result: 'data.result',
          total: 'data.page.total'
        },
        ajax: {
          // page  { pageSize, currentPage }
          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),
          // body  { removeRecords }
          delete: ({ body }) => Vue.http.post('/api/user/save', body),
          // body  { insertRecords, updateRecords, removeRecords, pendingRecords }
          save: ({ body }) => Vue.http.post('/api/user/save', body)
        }
      },
      tableToolbar: {
        id: 'toolbar_demo_1',
        buttons: [
          { code: 'insert_actived', name: '' },
          {
            code: 'mark_cancel',
            name: '',
            dropdowns: [
              { code: 'delete_selection', name: '' },
              { code: 'remove_selection', name: '' }
            ]
          },
          { code: 'save', name: '' },
          {
            name: '',
            dropdowns: [
              { code: 'open_import', name: '' },
              { code: 'open_export', name: '' }
            ]
          },
          { code: 'exportData111', name: '' }
        ],
        export: true,
        refresh: true,
        resizable: {
          storage: true
        },
        setting: {
          storage: true
        }
      },
      tableColumn: [
        { type: 'checkbox', width: 50 },
        { type: 'index', width: 60 },
        { field: 'name', title: 'Name', editRender: { name: 'input' } },
        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },
        { field: 'role', title: 'Role', editRender: { name: 'input' } },
        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }
      ],

    }
  },

  methods: {
    toolbarButtonClickEvent ({ code }, event) {
      switch (code) {
        case 'myBtn':
          this.$alert({ message: code })
          break
      }
    }
  }
}}],"styles":""}