export default {"path":"/XtableDemo/scroll/FullPartialLoad","name":"XtableDemo.scroll.FullPartialLoad","head":{"label":"XtableDemo.scroll.FullPartialLoad.label","description":"XtableDemo.scroll.FullPartialLoad.description"},"samples":[{"id":"XtableDemo.scroll.FullPartialLoad.sample1","label":"XtableDemo.scroll.FullPartialLoad.sample1.label","description":"XtableDemo.scroll.FullPartialLoad.sample1.description","code":"<vue-toolbar :loading=\"loading\">\n  <template v-slot:buttons>\n    <vue-button @click=\"loadList(20)\">+20</vue-button>\n    <vue-button @click=\"loadList(50)\">+50</vue-button>\n    <vue-button @click=\"loadList(100)\">+100</vue-button>\n    <vue-button @click=\"loadList(200)\">+200</vue-button>\n    <vue-button @click=\"loadList(300)\">+300</vue-button>\n    <vue-button @click=\"loadColumns(10)\">+10</vue-button>\n    <vue-button @click=\"loadColumns(30)\">+30</vue-button>\n    <vue-button @click=\"loadColumns(50)\">+50</vue-button>\n    <vue-button @click=\"$refs.xGrid.scrollTo(1000, 2000)\">x=1000,y=2000</vue-button>\n    <vue-button @click=\"$refs.xGrid.scrollTo(2000, 6000)\">x=2000,y=6000</vue-button>\n    <vue-button @click=\"$refs.xGrid.clearScroll()\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xgrid\n  border\n  show-overflow\n  show-header-overflow\n  ref=\"xGrid\"\n  height=\"500\"\n  row-id=\"id\"\n  :checkbox-config=\"{checkField: 'checked', labelField: 'id'}\"\n  :loading=\"loading\">\n</vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      colIndex: 0,\n      rowIndex: 1,\n\n    }\n  },\n  created () {\n    //  vue \n    this.allData = []\n    this.allColumn = []\n    this.init()\n  },\n\n  methods: {\n    init () {\n      this.loading = true\n      Promise.all([\n        this.findColumnList(200).then(data => {\n          this.allColumn = this.allData.concat(data)\n          this.$refs.xGrid.loadColumn(this.allColumn)\n        }),\n        this.findDataList(600).then(data => {\n          this.allData = this.allData.concat(data)\n          this.$refs.xGrid.loadData(this.allData)\n        })\n      ]).then(() => {\n        this.loading = false\n      })\n    },\n    loadColumns (size) {\n      this.loading = true\n      this.findColumnList(size).then(data => {\n        this.allColumn = this.allColumn.concat(data)// \n        this.$refs.xGrid.loadColumn(this.allColumn)\n        this.loading = false\n      })\n    },\n    loadList (size) {\n      this.loading = true\n      this.findDataList(size).then(data => {\n        this.allData = this.allData.concat(data)// \n        this.$refs.xGrid.loadData(this.allData)\n        this.loading = false\n      })\n    },\n    findColumnList (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          var columns = []\n          for (var index = 0; index < size; index++) {\n            let key = this.colIndex++\n            let config = {\n              field: key ? `col_${key}` : 'id',\n              title: key ? `_${key}` : 'ID',\n              width: 140\n            }\n            if (!key) {\n              config.type = 'checkbox'\n            }\n            if (key < 2) {\n              config.fixed = 'left'\n            }\n            columns.push(config)\n          }\n          resolve(columns)\n        }, 250)\n      })\n    },\n    findDataList (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          var list = []\n          for (var index = 0; index < size; index++) {\n            let key = this.rowIndex++\n            let item = { id: key, checked: false }\n            // 1000\n\n            for (let cIndex = 0; cIndex < 1000; cIndex++) {\n              item[`col_${cIndex}`] = `_${cIndex}_${index}`\n            }\n            list.push(item)\n          }\n          resolve(list)\n        }, 250)\n      })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :loading=\"loading\">\n      <template v-slot:buttons>\n        <vue-button @click=\"loadList(20)\">+20</vue-button>\n        <vue-button @click=\"loadList(50)\">+50</vue-button>\n        <vue-button @click=\"loadList(100)\">+100</vue-button>\n        <vue-button @click=\"loadList(200)\">+200</vue-button>\n        <vue-button @click=\"loadList(300)\">+300</vue-button>\n        <vue-button @click=\"loadColumns(10)\">+10</vue-button>\n        <vue-button @click=\"loadColumns(30)\">+30</vue-button>\n        <vue-button @click=\"loadColumns(50)\">+50</vue-button>\n        <vue-button @click=\"$refs.xGrid.scrollTo(1000, 2000)\">x=1000,y=2000</vue-button>\n        <vue-button @click=\"$refs.xGrid.scrollTo(2000, 6000)\">x=2000,y=6000</vue-button>\n        <vue-button @click=\"$refs.xGrid.clearScroll()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xgrid\n      border\n      show-overflow\n      show-header-overflow\n      ref=\"xGrid\"\n      height=\"500\"\n      row-id=\"id\"\n      :checkbox-config=\"{checkField: 'checked', labelField: 'id'}\"\n      :loading=\"loading\">\n    </vue-xgrid></div>","parameter": {
  data () {
    return {
      loading: false,
      colIndex: 0,
      rowIndex: 1,

    }
  },
  created () {
    //  vue 
    this.allData = []
    this.allColumn = []
    this.init()
  },

  methods: {
    init () {
      this.loading = true
      Promise.all([
        this.findColumnList(200).then(data => {
          this.allColumn = this.allData.concat(data)
          this.$refs.xGrid.loadColumn(this.allColumn)
        }),
        this.findDataList(600).then(data => {
          this.allData = this.allData.concat(data)
          this.$refs.xGrid.loadData(this.allData)
        })
      ]).then(() => {
        this.loading = false
      })
    },
    loadColumns (size) {
      this.loading = true
      this.findColumnList(size).then(data => {
        this.allColumn = this.allColumn.concat(data)// 
        this.$refs.xGrid.loadColumn(this.allColumn)
        this.loading = false
      })
    },
    loadList (size) {
      this.loading = true
      this.findDataList(size).then(data => {
        this.allData = this.allData.concat(data)// 
        this.$refs.xGrid.loadData(this.allData)
        this.loading = false
      })
    },
    findColumnList (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          var columns = []
          for (var index = 0; index < size; index++) {
            let key = this.colIndex++
            let config = {
              field: key ? `col_${key}` : 'id',
              title: key ? `_${key}` : 'ID',
              width: 140
            }
            if (!key) {
              config.type = 'checkbox'
            }
            if (key < 2) {
              config.fixed = 'left'
            }
            columns.push(config)
          }
          resolve(columns)
        }, 250)
      })
    },
    findDataList (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          var list = []
          for (var index = 0; index < size; index++) {
            let key = this.rowIndex++
            let item = { id: key, checked: false }
            // 1000

            for (let cIndex = 0; cIndex < 1000; cIndex++) {
              item[`col_${cIndex}`] = `_${cIndex}_${index}`
            }
            list.push(item)
          }
          resolve(list)
        }, 250)
      })
    }
  }
}}],"styles":""}