}},{"id":"InputNumberDemo.sample11","label":"InputNumberDemo.sample11.label","description":"InputNumberDemo.sample11.description","code":"<vue-number-input v-model=\"num\" :formatter=\"formatter\"></vue-number-input>\r\n<br>\r\nreal value: {{num}}\n\n<script>\nnew Vue({\r\n  data () {\r\n    return {\r\r\n      num: 100000,\r\r\n    }\r\n  },\r\n  methods: {\r\n    handleChange: function(value) {\r\n      console.log(value);\r\n    },\r\r\n    formatter: function(currentVal, isFocusing) {\r\n      if (isFocusing) {\r\n          return currentVal;\r\n      }\r\n      return Math.round(parseFloat(currentVal) / 10000) + '';\r\n    }\r\r\n  }\r\n})\n</script>","template":"<div class=\"source\">    <vue-number-input v-model=\"num\" :formatter=\"formatter\"></vue-number-input>\r\n    <br>\r\n    real value: {{num}}</div>","parameter": {
  data () {
    return {
      num: 100000,
    }
  },
  methods: {
    handleChange: function(value) {
      console.log(value);
    },
    formatter: function(currentVal, isFocusing) {
      if (isFocusing) {
          return currentVal;
      }
      return Math.round(parseFloat(currentVal) / 10000) + '';
    }
  }
