}},{"id":"XtableDemo.advanced.Span.sample2","label":"XtableDemo.advanced.Span.sample2.label","description":"XtableDemo.advanced.Span.sample2.description","code":"<vue-xtable\n  border\n  height=\"400\"\n  :span-method=\"rowspanMethod\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"key\" title=\"Key\"></vue-xtable-column>\n  <vue-xtable-column field=\"content\" title=\"Translate\"></vue-xtable-column>\n  <vue-xtable-column field=\"language\" title=\"Language\" :filters=\"[{label: '', value: 'zh_CN'}, {label: 'English', value: 'en_US'}]\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 20)\n    this.tableData = list\n  },\n\n  methods: {\n    // \n    rowspanMethod ({ row, $rowIndex, column, data }) {\n      let fields = ['key']\n      let cellValue = row[column.property]\n      if (cellValue && VueUtil.includes(fields, column.property)) {\n        let prevRow = data[$rowIndex - 1]\n        let nextRow = data[$rowIndex + 1]\n        if (prevRow && prevRow[column.property] === cellValue) {\n          return { rowspan: 0, colspan: 0 }\n        } else {\n          let countRowspan = 1\n          while (nextRow && nextRow[column.property] === cellValue) {\n            nextRow = data[++countRowspan + $rowIndex]\n          }\n          if (countRowspan > 1) {\n            return { rowspan: countRowspan, colspan: 1 }\n          }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      height=\"400\"\n      :span-method=\"rowspanMethod\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"key\" title=\"Key\"></vue-xtable-column>\n      <vue-xtable-column field=\"content\" title=\"Translate\"></vue-xtable-column>\n      <vue-xtable-column field=\"language\" title=\"Language\" :filters=\"[{label: '', value: 'zh_CN'}, {label: 'English', value: 'en_US'}]\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 20)
    this.tableData = list
  },

  methods: {
    // 
    rowspanMethod ({ row, $rowIndex, column, data }) {
      let fields = ['key']
      let cellValue = row[column.property]
      if (cellValue && VueUtil.includes(fields, column.property)) {
        let prevRow = data[$rowIndex - 1]
        let nextRow = data[$rowIndex + 1]
        if (prevRow && prevRow[column.property] === cellValue) {
          return { rowspan: 0, colspan: 0 }
        } else {
          let countRowspan = 1
          while (nextRow && nextRow[column.property] === cellValue) {
            nextRow = data[++countRowspan + $rowIndex]
          }
          if (countRowspan > 1) {
            return { rowspan: countRowspan, colspan: 1 }
          }
        }
      }
    }
  }
