export default {"path":"/XtableDemo/grid/TreeEdit","name":"XtableDemo.grid.TreeEdit","head":{"label":"XtableDemo.grid.TreeEdit.label","description":"XtableDemo.grid.TreeEdit.description"},"samples":[{"id":"XtableDemo.grid.TreeEdit.sample1","label":"XtableDemo.grid.TreeEdit.sample1.label","description":"XtableDemo.grid.TreeEdit.sample1.description","code":"<vue-xgrid\nresizable\nhighlight-hover-row\n:proxy-config=\"tableProxy\"\n:columns=\"tableColumn\"\n:toolbar=\"tableToolbar\"\n:checkbox-config=\"{labelField: 'id'}\"\n:tree-config=\"{children: 'children'}\"\n:edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableProxy: {\n        ajax: {\n          // \n          query: () => Vue.http.get('/api/file/list').then(data => VueUtil.toArrayTree(data.body, { key: 'id', parentKey: 'parentId', children: 'children' })),\n          save: ({ body }) => Vue.http.post('/api/file/save', body)\n        }\n      },\n      tableToolbar: {\n        id: 'treeEdit_demo1',\n        buttons: [\n          { code: 'reload', name: '', disabled: false },\n          { code: 'mark_cancel', name: '/', disabled: false },\n          { code: 'save', name: '', disabled: false },\n          { code: 'export', name: '.csv', disabled: false },\n          { code: 'other0', name: '1', disabled: true },\n          {\n            name: '',\n            disabled: false,\n            dropdowns: [\n              {\n                code: 'other1',\n                name: '1',\n                disabled: false\n              },\n              {\n                code: 'other2',\n                name: '2',\n                disabled: true\n              },\n              {\n                code: 'other3',\n                name: '3',\n                disabled: false\n              }\n            ]\n          }\n        ],\n        refresh: true,\n        resizable: {\n          storage: true\n        },\n        setting: {\n      