export default {"path":"/XtableDemo/base/DynamicStyle","name":"XtableDemo.base.DynamicStyle","head":{"label":"XtableDemo.base.DynamicStyle.label","description":"XtableDemo.base.DynamicStyle.description"},"samples":[{"id":"XtableDemo.base.DynamicStyle.sample1","label":"XtableDemo.base.DynamicStyle.sample1.label","description":"XtableDemo.base.DynamicStyle.sample1.description","code":"<vue-xtable\n  border\n  :header-cell-style=\"headerCellStyle\"\n  :row-style=\"rowStyle\"\n  :cell-style=\"cellStyle\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n  },\n\n  methods: {\n    headerCellStyle ({ column, columnIndex }) {\n      if (column.property === 'name') {\n        return {\n          backgroundColor: '#f60',\n          color: '#ffffff'\n        }\n      }\n    },\n    rowStyle ({ row, rowIndex }) {\n      if (VueUtil.includes([2, 3, 5], rowIndex)) {\n        return {\n          backgroundColor: 'red',\n          color: '#ffffff'\n        }\n      }\n    },\n    cellStyle ({ row, rowIndex, column, columnIndex }) {\n      if (column.property === 'sex') {\n        if (row.sex >= '1') {\n          return {\n            backgroundColor: '#187'\n          }\n        } else if (row.age === 26) {\n          return {\n            backgroundColor: '#2db7f5'\n          }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      :header-cell-style=\"headerCellStyle\"\n      :row-style=\"rowStyle\"\n      :cell-style=\"cellStyle\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)
  },

  methods: {
    headerCellStyle ({ column, columnIndex }) {
      if (column.property === 'name') {
        return {
          backgroundColor: '#f60',
          color: '#ffffff'
        }
      }
    },
    rowStyle ({ row, rowIndex }) {
      if (VueUtil.includes([2, 3, 5], rowIndex)) {
        return {
          backgroundColor: 'red',
          color: '#ffffff'
        }
      }
    },
    cellStyle ({ row, rowIndex, column, columnIndex }) {
      if (column.property === 'sex') {
        if (row.sex >= '1') {
          return {
            backgroundColor: '#187'
          }
        } else if (row.age === 26) {
          return {
            backgroundColor: '#2db7f5'
          }
        }
      }
    }
  }
}}],"styles":""}