 ['key']\n      let cellValue = row[column.property]\n      if (cellValue && VueUtil.includes(fields, column.property)) {\n        let prevRow = data[$rowIndex - 1]\n        let nextRow = data[$rowIndex + 1]\n        if (prevRow && prevRow[column.property] === cellValue) {\n          return { rowspan: 0, colspan: 0 }\n        } else {\n          let countRowspan = 1\n          while (nextRow && nextRow[column.property] === cellValue) {\n            nextRow = data[++countRowspan + $rowIndex]\n          }\n          if (countRowspan > 1) {\n            return { rowspan: countRowspan, colspan: 1 }\n          }\n        }\n      }\n    },\n    footerRowspanMethod ({ $rowIndex, column, columnIndex, data }) {\n      if ($rowIndex === 0) {\n        if (columnIndex === 2) {\n          return { rowspan: 2, colspan: 1 }\n        }\n      } else if ($rowIndex === 1) {\n        if (columnIndex === 2) {\n          return { rowspan: 0, colspan: 0 }\n        }\n      }\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      show-footer\n      height=\"400\"\n      :span-method=\"rowspanMethod\"\n      :footer-span-method=\"footerRowspanMethod\"\n      :footer-method=\"footerMethod\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"key\" title=\"Key\"></vue-xtable-column>\n      <vue-xtable-column field=\"content\" title=\"Translate\"></vue-xtable-column>\n      <vue-xtable-column field=\"language\" title=\"Language\" :filters=\"[{label: '', value: 'zh_CN'}, {label: 'English', value: 'en_US'}]\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 20)
    this.tableData = list
  },

  methods: {
    footerMethod ({ columns, data }) {
      const footerData = [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          // 
          if (VueUtil.includes(['age', 'rate'], column.property)) {
            return VueUtil.mean(data.map( data => data.age))
          }
          if (VueUtil.includes(['content'], column.property)) {
            return ''
          }
          return null
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          // 
          if (VueUtil.includes(['age', 'rate'], column.property)) {
            return VueUtil.sum(data.map( data => data.age))
          }
          if (VueUtil.includes(['content'], column.property)) {
            return ''
          }
          return null
        })
      ]
      return footerData
    },
    // 
    rowspanMethod ({ row, $rowIndex, column, data }) {
      let fields = ['key']
      let cellValue = row[column.property]
      if (cellValue && VueUtil.includes(fields, column.property)) {
        let prevRow = data[$rowIndex - 1]
        let nextRow = data[$rowIndex + 1]
        if (prevRow && prevRow[column.property] === cellValue) {
          return { rowspan: 0, colspan: 0 }
        } else {
          let countRowspan = 1
          while (nextRow && nextRow[column.property] === cellValue) {
            nextRow = data[++countRowspan + $rowIndex]
          }
          if (countRowspan > 1) {
            return { rowspan: countRowspan, colspan: 1 }
          }
        }
      }
    },
    footerRowspanMethod ({ $rowIndex, column, columnIndex, data }) {
      if ($rowIndex === 0) {
        if (columnIndex === 2) {
          return { rowspan: 2, colspan: 1 }
        }
      } else if ($rowIndex === 1) {
        if (columnIndex === 2) {
          return { rowspan: 0, colspan: 0 }
        }
      }
    }
  }
