export default {"path":"/XtableDemo/tree/RowValid","name":"XtableDemo.tree.RowValid","head":{"label":"XtableDemo.tree.RowValid.label","description":"XtableDemo.tree.RowValid.description"},"samples":[{"id":"XtableDemo.tree.RowValid.sample1","label":"XtableDemo.tree.RowValid.sample1.label","description":"XtableDemo.tree.RowValid.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"validEvent\"></vue-button>\n    <vue-button @click=\"fullValidEvent\"></vue-button>\n    <vue-button @click=\"selectValidEvent\"></vue-button>\n    <vue-button @click=\"getSelectEvent\"></vue-button>\n    <vue-button @click=\"getUpdateEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  resizable\n  ref=\"xTree\"\n  :edit-rules=\"validRules\"\n  :tree-config=\"treeConfig\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n  :checkbox-config=\"{labelField: 'id'}\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"checkbox\" title=\"ID\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      treeConfig: {\n        children: 'children'\n      },\n      validRules: {\n        name: [\n          { required: true, message: '' },\n          { min: 3, max: 50, message: ' 3  50 ' }\n        ]\n      },\n\n    }\n  },\n  created () {\n    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)\n  },\n\n  methods: {\n    validEvent () {\n      this.$refs.xTree.validate(valid => {\n        if (valid) {\n          this.$notify.success({ message:''});\n        } else {\n          this.$notify.error({ message:''});\n        }\n      })\n    },\n    fullValidEvent () {\n      this.$refs.xTree.fullValidate((valid, errMap) => {\n        if (valid) {\n          this.$notify.success({ message:''});\n        } else {\n          let msgList = []\n          Object.values(errMap).forEach(errList => {\n            errList.forEach(params => {\n              let { row, column, rules } = params\n              let matchObj = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)\n              let seq = matchObj.path.filter(item => item !== this.treeConfig.children).map(item => Number(item) + 1).join('.')\n              rules.forEach(rule => {\n                msgList.push(` ${seq}  ${column.title} ${rule.message}`)\n              })\n            })\n          })\n\n          var h = this.$createElement;\n          this.$alert({\n            type: \"error\",\n            message: h('div',[msgList.map(function (msg) {\n              return h(\"div\", [msg]);\n            })])\n          });\n        }\n      })\n    },\n    selectValidEvent () {\n      let selectRecords = this.$refs.xTree.getSelectRecords()\n      if (selectRecords.length > 0) {\n        this.$refs.xTree.validate(selectRecords, valid => {\n          if (valid) {\n            this.$notify.success({ message:''});\n          } else {\n            this.$notify.error({ message:''});\n          }\n        })\n      } else {\n        this.$notify.warning({ message:''});\n      }\n    },\n    getSelectEvent () {\n      let selectRecords = this.$refs.xTree.getSelectRecords()\n      this.$alert({ message: selectRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTree.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"validEvent\"></vue-button>\n        <vue-button @click=\"fullValidEvent\"></vue-button>\n        <vue-button @click=\"selectValidEvent\"></vue-button>\n        <vue-button @click=\"getSelectEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      resizable\n      ref=\"xTree\"\n      :edit-rules=\"validRules\"\n      :tree-config=\"treeConfig\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      :checkbox-config=\"{labelField: 'id'}\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"checkbox\" title=\"ID\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      treeConfig: {
        children: 'children'
      },
      validRules: {
        name: [
          { required: true, message: '' },
          { min: 3, max: 50, message: ' 3  50 ' }
        ]
      },

    }
  },
  created () {
    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)
  },

  methods: {
    validEvent () {
      this.$refs.xTree.validate(valid => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          this.$notify.error({ message:''});
        }
      })
    },
    fullValidEvent () {
      this.$refs.xTree.fullValidate((valid, errMap) => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          let msgList = []
          Object.values(errMap).forEach(errList => {
            errList.forEach(params => {
              let { row, column, rules } = params
              let matchObj = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)
              let seq = matchObj.path.filter(item => item !== this.treeConfig.children).map(item => Number(item) + 1).join('.')
              rules.forEach(rule => {
                msgList.push(` ${seq}  ${column.title} ${rule.message}`)
              })
            })
          })

          var h = this.$createElement;
          this.$alert({
            type: "error",
            message: h('div',[msgList.map(function (msg) {
              return h("div", [msg]);
            })])
          });
        }
      })
    },
    selectValidEvent () {
      let selectRecords = this.$refs.xTree.getSelectRecords()
      if (selectRecords.length > 0) {
        this.$refs.xTree.validate(selectRecords, valid => {
          if (valid) {
            this.$notify.success({ message:''});
          } else {
            this.$notify.error({ message:''});
          }
        })
      } else {
        this.$notify.warning({ message:''});
      }
    },
    getSelectEvent () {
      let selectRecords = this.$refs.xTree.getSelectRecords()
      this.$alert({ message: selectRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTree.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    }
  }
}}],"styles":""}