export default {"path":"/XtableDemo/grid/Page","name":"XtableDemo.grid.Page","head":{"label":"XtableDemo.grid.Page.label","description":"XtableDemo.grid.Page.description"},"samples":[{"id":"XtableDemo.grid.Page.sample1","label":"XtableDemo.grid.Page.sample1.label","description":"XtableDemo.grid.Page.sample1.description","code":"<vue-xgrid\nborder\nresizable\nheight=\"530\"\n:loading=\"loading\"\n:start-index=\"(tablePage.currentPage - 1) * tablePage.pageSize\"\n:pager-config=\"tablePage\"\n:columns=\"tableColumn\"\n:data=\"tableData\"\n@page-change=\"handlePageChange\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tablePage: {\n        total: 0,\n        currentPage: 1,\n        pageSize: 10,\n        pageSizes: [10, 20, 50, 100, 200, 500]\n      },\n      tableColumn: [\n        { type: 'index', width: 60 },\n        { type: 'checkbox', width: 50 },\n        { field: 'name', title: 'Name' },\n        { field: 'nickname', title: 'Nickname' },\n        { field: 'role', title: 'Role' },\n        { field: 'describe', title: 'Describe', showOverflow: true }\n      ],\n      tableData: [],\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      // \n      this.loading = true\n      Vue.http.get(`/api/user/page/list/${this.tablePage.pageSize}/${this.tablePage.currentPage}`).then(response => {\n        let { page, result } = response.data\n        this.tableData = result\n        this.tablePage.total = page.total\n        this.loading = false\n      }).catch(e => {\n        this.loading = false\n      })\n    },\n    searchEvent () {\n      this.tablePage.currentPage = 1\n      this.findList()\n    },\n    handlePageChange ({ currentPage, pageSize }) {\n      this.tablePage.currentPage = currentPage\n      this.tablePage.pageSize = pageSize\n      this.findList()\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      height=\"530\"\n      :loading=\"loading\"\n      :start-index=\"(tablePage.currentPage - 1) * tablePage.pageSize\"\n      :pager-config=\"tablePage\"\n      :columns=\"tableColumn\"\n      :data=\"tableData\"\n      @page-change=\"handlePageChange\"></vue-xgrid></div>","parameter": {
  data () {
    return {
      loading: false,
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [10, 20, 50, 100, 200, 500]
      },
      tableColumn: [
        { type: 'index', width: 60 },
        { type: 'checkbox', width: 50 },
        { field: 'name', title: 'Name' },
        { field: 'nickname', title: 'Nickname' },
        { field: 'role', title: 'Role' },
        { field: 'describe', title: 'Describe', showOverflow: true }
      ],
      tableData: [],

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      // 
      this.loading = true
      Vue.http.get(`/api/user/page/list/${this.tablePage.pageSize}/${this.tablePage.currentPage}`).then(response => {
        let { page, result } = response.data
        this.tableData = result
        this.tablePage.total = page.total
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    searchEvent () {
      this.tablePage.currentPage = 1
      this.findList()
    },
    handlePageChange ({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage
      this.tablePage.pageSize = pageSize
      this.findList()
    }
  }
}}],"styles":""}