export default {"path":"/XtableDemo/tree/Insert","name":"XtableDemo.tree.Insert","head":{"label":"XtableDemo.tree.Insert.label","description":"XtableDemo.tree.Insert.description"},"samples":[{"id":"XtableDemo.tree.Insert.sample1","label":"XtableDemo.tree.Insert.sample1.label","description":"XtableDemo.tree.Insert.sample1.description","code":"<vue-toolbar :data=\"tableData\" setting>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent()\"></vue-button>\n    <vue-button @click=\"insertAtEvent()\"></vue-button>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  resizable\n  ref=\"xTree\"\n  :tree-config=\"treeConfig\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"checkbox\" width=\"120\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      treeConfig: {\n        children: 'children'\n      },\n\n    }\n  },\n  created () {\n    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)\n  },\n\n  methods: {\n    insertEvent () {\n      let xTree = this.$refs.xTree\n      xTree.createRow({\n        name: '',\n        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),\n        isNew: true\n      }).then(newRow => {\n        // \n        this.tableData.unshift(newRow)\n        xTree.refreshData().then(() => xTree.setActiveRow(newRow))\n      })\n    },\n    insertAtEvent () {\n      let xTree = this.$refs.xTree\n      xTree.createRow({\n        name: '',\n        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),\n        isNew: true\n      }).then(newRow => {\n        //  id  11000 \n        let rowNode = VueUtil.findTree(this.tableData, item => item.id === '11000', this.treeConfig)\n        if (rowNode) {\n          rowNode.items.splice(rowNode.index, 0, newRow)\n          xTree.refreshData().then(() => xTree.setActiveRow(newRow))\n        }\n      })\n    },\n    getInsertEvent () {\n      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)\n      this.$alert({ message: insertRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :data=\"tableData\" setting>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent()\"></vue-button>\n        <vue-button @click=\"insertAtEvent()\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      resizable\n      ref=\"xTree\"\n      :tree-config=\"treeConfig\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"checkbox\" width=\"120\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      treeConfig: {
        children: 'children'
      },

    }
  },
  created () {
    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)
  },

  methods: {
    insertEvent () {
      let xTree = this.$refs.xTree
      xTree.createRow({
        name: '',
        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),
        isNew: true
      }).then(newRow => {
        // 
        this.tableData.unshift(newRow)
        xTree.refreshData().then(() => xTree.setActiveRow(newRow))
      })
    },
    insertAtEvent () {
      let xTree = this.$refs.xTree
      xTree.createRow({
        name: '',
        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),
        isNew: true
      }).then(newRow => {
        //  id  11000 
        let rowNode = VueUtil.findTree(this.tableData, item => item.id === '11000', this.treeConfig)
        if (rowNode) {
          rowNode.items.splice(rowNode.index, 0, newRow)
          xTree.refreshData().then(() => xTree.setActiveRow(newRow))
        }
      })
    },
    getInsertEvent () {
      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)
      this.$alert({ message: insertRecords.length })
    }
  }
}}],"styles":""}