export default {"path":"/XtableDemo/edit/Manual","name":"XtableDemo.edit.Manual","head":{"label":"XtableDemo.edit.Manual.label","description":"XtableDemo.edit.Manual.description"},"samples":[{"id":"XtableDemo.edit.Manual.sample1","label":"XtableDemo.edit.Manual.sample1.label","description":"XtableDemo.edit.Manual.sample1.description","code":"<vue-xtable\n  ref=\"xTable\"\n  border\n  resizable\n  show-overflow\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'manual', mode: 'row'}\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"role\" title=\"Role\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'select', options: sexList}\"></vue-xtable-column>\n  <vue-xtable-column field=\"num6\" title=\"Number\" :edit-render=\"{name: 'input', attrs: {type: 'number'}}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date12\" title=\"Date\" :edit-render=\"{name: 'input', attrs: {type: 'date'}}\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column title=\"\">\n    <template v-slot=\"scope\">\n      <template v-if=\"$refs.xTable.isActiveByRow(scope.row)\">\n        <vue-button @click=\"saveRowEvent(scope.row)\"></vue-button>\n        <vue-button @click=\"cancelRowEvent(scope.row)\"></vue-button>\n      </template>\n      <template v-else>\n        <vue-button @click=\"editRowEvent(scope.row)\"></vue-button>\n      </template>\n    </template>\n  </vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      sexList: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n    this.findSexList()\n  },\n\n  methods: {\n    findSexList () {\n      return Vue.http.get('/api/conf/sex/list').then(data => {\n        this.sexList = data.data;\n      })\n    },\n    editRowEvent (row) {\n      this.$refs.xTable.setActiveRow(row)\n    },\n    saveRowEvent (row) {\n      this.$refs.xTable.clearActived().then(() => {\n        this.$alert({ message: 'success' })\n      })\n    },\n    cancelRowEvent (row) {\n      this.$refs.xTable.clearActived()\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      ref=\"xTable\"\n      border\n      resizable\n      show-overflow\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'manual', mode: 'row'}\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"role\" title=\"Role\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'select', options: sexList}\"></vue-xtable-column>\n      <vue-xtable-column field=\"num6\" title=\"Number\" :edit-render=\"{name: 'input', attrs: {type: 'number'}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date12\" title=\"Date\" :edit-render=\"{name: 'input', attrs: {type: 'date'}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column title=\"\">\n        <template v-slot=\"scope\">\n          <template v-if=\"$refs.xTable.isActiveByRow(scope.row)\">\n            <vue-button @click=\"saveRowEvent(scope.row)\"></vue-button>\n            <vue-button @click=\"cancelRowEvent(scope.row)\"></vue-button>\n          </template>\n          <template v-else>\n            <vue-button @click=\"editRowEvent(scope.row)\"></vue-button>\n          </template>\n        </template>\n      </vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      sexList: [],

    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)
    this.findSexList()
  },

  methods: {
    findSexList () {
      return Vue.http.get('/api/conf/sex/list').then(data => {
        this.sexList = data.data;
      })
    },
    editRowEvent (row) {
      this.$refs.xTable.setActiveRow(row)
    },
    saveRowEvent (row) {
      this.$refs.xTable.clearActived().then(() => {
        this.$alert({ message: 'success' })
      })
    },
    cancelRowEvent (row) {
      this.$refs.xTable.clearActived()
    }
  }
