  methods: {
    loadList (size) {
      this.loading = true
      setTimeout(() => {
        let xTable = this.$refs.xTable
        if (xTable) {
          //  vue 
          xTable.reloadData(window.MOCK_DATA_LIST.slice(0, size)).then(() => {
            this.loading = false
          })
        } else {
          this.loading = false
        }
      }, 300)
    },
    getSelectEvent () {
      let selectRecords = this.$refs.xTable.getSelectRecords()
      this.$alert({ message: selectRecords.length })
    }
  }
