le\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input', events: {keydown: keydownEvent}}\"></vue-xtable-column>\n      <vue-xtable-column\n        field=\"role\"\n        title=\"Role\"\n        :filters=\"[{ data: '' }]\"\n        :filter-render=\"{name: 'input', attrs: {placeholder: ''}, events: {keyup: enterFilterEvent}}\"\n        :edit-render=\"{name: 'input', events: {focus: focusEvent}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'select', options: sexList, events: {change: changeEvent}}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
