export default {"path":"/XtableDemo/tree/Toolbar","name":"XtableDemo.tree.Toolbar","head":{"label":"XtableDemo.tree.Toolbar.label","description":"XtableDemo.tree.Toolbar.description"},"samples":[{"id":"XtableDemo.tree.Toolbar.sample1","label":"XtableDemo.tree.Toolbar.sample1.label","description":"XtableDemo.tree.Toolbar.sample1.description","code":"<vue-toolbar :data=\"tableData\" export setting>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent\">{{ $t('') }}</vue-button>\n    <vue-button @click=\"removeEvent\"></vue-button>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n    <vue-button @click=\"getRemoveEvent\"></vue-button>\n    <vue-button @click=\"getUpdateEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  resizable\n  ref=\"xTree\"\n  :tree-config=\"treeConfig\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"checkbox\" width=\"120\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      removeList: [],\n      treeConfig: {\n        children: 'children'\n      },\n\n    }\n  },\n  created () {\n    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)\n  },\n\n  methods: {\n    insertEvent () {\n      let xTree = this.$refs.xTree\n      xTree.createRow({\n        name: '',\n        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),\n        isNew: true\n      }).then(newRow => {\n        // \n        this.tableData.unshift(newRow)\n        xTree.refreshData().then(() => xTree.setActiveRow(newRow))\n      })\n    },\n    removeEvent () {\n      let xTree = this.$refs.xTree\n      let removeRecords = xTree.getSelectRecords()\n      removeRecords.forEach(row => {\n        let matchObj = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)\n        if (matchObj) {\n          let { items, index } = matchObj\n          // \n          let restRow = items.splice(index, 1)[0]\n          this.removeList.push(restRow)\n        }\n      })\n      xTree.refreshData()\n    },\n    getInsertEvent () {\n      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)\n      this.$alert({ message: insertRecords.length })\n    },\n    getRemoveEvent () {\n      let removeRecords = this.removeList\n      this.$alert({ message: removeRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTree.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :data=\"tableData\" export setting>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent\">{{ $t('') }}</vue-button>\n        <vue-button @click=\"removeEvent\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      resizable\n      ref=\"xTree\"\n      :tree-config=\"treeConfig\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"checkbox\" width=\"120\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"size\" title=\"Size\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"type\" title=\"Type\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      removeList: [],
      treeConfig: {
        children: 'children'
      },

    }
  },
  created () {
    this.tableData = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)
  },

  methods: {
    insertEvent () {
      let xTree = this.$refs.xTree
      xTree.createRow({
        name: '',
        date: VueUtil.formatDate(new Date(), 'yyyy-MM-dd'),
        isNew: true
      }).then(newRow => {
        // 
        this.tableData.unshift(newRow)
        xTree.refreshData().then(() => xTree.setActiveRow(newRow))
      })
    },
    removeEvent () {
      let xTree = this.$refs.xTree
      let removeRecords = xTree.getSelectRecords()
      removeRecords.forEach(row => {
        let matchObj = VueUtil.findTree(this.tableData, item => item === row, this.treeConfig)
        if (matchObj) {
          let { items, index } = matchObj
          // 
          let restRow = items.splice(index, 1)[0]
          this.removeList.push(restRow)
        }
      })
      xTree.refreshData()
    },
    getInsertEvent () {
      let insertRecords = VueUtil.filterTree(this.tableData, item => item.isNew, this.treeConfig)
      this.$alert({ message: insertRecords.length })
    },
    getRemoveEvent () {
      let removeRecords = this.removeList
      this.$alert({ message: removeRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTree.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    }
  }
}}],"styles":""}