             +' <div style="padding:6px 0;">{{$t("scrollviewDemo.methodInfo.detail4")}}</div>'
             +' <div style="margin-top:15px;font-size:20px;font-weight:500;line-height:1.1;color:inherit;">triggerRefresh()</div>'
             +' <div style="padding:6px 0;">{{$t("scrollviewDemo.methodInfo.detail5")}}</div>'
             +' <vue-collapse v-model="activeNames" :expand-on-click-header="false">'
             +' 	<vue-collapse-item class="collapse-item-blue" name="trigger_refresh_item">'
             +' 		<template slot="title">'
             +' 			<vue-button type="text" icon="vue-icon-document">{{$t("main.code")}}</vue-button>'
             +' 		</template>'
             +' 		<pre class="toolbar:false;brush:jsp">{{trigger_refresh_code}}</pre>'
             +' 	</vue-collapse-item>'
             +' </vue-collapse>'

             +' <div style="margin-top:15px;font-size:20px;font-weight:500;line-height:1.1;color:inherit;">finishRefresh()</div>'
             +' <div style="padding:6px 0;">{{$t("scrollviewDemo.methodInfo.detail6")}}</div>'
             +' <vue-collapse v-model="activeNames2" :expand-on-click-header="false">'
             +' 	<vue-collapse-item class="collapse-item-blue" name="finish_refresh_item">'
             +' 		<template slot="title">'
             +' 			<vue-button type="text" icon="vue-icon-document">{{$t("main.code")}}</vue-button>'
             +' 		</template>'
             +' 		<pre class="toolbar:false;brush:jsp">{{finish_refresh_code}}</pre>'
             +' 	</vue-collapse-item>'
             +' </vue-collapse>'

             +' <div style="margin-top:15px;font-size:20px;font-weight:500;line-height:1.1;color:inherit;">triggerLoadMore()</div>'
             +' <div style="padding:6px 0;">{{$t("scrollviewDemo.methodInfo.detail7")}}</div>'
             +' <vue-collapse v-model="activeNames3" :expand-on-click-header="false">'
