             +' 		<template slot="title">'
             +' 			<vue-button type="text" icon="vue-icon-document">{{$t("main.code")}}</vue-button>'
             +' 		</template>'
             +' 		<pre class="toolbar:false;brush:jsp">{{trigger_loadmore_code}}</pre>'
             +' 	</vue-collapse-item>'
             +' </vue-collapse>'


             +' <div style="margin-top:15px;font-size:20px;font-weight:500;line-height:1.1;color:inherit;">finishLoadMore()</div>'
             +' <div style="padding:6px 0;">{{$t("scrollviewDemo.methodInfo.detail8")}}</div>'
             +' <vue-collapse v-model="activeNames4" :expand-on-click-header="false">'
             +' 	<vue-collapse-item class="collapse-item-blue" name="finish_loadmore_item">'
             +' 		<template slot="title">'
             +' 			<vue-button type="text" icon="vue-icon-document">{{$t("main.code")}}</vue-button>'
