export default {"path":"/XtableDemo/scroll/PartialLoad","name":"XtableDemo.scroll.PartialLoad","head":{"label":"XtableDemo.scroll.PartialLoad.label","description":"XtableDemo.scroll.PartialLoad.description"},"samples":[{"id":"XtableDemo.scroll.PartialLoad.sample1","label":"XtableDemo.scroll.PartialLoad.sample1.label","description":"XtableDemo.scroll.PartialLoad.sample1.description","code":"<vue-toolbar :loading=\"loading\">\n  <template v-slot:buttons>\n    <vue-button @click=\"loadList(20)\">+20</vue-button>\n    <vue-button @click=\"loadList(100)\">+100</vue-button>\n    <vue-button @click=\"loadList(500)\">+500</vue-button>\n    <vue-button @click=\"loadList(1000)\">+1000</vue-button>\n    <vue-button @click=\"loadList(2000)\">+2000</vue-button>\n    <vue-button @click=\"$refs.xTable.scrollTo(null, 4000)\">y=4000</vue-button>\n    <vue-button @click=\"$refs.xTable.clearScroll()\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  ref=\"xTable\"\n  border\n  resizable\n  show-overflow\n  height=\"500\"\n  row-id=\"id\"\n  :loading=\"loading\">\n  <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n  <vue-xtable-column field=\"role\" title=\"Role\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n\n    }\n  },\n  created () {\n    //  vue \n    this.allData = []\n    this.loadList(600)\n  },\n\n  methods: {\n    loadList (size) {\n      this.loading = true\n      this.findList(size).then(data => {\n        this.allData = this.allData.concat(data)// \n        this.$refs.xTable.loadData(this.allData)\n        this.loading = false\n      })\n    },\n    findList (size) {\n      return new Promise(resolve => {\n        setTimeout(() => {\n          var list = []\n          for (var index = 0; index < size; index++) {\n            list.push({\n              id: VueUtil.uniqueId(),\n              name: 'test' + index,\n              role: 'developer',\n              age: 10,\n              date: '2019-05-01',\n              address: 'address abc' + index\n            })\n          }\n          resolve(list)\n        }, 250)\n      })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar :loading=\"loading\">\n      <template v-slot:buttons>\n        <vue-button @click=\"loadList(20)\">+20</vue-button>\n        <vue-button @click=\"loadList(100)\">+100</vue-button>\n        <vue-button @click=\"loadList(500)\">+500</vue-button>\n        <vue-button @click=\"loadList(1000)\">+1000</vue-button>\n        <vue-button @click=\"loadList(2000)\">+2000</vue-button>\n        <vue-button @click=\"$refs.xTable.scrollTo(null, 4000)\">y=4000</vue-button>\n        <vue-button @click=\"$refs.xTable.clearScroll()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      ref=\"xTable\"\n      border\n      resizable\n      show-overflow\n      height=\"500\"\n      row-id=\"id\"\n      :loading=\"loading\">\n      <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n      <vue-xtable-column field=\"role\" title=\"Role\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,

    }
  },
  created () {
    //  vue 
    this.allData = []
    this.loadList(600)
  },

  methods: {
    loadList (size) {
      this.loading = true
      this.findList(size).then(data => {
        this.allData = this.allData.concat(data)// 
        this.$refs.xTable.loadData(this.allData)
        this.loading = false
      })
    },
    findList (size) {
      return new Promise(resolve => {
        setTimeout(() => {
          var list = []
          for (var index = 0; index < size; index++) {
            list.push({
              id: VueUtil.uniqueId(),
              name: 'test' + index,
              role: 'developer',
              age: 10,
              date: '2019-05-01',
              address: 'address abc' + index
            })
          }
          resolve(list)
        }, 250)
      })
    }
  }
}}],"styles":""}