ecords.length })\n    },\n    getRemoveEvent () {\n      let removeRecords = this.$refs.xTable.getRemoveRecords()\n      this.$alert({ message: removeRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTable.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent\"></vue-button>\n        <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n        <vue-button @click=\"validEvent\"></vue-button>\n        <vue-button @click=\"fullValidEvent\"></vue-button>\n        <vue-button @click=\"selectValidEvent\"></vue-button>\n        <vue-button @click=\"getSelectEvent\"></vue-button>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      show-overflow\n      height=\"500\"\n      ref=\"xTable\"\n      :data=\"tableData\"\n      :edit-rules=\"validRules\"\n      :edit-config=\"{trigger: 'click', mode: 'cell', showStatus: true}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      validRules: {
        name: [
          { required: true, message: '' },
          { min: 3, max: 50, message: ' 3  50 ' }
        ],
        sex: [
          { required: true, message: '' }
        ]
      },

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 6)
    this.tableData = list
  },

  methods: {
    validEvent () {
      this.$refs.xTable.validate(valid => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          this.$notify.error({ message:''});
        }
      })
    },
    fullValidEvent () {
      this.$refs.xTable.fullValidate((valid, errMap) => {
        if (valid) {
          this.$notify.success({ message:''});
        } else {
          let msgList = []
          Object.values(errMap).forEach(errList => {
            errList.forEach(params => {
              let { rowIndex, column, rules } = params
              rules.forEach(rule => {
                msgList.push(` ${rowIndex}  ${column.title} ${rule.message}`)
              })
            })
          })

          var h = this.$createElement;
          this.$alert({
            type: "error",
            message: h('div',[msgList.map(function (msg) {
              return h("div", [msg]);
            })])
          });
        }
      })
    },
    selectValidEvent () {
      let selectRecords = this.$refs.xTable.getSelectRecords()
      if (selectRecords.length > 0) {
        this.$refs.xTable.validate(selectRecords, valid => {
          if (valid) {
            this.$notify.success({ message:''});
          } else {
            this.$notify.error({ message:''});
          }
        })
      } else {
        this.$notify.warning({ message:''});
      }
    },
    insertEvent () {
      this.$refs.xTable.insert().then(({ row }) => {
        // 
        this.$refs.xTable.validate(row, valid => {
          if (valid) {

          }
        })
      })
    },
    getSelectEvent () {
      let selectRecords = this.$refs.xTable.getSelectRecords()
      this.$alert({ message: selectRecords.length })
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    },
    getRemoveEvent () {
      let removeRecords = this.$refs.xTable.getRemoveRecords()
      this.$alert({ message: removeRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTable.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    }
  }
}}],"styles":""}