export default {"path":"/XtableDemo/advanced/Template","name":"XtableDemo.advanced.Template","head":{"label":"XtableDemo.advanced.Template.label","description":"XtableDemo.advanced.Template.description"},"samples":[{"id":"XtableDemo.advanced.Template.sample1","label":"XtableDemo.advanced.Template.sample1.label","description":"XtableDemo.advanced.Template.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button>{{ $t('') }}</vue-button>\n    <vue-button>\n      <template></template>\n      <template v-slot:dropdowns>\n        <vue-button></vue-button>\n        <vue-button></vue-button>\n      </template>\n    </vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  resizable\n  :data=\"tableData\">\n  <vue-xtable-column type=\"index\" width=\"100\" show-overflow>\n    <template v-slot=\"scope\">\n      <vue-button @click=\"showDetailEvent(scope.row)\">{{ scope.seq }}</vue-button>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"\" sortable>\n    <template v-slot=\"scope\">\n      <a href=\"/\" target=\"_black\">{{ scope.row.name }}</a>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"\" :filters=\"[{data: ''}]\" :filter-method=\"filterSexMethod\">\n    <template v-slot:header=\"scope\">\n      <vue-xtable-tooltip v-model=\"showSexTip\" content=\"\">\n        <span style=\"color: red;\" @click=\"showSexTip = !showSexTip\"></span>\n      </vue-xtable-tooltip>\n    </template>\n    <template v-slot:filter=\"scope\">\n      <template v-for=\"(option, index) in scope.column.filters\">\n        <input type=\"type\" v-model=\"option.data\" :key=\"index\" @input=\"changeFilterEvent($event, option, scope.context)\">\n      </template>\n    </template>\n    <template v-slot=\"scope\">\n      <span>{{ scope.row.sex }} </span>\n      <vue-button type=\"text\"></vue-button>\n      <vue-button type=\"text\"></vue-button>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"time\" title=\"Time\">\n    <template>\n      <vue-input placeholder=\"\" size=\"mini\"></vue-input>\n    </template>\n    <template v-slot=\"scope\">\n      <span>{{ formatDate(scope.row.time) }}</span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" show-overflow>\n    <template v-slot=\"scope\">\n      <select v-if=\"scope.rowIndex === 1\">\n        <option value=\"1\"></option>\n      </select>\n      <a href=\"/\">{{ scope.row.name }}</a>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"html1\" title=\"Html\" width=\"160\" show-overflow>\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.html1\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"img1\" title=\"\" width=\"120\">\n    <template v-slot=\"scope\">\n      <img v-if=\"scope.row.img1\" :src=\"scope.row.img1\" style=\"width: 100px;\">\n      <span v-else></span>\n    </template>\n  </vue-xtable-column>\n</vue-xtable>\n\n<vue-dialog v-model=\"showDetails\" title=\"\" width=\"800\" height=\"400\" resize>\n  <template>{{ selectRow ? selectRow.text : '' }}</template>\n</vue-dialog>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      showSexTip: false,\n      showDetails: false,\n      selectRow: null,\n      tableData: [],\n\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 6)\n    this.tableData = list\n  },\n\n  methods: {\n    formatDate (value) {\n      return VueUtil.formatDate(value, 'yyyy-MM-dd HH:mm:ss.S')\n    },\n    filterSexMethod ({ option, row }) {\n      return row.sex === option.data\n    },\n    changeFilterEvent (evnt, option, context) {\n      context.changeMultipleOption(evnt, !!option.data, option)\n    },\n    showDetailEvent (row) {\n      this.selectRow = row\n      this.showDetails = true\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button>{{ $t('') }}</vue-button>\n        <vue-button>\n          <template></template>\n          <template v-slot:dropdowns>\n            <vue-button></vue-button>\n            <vue-button></vue-button>\n          </template>\n        </vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"100\" show-overflow>\n        <template v-slot=\"scope\">\n          <vue-button @click=\"showDetailEvent(scope.row)\">{{ scope.seq }}</vue-button>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"\" sortable>\n        <template v-slot=\"scope\">\n          <a href=\"/\" target=\"_black\">{{ scope.row.name }}</a>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"\" :filters=\"[{data: ''}]\" :filter-method=\"filterSexMethod\">\n        <template v-slot:header=\"scope\">\n          <vue-xtable-tooltip v-model=\"showSexTip\" content=\"\">\n            <span style=\"color: red;\" @click=\"showSexTip = !showSexTip\"></span>\n          </vue-xtable-tooltip>\n        </template>\n        <template v-slot:filter=\"scope\">\n          <template v-for=\"(option, index) in scope.column.filters\">\n            <input type=\"type\" v-model=\"option.data\" :key=\"index\" @input=\"changeFilterEvent($event, option, scope.context)\">\n          </template>\n        </template>\n        <template v-slot=\"scope\">\n          <span>{{ scope.row.sex }} </span>\n          <vue-button type=\"text\"></vue-button>\n          <vue-button type=\"text\"></vue-button>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"time\" title=\"Time\">\n        <template>\n          <vue-input placeholder=\"\" size=\"mini\"></vue-input>\n        </template>\n        <template v-slot=\"scope\">\n          <span>{{ formatDate(scope.row.time) }}</span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" show-overflow>\n        <template v-slot=\"scope\">\n          <select v-if=\"scope.rowIndex === 1\">\n            <option value=\"1\"></option>\n          </select>\n          <a href=\"/\">{{ scope.row.name }}</a>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"html1\" title=\"Html\" width=\"160\" show-overflow>\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.html1\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"img1\" title=\"\" width=\"120\">\n        <template v-slot=\"scope\">\n          <img v-if=\"scope.row.img1\" :src=\"scope.row.img1\" style=\"width: 100px;\">\n          <span v-else></span>\n        </template>\n      </vue-xtable-column>\n    </vue-xtable>\n\n    <vue-dialog v-model=\"showDetails\" title=\"\" width=\"800\" height=\"400\" resize>\n      <template>{{ selectRow ? selectRow.text : '' }}</template>\n    </vue-dialog></div>","parameter": {
  data () {
    return {
      showSexTip: false,
      showDetails: false,
      selectRow: null,
      tableData: [],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 6)
    this.tableData = list
  },

  methods: {
    formatDate (value) {
      return VueUtil.formatDate(value, 'yyyy-MM-dd HH:mm:ss.S')
    },
    filterSexMethod ({ option, row }) {
      return row.sex === option.data
    },
    changeFilterEvent (evnt, option, context) {
      context.changeMultipleOption(evnt, !!option.data, option)
    },
    showDetailEvent (row) {
      this.selectRow = row
      this.showDetails = true
    }
  }
}}],"styles":""}