   loading: false,\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      return new Promise(resolve => {\n        setTimeout(() => {\n          let tableData = window.MOCK_DATA_LIST.slice(0, 2000)\n          //  vue  vue \n          if (this.$refs.xTable) {\n            this.$refs.xTable.loadData(tableData)\n          }\n          resolve()\n          this.loading = false\n        }, 300)\n      })\n    },\n    insertEvent (row) {\n      let xTable = this.$refs.xTable\n      let record = {}\n      xTable.insertAt(record, row).then(({ row }) => {\n        xTable.setActiveRow(row)\n      })\n    },\n    getInsertEvent () {\n      let insertRecords = this.$refs.xTable.getInsertRecords()\n      this.$alert({ message: insertRecords.length })\n    },\n    getRemoveEvent () {\n      let removeRecords = this.$refs.xTable.getRemoveRecords()\n      this.$alert({ message: removeRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTable.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    },\n    handleCommand1 (command) {\n      switch (command) {\n        case 'insertFirst':\n          this.insertEvent(null);\n          break;\n        case 'insertLast':\n          this.insertEvent(-1);\n          break;\n        case 'insertAt100':\n          this.insertEvent(this.$refs.xTable.getData(100));\n          break;\n      }\n    },\n    handleCommand2 (command) {\n      switch (command) {\n        case 'removeSelecteds':\n          this.$refs.xTable.removeSelecteds();\n          break;\n        case 'removeFirst':\n          this.$refs.xTable.remove(this.$refs.xTable.getData(0));\n          break;\n        case 'removeLast':\n          this.$refs.xTable.remove(this.$refs.xTable.getData(this.$refs.xTable.getData().length - 1));\n          break;\n        case 'removeAt100':\n          this.$refs.xTable.remove(this.$refs.xTable.getData(100));\n          break;\n      }\n    },\n\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar export :refresh=\"{query: findList}\">\n      <template v-slot:buttons>\n        <vue-dropdown  @command=\"handleCommand1\">\n          <vue-button><i class=\"vue-icon-arrow-down vue-icon--right\"></i></vue-button>\n          <vue-dropdown-menu slot=\"dropdown\">\n            <vue-dropdown-item command=\"insertFirst\" @click=\"insertEvent(null)\"></vue-dropdown-item>\n            <vue-dropdown-item command=\"insertLast\" @click=\"insertEvent(-1)\"></vue-dropdown-item>\n            <vue-dropdown-item command=\"insertAt100\" @click=\"insertEvent($refs.xTable.getData(100))\"> 100 </vue-dropdown-item>\n          </vue-dropdown-menu>\n        </vue-dropdown>\n\n        <vue-dropdown  @command=\"handleCommand2\">\n          <vue-button><i class=\"vue-icon-arrow-down vue-icon--right\"></i></vue-button>\n          <vue-dropdown-menu slot=\"dropdown\">\n            <vue-dropdown-item command=\"removeSelecteds\"></vue-dropdown-item>\n            <vue-dropdown-item command=\"removeFirst\"></vue-dropdown-item>\n            <vue-dropdown-item command=\"removeLast\"></vue-dropdown-item>\n            <vue-dropdown-item command=\"removeAt100\"> 100 </vue-dropdown-item>\n          </vue-dropdown-menu>\n        </vue-dropdown>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      show-overflow\n      ref=\"xTable\"\n      height=\"300\"\n      :loading=\"loading\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      :checkbox-config=\"{checkField: 'checked'}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" width=\"200\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"rate\" title=\"Rate\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"region\" title=\"Region\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"time\" title=\"Time\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" width=\"300\" show-overflow></vue-xtable-column>\n      <vue-xtable-column field=\"updateTime\" title=\"UpdateTime\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"createTime\" title=\"CreateTime\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr1\" title=\"Attr1\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr2\" title=\"Attr2\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr3\" title=\"Attr3\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr4\" title=\"Attr4\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr5\" title=\"Attr5\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr6\" title=\"Attr6\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr7\" title=\"Attr7\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr8\" title=\"Attr8\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"attr9\" title=\"Attr9\" width=\"200\"></vue-xtable-column>\n      <vue-xtable-column field=\"createTime\" title=\"CreateTime\" width=\"200\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      this.loading = true
      return new Promise(resolve => {
        setTimeout(() => {
          let tableData = window.MOCK_DATA_LIST.slice(0, 2000)
          //  vue  vue 
          if (this.$refs.xTable) {
            this.$refs.xTable.loadData(tableData)
          }
          resolve()
          this.loading = false
        }, 300)
      })
    },
    insertEvent (row) {
      let xTable = this.$refs.xTable
      let record = {}
      xTable.insertAt(record, row).then(({ row }) => {
        xTable.setActiveRow(row)
      })
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    },
    getRemoveEvent () {
      let removeRecords = this.$refs.xTable.getRemoveRecords()
      this.$alert({ message: removeRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTable.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    },
    handleCommand1 (command) {
      switch (command) {
        case 'insertFirst':
          this.insertEvent(null);
          break;
        case 'insertLast':
          this.insertEvent(-1);
          break;
        case 'insertAt100':
          this.insertEvent(this.$refs.xTable.getData(100));
          break;
      }
    },
    handleCommand2 (command) {
      switch (command) {
        case 'removeSelecteds':
          this.$refs.xTable.removeSelecteds();
          break;
        case 'removeFirst':
          this.$refs.xTable.remove(this.$refs.xTable.getData(0));
          break;
        case 'removeLast':
          this.$refs.xTable.remove(this.$refs.xTable.getData(this.$refs.xTable.getData().length - 1));
          break;
        case 'removeAt100':
          this.$refs.xTable.remove(this.$refs.xTable.getData(100));
          break;
      }
    },

  }
}}],"styles":""}