      '        },\n' +
      '        rules: {\n' +
      '          pass: [{\n' +
      '            validator: this.validatePass,\n' +
      '            trigger: "blur"\n' +
      '          }],\n' +
      '          checkPass: [{\n' +
      '            validator: this.validatePass2,\n' +
      '            trigger: "blur"\n' +
      '          }],\n' +
      '          age: [{\n' +
      '            validator: this.checkAge,\n' +
      '            trigger: "blur"\n' +
      '          }]\n' +
      '        }\n' +
      '      }\n' +
      '    },\n' +
      '    methods: {\n' +
      '      checkAge: function(rule, value, callback) {\n' +
      '        if (!value) {\n' +
      '          return callback(new Error("Please input the age"));\n' +
      '        }\n' +
      '        setTimeout(function() {\n' +
      '          if (!Number.isInteger(value)) {\n' +
      '            callback(new Error("Please input digits"));\n' +
      '          } else {\n' +
      '            if (value < 18) {\n' +
      '              callback(new Error("Age must be greater than 18"));\n' +
      '            } else {\n' +
      '              callback();\n' +
      '            }\n' +
      '          }\n' +
      '        }, 1000);\n' +
      '      },\n' +
      '      validatePass: function(rule, value, callback) {\n' +
      '        if (value === "") {\n' +
      '          callback(new Error("Please input the password"));\n' +
      '        } else {\n' +
      '          if (this.ruleForm.checkPass !== "") {\n' +
      '            this.$refs.ruleForm.validateField("checkPass");\n' +
      '          }\n' +
      '          callback();\n' +
      '        }\n' +
      '      },\n' +
      '      validatePass2: function(rule, value, callback) {\n' +
      '        if (value === "") {\n' +
      '          callback(new Error("Please input the password again"));\n' +
      '        } else if (value !== this.ruleForm.pass) {\n' +
      '          callback(new Error("Two password don\'t match!"));\n' +
      '        } else {\n' +
      '          callback();\n' +
      '        }\n' +
      '      },\n' +
      '      showError: function(msgs) {\n' +
      '        var self = this;\n' +
      '        if (VueUtil.isArray(msgs)) {\n' +
      '          msgs.forEach(function(msg) {\n' +
      '            msg && self.$notify.error({message: msg});\n' +
      '          });\n' +
      '        } else {\n' +
      '          msgs && self.$notify.error({message: msgs});\n' +
      '        }\n' +
      '      },\n' +
      '      submitForm: function(formName) {\n' +
      '        var self = this;\n' +
      '        self.$refs[formName].validate(function(valid) {\n' +
      '          if (valid) {\n' +
      '            self.$notify({title: "submit!"});\n' +
      '          }\n' +
      '        });\n' +
      '      },\n' +
      '      resetForm: function(formName) {\n' +
      '        this.$refs[formName].resetFields();\n' +
      '      }\n' +
      '    }\n' +
      '  }).$mount();\n' +
      '</script>'
