export default {"path":"/XtableDemo/grid/Template","name":"XtableDemo.grid.Template","head":{"label":"XtableDemo.grid.Template.label","description":"XtableDemo.grid.Template.description"},"samples":[{"id":"XtableDemo.grid.Template.sample1","label":"XtableDemo.grid.Template.sample1.label","description":"XtableDemo.grid.Template.sample1.description","code":"<vue-xgrid\n  border\n  resizable\n  height=\"400\"\n  :toolbar=\"tableToolbar\"\n  :columns=\"tableColumn\"\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n</vue-xgrid>\n\n<vue-dialog v-model=\"showDetails\" title=\"\" show-close>\n  <template>\n    <div v-if=\"selectRow\" v-html=\"selectRow.html3\"></div>\n  </template>\n</vue-dialog>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      showDetails: false,\n      selectRow: null,\n      tableData: [],\n      tableColumn: [\n        { type: 'index', width: 50 },\n        {\n          field: 'name',\n          title: 'Name',\n          slots: {\n            default: ({ row, column }) => {\n              return [\n                <span style=\"color: red;\">{ row.name }</span>,\n                <button onClick={ () => this.showDetailEvent(row, column) }></button>\n              ]\n            }\n          }\n        },\n        {\n          field: 'sex',\n          title: 'Sex',\n          showHeaderOverflow: true,\n          filters: [{ data: '' }],\n          filterMethod: this.filterSexMethod,\n          editRender: { type: 'default' },\n          slots: {\n            default: ({ row, column }) => {\n              return [\n                <a class=\"link\" href=\"/\">{ row.sex }</a>\n              ]\n            },\n            header: ({ column }) => {\n              return [\n                <span>\n                  <i>@</i>\n                  <span style=\"color: red;\" onClick={ this.headerClickEvent }>{ column.title }</span>\n                </span>\n              ]\n            },\n            filter: ({ column, context }) => {\n              return column.filters.map(option => {\n                return <input type=\"type\" value={ option.data } onInput={ evnt => this.changeFilterEvent(evnt, option, context) }/>\n              })\n            },\n            edit: ({ row }) => {\n              return [\n                <input type=\"text\" value={ row.sex } onInput={ evnt => { row.sex = evnt.target.value } }/>\n              ]\n            }\n          }\n        },\n        {\n          field: 'address',\n          title: 'Address',\n          showOverflow: true,\n          slots: {\n            default: ({ row }, h) => {\n              return [\n                h('span', {\n                  style: {\n                    color: 'blue'\n                  },\n                  on: {\n                    click: () => this.addressClickEvent(row)\n                  }\n                }, row.address)\n              ]\n            }\n          }\n        },\n        {\n          field: 'html1',\n          title: 'Html',\n          showOverflow: true,\n          slots: {\n            default: ({ row }, h) => {\n              return [\n                <span domPropsInnerHTML={ row.html1 }></span>\n              ]\n            }\n          }\n        },\n        {\n          field: 'img1',\n          title: '',\n          slots: {\n            default: ({ row }, h) => {\n              return [\n                row.img1 ? <img src={ row.img1 } style=\"width: 100px;\"/> : <span></span>\n              ]\n            }\n          }\n        }\n      ],\n      tableToolbar: {\n        setting: true,\n        slots: {\n          buttons: () => {\n            return [\n              <vue-checkbox>Option</vue-checkbox>,\n              <vue-button>1</vue-button>,\n              <vue-button>2</vue-button>\n            ]\n          }\n        }\n      },\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 100)\n    this.tableData = list\n  },\n  mounted () {\n    Array.from(this.$el.querySelectorAll('pre code')).forEach((block) => {\n      hljs.highlightBlock(block)\n    })\n  },\n  methods: {\n    showDetailEvent (row) {\n      this.selectRow = row\n      this.showDetails = true\n    },\n    headerClickEvent (evnt) {\n      this.$alert({message: ''})\n    },\n    addressClickEvent (row) {\n      this.$alert({message: `address${row.row}`})\n    },\n    filterSexMethod ({ option, row }) {\n      return row.sex === option.data\n    },\n    changeFilterEvent (evnt, option, context) {\n      option.data = evnt.target.value\n      context.changeMultipleOption(evnt, !!option.data, option)\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      height=\"400\"\n      :toolbar=\"tableToolbar\"\n      :columns=\"tableColumn\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n    </vue-xgrid>\n\n    <vue-dialog v-model=\"showDetails\" title=\"\" show-close>\n      <template>\n        <div v-if=\"selectRow\" v-html=\"selectRow.html3\"></div>\n      </template>\n    </vue-dialog></div>","parameter": {
  data () {
    return {
      showDetails: false,
      selectRow: null,
      tableData: [],
      tableColumn: [
        { type: 'index', width: 50 },
        {
          field: 'name',
          title: 'Name',
          slots: {
            default: ({ row, column }) => {
              return [
                <span style="color: red;">{ row.name }</span>,
                <button onClick={ () => this.showDetailEvent(row, column) }></button>
              ]
            }
          }
        },
        {
          field: 'sex',
          title: 'Sex',
          showHeaderOverflow: true,
          filters: [{ data: '' }],
          filterMethod: this.filterSexMethod,
          editRender: { type: 'default' },
          slots: {
            default: ({ row, column }) => {
              return [
                <a class="link" href="/">{ row.sex }</a>
              ]
            },
            header: ({ column }) => {
              return [
                <span>
                  <i>@</i>
                  <span style="color: red;" onClick={ this.headerClickEvent }>{ column.title }</span>
                </span>
              ]
            },
            filter: ({ column, context }) => {
              return column.filters.map(option => {
                return <input type="type" value={ option.data } onInput={ evnt => this.changeFilterEvent(evnt, option, context) }/>
              })
            },
            edit: ({ row }) => {
              return [
                <input type="text" value={ row.sex } onInput={ evnt => { row.sex = evnt.target.value } }/>
              ]
            }
          }
        },
        {
          field: 'address',
          title: 'Address',
          showOverflow: true,
          slots: {
            default: ({ row }, h) => {
              return [
                h('span', {
                  style: {
                    color: 'blue'
                  },
                  on: {
                    click: () => this.addressClickEvent(row)
                  }
                }, row.address)
              ]
            }
          }
        },
        {
          field: 'html1',
          title: 'Html',
          showOverflow: true,
          slots: {
            default: ({ row }, h) => {
              return [
                <span domPropsInnerHTML={ row.html1 }></span>
              ]
            }
          }
        },
        {
          field: 'img1',
          title: '',
          slots: {
            default: ({ row }, h) => {
              return [
                row.img1 ? <img src={ row.img1 } style="width: 100px;"/> : <span></span>
              ]
            }
          }
        }
      ],
      tableToolbar: {
        setting: true,
        slots: {
          buttons: () => {
            return [
              <vue-checkbox>Option</vue-checkbox>,
              <vue-button>1</vue-button>,
              <vue-button>2</vue-button>
            ]
          }
        }
      },
    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 100)
    this.tableData = list
  },
  mounted () {
    Array.from(this.$el.querySelectorAll('pre code')).forEach((block) => {
      hljs.highlightBlock(block)
    })
  },
  methods: {
    showDetailEvent (row) {
      this.selectRow = row
      this.showDetails = true
    },
    headerClickEvent (evnt) {
      this.$alert({message: ''})
    },
    addressClickEvent (row) {
      this.$alert({message: `address${row.row}`})
    },
    filterSexMethod ({ option, row }) {
      return row.sex === option.data
    },
    changeFilterEvent (evnt, option, context) {
      option.data = evnt.target.value
      context.changeMultipleOption(evnt, !!option.data, option)
    }
  }
}}],"styles":""}