export default {"path":"/XtableDemo/advanced/Dynamic","name":"XtableDemo.advanced.Dynamic","head":{"label":"XtableDemo.advanced.Dynamic.label","description":"XtableDemo.advanced.Dynamic.description"},"samples":[{"id":"XtableDemo.advanced.Dynamic.sample1","label":"XtableDemo.advanced.Dynamic.sample1.label","description":"XtableDemo.advanced.Dynamic.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"addColumn()\"></vue-button>\n    <vue-button @click=\"removeColumn()\"></vue-button>\n    <vue-button @click=\"updateFilter(4)\"></vue-button>\n    <vue-button @click=\"toggleFixedColumn(0, 'left')\"></vue-button>\n    <vue-button @click=\"toggleFixedColumn(1, 'left')\"></vue-button>\n    <vue-button @click=\"updateWidthColumn(2, 500)\"></vue-button>\n    <vue-button @click=\"updateWidthColumn(3, 500)\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  highlight-hover-row\n  highlight-current-row\n  ref=\"xTable\"\n  height=\"300\"\n  :data=\"tableData\">\n  <vue-xtable-column v-for=\"(config, index) in tableColumn\" :key=\"index\" v-bind=\"config\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      tableColumn: [\n        { type: 'index', width: 60, fixed: null },\n        { type: 'checkbox', width: 50, fixed: null },\n        { field: 'name', title: 'Name', width: 200 },\n        { field: 'nickname', title: 'Nickname', width: 300 },\n        { field: 'sex', title: 'Sex', width: 200, filters: [{ value: '1', label: '' }] },\n        { field: 'role', title: 'Role', width: 200 },\n        { field: 'address', title: 'Address', width: 300, showOverflow: true }\n      ],\n\n    }\n  },\n\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n  },\n  methods: {\n    addColumn () {\n      const uniqueId = VueUtil.uniqueId()\n      this.tableColumn.push({\n        field: `new_${uniqueId}`,\n        title: `_${uniqueId}`,\n        minWidth: 100\n      })\n    },\n    removeColumn () {\n      this.tableColumn.pop()\n    },\n    updateFilter (index) {\n      let xTable = this.$refs.xTable\n      xTable.filter(this.tableColumn[index].field, options => {\n        //  checked \n        return [\n          { value: '1', label: '' },\n          { value: '0', label: '', checked: true }\n        ]\n      }).then(() => {\n        //  updateData \n        xTable.updateData()\n      })\n    },\n    toggleFixedColumn (index, value) {\n      const xTable = this.$refs.xTable\n      this.tableColumn[index].fixed = this.tableColumn[index].fixed ? null : value\n      // \n      this.$nextTick(() => {\n        xTable.refreshColumn()\n          // \n          .then(() => xTable.refreshScroll())\n      })\n    },\n    updateWidthColumn (index, value) {\n      this.tableColumn[index].width = value\n      // \n      this.$nextTick(() => {\n        this.$refs.xTable.refreshColumn()\n      })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"addColumn()\"></vue-button>\n        <vue-button @click=\"removeColumn()\"></vue-button>\n        <vue-button @click=\"updateFilter(4)\"></vue-button>\n        <vue-button @click=\"toggleFixedColumn(0, 'left')\"></vue-button>\n        <vue-button @click=\"toggleFixedColumn(1, 'left')\"></vue-button>\n        <vue-button @click=\"updateWidthColumn(2, 500)\"></vue-button>\n        <vue-button @click=\"updateWidthColumn(3, 500)\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      highlight-hover-row\n      highlight-current-row\n      ref=\"xTable\"\n      height=\"300\"\n      :data=\"tableData\">\n      <vue-xtable-column v-for=\"(config, index) in tableColumn\" :key=\"index\" v-bind=\"config\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      tableColumn: [
        { type: 'index', width: 60, fixed: null },
        { type: 'checkbox', width: 50, fixed: null },
        { field: 'name', title: 'Name', width: 200 },
        { field: 'nickname', title: 'Nickname', width: 300 },
        { field: 'sex', title: 'Sex', width: 200, filters: [{ value: '1', label: '' }] },
        { field: 'role', title: 'Role', width: 200 },
        { field: 'address', title: 'Address', width: 300, showOverflow: true }
      ],

    }
  },

  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)
  },
  methods: {
    addColumn () {
      const uniqueId = VueUtil.uniqueId()
      this.tableColumn.push({
        field: `new_${uniqueId}`,
        title: `_${uniqueId}`,
        minWidth: 100
      })
    },
    removeColumn () {
      this.tableColumn.pop()
    },
    updateFilter (index) {
      let xTable = this.$refs.xTable
      xTable.filter(this.tableColumn[index].field, options => {
        //  checked 
        return [
          { value: '1', label: '' },
          { value: '0', label: '', checked: true }
        ]
      }).then(() => {
        //  updateData 
        xTable.updateData()
      })
    },
    toggleFixedColumn (index, value) {
      const xTable = this.$refs.xTable
      this.tableColumn[index].fixed = this.tableColumn[index].fixed ? null : value
      // 
      this.$nextTick(() => {
        xTable.refreshColumn()
          // 
          .then(() => xTable.refreshScroll())
      })
    },
    updateWidthColumn (index, value) {
      this.tableColumn[index].width = value
      // 
      this.$nextTick(() => {
        this.$refs.xTable.refreshColumn()
      })
    }
  }
}}],"styles":""}