export default {"path":"/XtableDemo/grid/Footer","name":"XtableDemo.grid.Footer","head":{"label":"XtableDemo.grid.Footer.label","description":"XtableDemo.grid.Footer.description"},"samples":[{"id":"XtableDemo.grid.Footer.sample1","label":"XtableDemo.grid.Footer.sample1.label","description":"XtableDemo.grid.Footer.sample1.description","code":"<vue-xgrid\nborder\nstripe\nresizable\nshow-footer\nheight=\"500\"\n:footer-method=\"footerMethod\"\n:columns=\"tableColumn\"\n:data=\"tableData\"></vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableColumn: [\n        { type: 'index', width: 50 },\n        { field: 'name', title: '' },\n        { field: 'sex', title: '' },\n        { field: 'age', title: 'Age' },\n        { field: 'rate', title: 'Rate' },\n        { field: 'address', title: 'Address', showOverflow: true }\n      ],\n      tableData: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 50)\n  },\n\n  methods: {\n    footerMethod ({ columns, data }) {\n      return [\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age', 'rate'], column.property)) {\n            return VueUtil.sum(data.map( data => data[column.property]))\n          }\n          return null\n        }),\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age', 'rate'], column.property)) {\n            return VueUtil.mean(data.map( data => data[column.property]))\n          }\n          return null\n        })\n      ]\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      stripe\n      resizable\n      show-footer\n      height=\"500\"\n      :footer-method=\"footerMethod\"\n      :columns=\"tableColumn\"\n      :data=\"tableData\"></vue-xgrid></div>","parameter": {
