export default {"path":"/XtableDemo/advanced/Search","name":"XtableDemo.advanced.Search","head":{"label":"XtableDemo.advanced.Search.label","description":"XtableDemo.advanced.Search.description"},"samples":[{"id":"XtableDemo.advanced.Search.sample1","label":"XtableDemo.advanced.Search.sample1.label","description":"XtableDemo.advanced.Search.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-input v-model=\"filterName1\" type=\"search\" placeholder=\"\"></vue-input>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  height=\"300\"\n  :data=\"list1\">\n  <vue-xtable-column type=\"index\" width=\"80\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.name\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"role\" title=\"Role\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.role\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.age\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.address\"></span>\n    </template>\n  </vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      filterName1: '',\n      tableData1: [],\n    }\n  },\n  computed: {\n    list1 () {\n      let filterName = VueUtil.toString(this.filterName1).trim().toLowerCase()\n      if (filterName) {\n        let filterRE = new RegExp(filterName, 'gi')\n        let searchProps = ['name', 'role', 'age', 'address']\n        let rest = this.tableData1.filter(item => searchProps.some(key => VueUtil.toString(item[key]).toLowerCase().indexOf(filterName) > -1))\n        return rest.map(row => {\n          let item = Object.assign({}, row)\n          searchProps.forEach(key => {\n            item[key] = VueUtil.toString(item[key]).replace(filterRE, match => `<span class=\"keyword-lighten\">${match}</span>`)\n          })\n          return item\n        })\n      }\n      return this.tableData1\n    },\n  },\n  created () {\n    this.tableData1 = window.MOCK_DATA_LIST.slice(0, 50)\n  },\n\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-input v-model=\"filterName1\" type=\"search\" placeholder=\"\"></vue-input>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      height=\"300\"\n      :data=\"list1\">\n      <vue-xtable-column type=\"index\" width=\"80\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.name\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"role\" title=\"Role\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.role\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.age\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.address\"></span>\n        </template>\n      </vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      filterName1: '',
      tableData1: [],
    }
  },
  computed: {
    list1 () {
      let filterName = VueUtil.toString(this.filterName1).trim().toLowerCase()
      if (filterName) {
        let filterRE = new RegExp(filterName, 'gi')
        let searchProps = ['name', 'role', 'age', 'address']
        let rest = this.tableData1.filter(item => searchProps.some(key => VueUtil.toString(item[key]).toLowerCase().indexOf(filterName) > -1))
        return rest.map(row => {
          let item = Object.assign({}, row)
          searchProps.forEach(key => {
            item[key] = VueUtil.toString(item[key]).replace(filterRE, match => `<span class="keyword-lighten">${match}</span>`)
          })
          return item
        })
      }
      return this.tableData1
    },
  },
  created () {
    this.tableData1 = window.MOCK_DATA_LIST.slice(0, 50)
  },

}},{"id":"XtableDemo.advanced.Search.sample2","label":"XtableDemo.advanced.Search.sample2.label","description":"XtableDemo.advanced.Search.sample2.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-input v-model=\"filterName2\" type=\"search\" placeholder=\"\"></vue-input>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  :data=\"list2\"\n  :tree-config=\"{children: 'children', expandAll: !!filterName2}\">\n  <vue-xtable-column type=\"index\" width=\"160\" title=\"\" tree-node></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.name\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"size\" title=\"Size\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.size\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"type\" title=\"Type\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.type\"></span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"date\" title=\"Date\">\n    <template v-slot=\"scope\">\n      <span v-html=\"scope.row.date\"></span>\n    </template>\n  </vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      filterName2: '',\n      tableData2: [],\n    }\n  },\n  computed: {\n    list2 () {\n      let filterName = VueUtil.toString(this.filterName2).trim().toLowerCase()\n      if (filterName) {\n        let filterRE = new RegExp(filterName, 'gi')\n        let options = { children: 'children' }\n        let searchProps = ['name', 'size', 'type', 'date']\n        let rest = VueUtil.searchTree(this.tableData2, item => searchProps.some(key => VueUtil.toString(item[key]).toLowerCase().indexOf(filterName) > -1), options)\n        VueUtil.eachTree(rest, item => {\n          searchProps.forEach(key => {\n            item[key] = VueUtil.toString(item[key]).replace(filterRE, match => `<span class=\"keyword-lighten\">${match}</span>`)\n          })\n        }, options)\n        return rest\n      }\n      return this.tableData2\n    }\n  },\n  created () {\n    this.tableData2 = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)\n  },\n\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-input v-model=\"filterName2\" type=\"search\" placeholder=\"\"></vue-input>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      :data=\"list2\"\n      :tree-config=\"{children: 'children', expandAll: !!filterName2}\">\n      <vue-xtable-column type=\"index\" width=\"160\" title=\"\" tree-node></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.name\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"size\" title=\"Size\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.size\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"type\" title=\"Type\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.type\"></span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"date\" title=\"Date\">\n        <template v-slot=\"scope\">\n          <span v-html=\"scope.row.date\"></span>\n        </template>\n      </vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      filterName2: '',
      tableData2: [],
    }
  },
  computed: {
    list2 () {
      let filterName = VueUtil.toString(this.filterName2).trim().toLowerCase()
      if (filterName) {
        let filterRE = new RegExp(filterName, 'gi')
        let options = { children: 'children' }
        let searchProps = ['name', 'size', 'type', 'date']
        let rest = VueUtil.searchTree(this.tableData2, item => searchProps.some(key => VueUtil.toString(item[key]).toLowerCase().indexOf(filterName) > -1), options)
        VueUtil.eachTree(rest, item => {
          searchProps.forEach(key => {
            item[key] = VueUtil.toString(item[key]).replace(filterRE, match => `<span class="keyword-lighten">${match}</span>`)
          })
        }, options)
        return rest
      }
      return this.tableData2
    }
  },
  created () {
    this.tableData2 = VueUtil.clone(window.MOCK_TREE_DATA_LIST, true)
  },

}}],"styles":""}