export default {"path":"/XtableDemo/grid/Menu","name":"XtableDemo.grid.Menu","head":{"label":"XtableDemo.grid.Menu.label","description":"XtableDemo.grid.Menu.description"},"samples":[{"id":"XtableDemo.grid.Menu.sample1","label":"XtableDemo.grid.Menu.sample1.label","description":"XtableDemo.grid.Menu.sample1.description","code":"<vue-xgrid\nborder\nresizable\nshow-footer\nhighlight-current-row\nref=\"xGrid\"\n:footer-method=\"footerMethod\"\n:columns=\"tableColumn\"\n:data=\"tableData\"\n:context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, footer: {options: footerMenus}, visibleMethod:visibleMethod}\"\n@cell-context-menu=\"cellContextMenuEvent\"\n@context-menu-click=\"contextMenuClickEvent\"></vue-xgrid>\n\n<script>\n// import VueUtil.clipboard from 'xe-clipboard'\n\nnew Vue({\n  data () {\n    return {\n      tableColumn: [\n        { type: 'index', width: 50 },\n        { field: 'name', title: '' },\n        { field: 'age', title: '' },\n        { field: 'address', title: 'Address', showOverflow: true }\n      ],\n      headerMenus: [\n        [\n          {\n            code: 'exportAll',\n            name: '.csv',\n            visible: true,\n            disabled: false\n          }\n        ]\n      ],\n      bodyMenus: [\n        [\n          {\n            code: 'copy',\n            name: '',\n            visible: true,\n            disabled: false,\n            prefixIcon: 'fa fa-copy'\n          },\n          {\n            code: 'clear',\n            name: '',\n            visible: true,\n            disabled: false\n          },\n          {\n            code: 'reload',\n            name: '',\n            visible: true,\n            disabled: false\n          },\n          {\n            code: 'myExport',\n            name: '.csv',\n            visible: true,\n            disabled: false,\n            prefixIcon: 'fa fa-download'\n          }\n        ]\n      ],\n      footerMenus: [\n        [\n          {\n            code: 'exportAll',\n            name: '.csv',\n            visible: true,\n            disabled: false\n          }\n        ]\n      ],\n      tableData: [],\n\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 4)\n  },\n\n  methods: {\n    visibleMethod ({ type, options, column }) {\n      //  name  age \n      // \n      let isDisabled = !column || column.property !== 'name'\n      let isVisible = column && column.property === 'age'\n      options.forEach(list => {\n        list.forEach(item => {\n          if (VueUtil.includes(['copy'], item.code)) {\n            item.disabled = isDisabled\n          }\n          if (VueUtil.includes(['clear'], item.code)) {\n            item.visible = isVisible\n          }\n        })\n      })\n      return true\n    },\n    cellContextMenuEvent ({ row }) {\n      this.$refs.xGrid.setCurrentRow(row)\n    },\n    contextMenuClickEvent ({ menu, row, column }) {\n      let xGrid = this.$refs.xGrid\n      switch (menu.code) {\n        case 'copy':\n          if (row && column) {\n            if (VueUtil.clipboard.copy(row[column.property])) {\n              this.$notify.success({ message:''});\n            }\n          }\n          break\n        case 'clear':\n          xGrid.clearData(row, column.property)\n          break\n        case 'myExport':\n          xGrid.exportData()\n          break\n      }\n    },\n    footerMethod ({ columns, data }) {\n      return [\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age'], column.property)) {\n            return VueUtil.sum(data.map( data => data[column.property]))\n          }\n          return null\n        })\n      ]\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      show-footer\n      highlight-current-row\n      ref=\"xGrid\"\n      :footer-method=\"footerMethod\"\n      :columns=\"tableColumn\"\n      :data=\"tableData\"\n      :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, footer: {options: footerMenus}, visibleMethod:visibleMethod}\"\n      @cell-context-menu=\"cellContextMenuEvent\"\n      @context-menu-click=\"contextMenuClickEvent\"></vue-xgrid></div>","parameter":// import VueUtil.clipboard from 'xe-clipboard'

 {
  data () {
    return {
      tableColumn: [
        { type: 'index', width: 50 },
        { field: 'name', title: '' },
        { field: 'age', title: '' },
        { field: 'address', title: 'Address', showOverflow: true }
      ],
      headerMenus: [
        [
          {
            code: 'exportAll',
            name: '.csv',
            visible: true,
            disabled: false
          }
        ]
      ],
      bodyMenus: [
        [
          {
            code: 'copy',
            name: '',
            visible: true,
            disabled: false,
            prefixIcon: 'fa fa-copy'
          },
          {
            code: 'clear',
            name: '',
            visible: true,
            disabled: false
          },
          {
            code: 'reload',
            name: '',
            visible: true,
            disabled: false
          },
          {
            code: 'myExport',
            name: '.csv',
            visible: true,
            disabled: false,
            prefixIcon: 'fa fa-download'
          }
        ]
      ],
      footerMenus: [
        [
          {
            code: 'exportAll',
            name: '.csv',
            visible: true,
            disabled: false
          }
        ]
      ],
      tableData: [],

    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 4)
  },

  methods: {
    visibleMethod ({ type, options, column }) {
      //  name  age 
      // 
      let isDisabled = !column || column.property !== 'name'
      let isVisible = column && column.property === 'age'
      options.forEach(list => {
        list.forEach(item => {
          if (VueUtil.includes(['copy'], item.code)) {
            item.disabled = isDisabled
          }
          if (VueUtil.includes(['clear'], item.code)) {
            item.visible = isVisible
          }
        })
      })
      return true
    },
    cellContextMenuEvent ({ row }) {
      this.$refs.xGrid.setCurrentRow(row)
    },
    contextMenuClickEvent ({ menu, row, column }) {
      let xGrid = this.$refs.xGrid
      switch (menu.code) {
        case 'copy':
          if (row && column) {
            if (VueUtil.clipboard.copy(row[column.property])) {
              this.$notify.success({ message:''});
            }
          }
          break
        case 'clear':
          xGrid.clearData(row, column.property)
          break
        case 'myExport':
          xGrid.exportData()
          break
      }
    },
    footerMethod ({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          if (VueUtil.includes(['age'], column.property)) {
            return VueUtil.sum(data.map( data => data[column.property]))
          }
          return null
        })
      ]
    }
  }
}}],"styles":""}