'use strict';
require('./mock-data')

// ---------demo-------
var context = require.context("./", true, /\.vue$/);

function addcss(css){
  var head = document.getElementsByTagName('head')[0];
  var s = document.createElement('style');
  s.setAttribute('type', 'text/css');
  if (s.styleSheet) {   // IE
      s.styleSheet.cssText = css;
  } else {                // the world
      s.appendChild(document.createTextNode(css));
  }
  head.appendChild(s);
}

var demos = {};
var tempCss = '';

context.keys().forEach(function(key) {
  var demo = context(key).default;

  var category = demo.name.split('.')[1];
  if (!demos[category]) {
    demos[category] = {
      head: {
        label: 'XtableDemo.' + category + '.label',
      },
      children: []
    }
  }
  demos[category].children.push(demo);

  if(demo.styles.trim()) {
    tempCss += demo.styles;
  }
})

// democsshead
addcss(tempCss);

// 
var orders = {
  base: ['Basic', 'Size', 'Overflow', 'Stripe', 'Border', 'Style', 'Index', 'DynamicStyle', 'Header', 'Resizable', 'MaxHeight', 'AutoHeight', 'Height', 'Fixed', 'FixedFull', 'Group', 'HeaderHighlight', 'Current', 'Radio', 'Selection', 'Sort', 'Filter', 'Empty', 'Loading', 'Format'],
  advanced: ['Event', 'Template', 'Dynamic', 'ManualFilter', 'Span', 'SpanRow', 'Footer', 'FooterSpan', 'Import', 'Export', 'Print', 'FixedType', 'Menu', 'MenuPrivilege', 'Expand', 'Toolbar', 'Custom', 'CustomStorage', 'CustomlWidthStorage', 'Search', 'Page', 'Highlight', 'CopyPaste', 'ColumnDrag', 'RowDrag'],
  tree: ['Basic', 'Radio', 'Selection', 'Fixed', 'Expand', 'CellValid', 'Edit', 'Toolbar', 'Insert', 'Menu', 'Highlight', 'Lazy', 'RowValid', 'ForceCellValid', 'ForceRowValid', 'FullEdit', 'GroupBy', 'Keyboard', 'Span', 'Template'],
  edit: ['Manual', 'Click', 'DBLClick', 'Select', 'AutoClear', 'Insert', 'Remove', 'Revert', 'Status', 'CellDisable', 'RowDisable', 'CellValid', 'RowValid', 'ForceCellValid', 'ForceRowValid', 'Keyboard', 'Footer', 'FooterImmediately', 'Expand', 'Menu', 'Span', 'UniqueSelect', 'CascadingSelect', 'Events', 'Template', 'Full'],
  grid: ['Basic', 'Group', 'Reverse', 'Template', 'Footer', 'Page', 'Proxy', 'PageProxy', 'Edit', 'Toolbar', 'CustomToolbar', 'Dynamic', 'Menu', 'Tree', 'TreeEdit', 'FullQuery', 'FullEdit', 'Span', ],
  scroll: ['Scroll', 'Highlight', 'Keyboard', 'Edit', 'CellValid', 'RowValid', 'PartialLoad', 'FullPartialLoad', 'Template', 'ForceCellValid', 'ForceRowValid', 'ScrollRows', 'ScrollCols', 'ScrollFullRows', 'ScrollFullCols', 'Tree']
}

VueUtil.keys(demos).forEach(function(key) {
  var order = orders[key];
  var demo = demos[key];
  demo.children.sort(function(a, b) {
    if (!order) {
      return 0;
    }
    var aKey = VueUtil.last(a.name.split('.'));
    var bKey = VueUtil.last(b.name.split('.'));
    var aIndex = order.indexOf(aKey) >= 0 ? order.indexOf(aKey) : 999;
    var bIndex = order.indexOf(bKey) >= 0 ? order.indexOf(bKey) : 999;
    return aIndex - bIndex;
  })
})

// 
var XtableDemo = {
  head: {
    label: 'XtableDemo.label',
  },
  // 
  children: [demos.base, demos.advanced, demos.grid, demos.tree, demos.scroll, demos.edit]
};

module.exports = XtableDemo;
