export default {"path":"/XtableDemo/edit/Remove","name":"XtableDemo.edit.Remove","head":{"label":"XtableDemo.edit.Remove.label","description":"XtableDemo.edit.Remove.description"},"samples":[{"id":"XtableDemo.edit.Remove.sample1","label":"XtableDemo.edit.Remove.sample1.label","description":"XtableDemo.edit.Remove.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"insertEvent()\">1</vue-button>\n    <vue-button @click=\"$refs.xTable.remove(tableData[1])\">2</vue-button>\n    <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n    <vue-button @click=\"getRemoveEvent\"></vue-button>\n    <vue-button @click=\"getSelectionEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  ref=\"xTable\"\n  border\n  show-overflow\n  :data=\"tableData\"\n  :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n  <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 6)\n    this.tableData = list\n  },\n\n  methods: {\n    insertEvent (row) {\n      let record = {\n        sex: '1'\n      }\n      this.$refs.xTable.insertAt(record, row)\n        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'sex'))\n    },\n    getRemoveEvent () {\n      let removeRecords = this.$refs.xTable.getRemoveRecords()\n      this.$alert({ message: removeRecords.length })\n    },\n    getSelectionEvent () {\n      let removeRecords = this.$refs.xTable.getSelectRecords()\n      this.$alert({ message: removeRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent()\">1</vue-button>\n        <vue-button @click=\"$refs.xTable.remove(tableData[1])\">2</vue-button>\n        <vue-button @click=\"$refs.xTable.removeSelecteds()\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getSelectionEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      ref=\"xTable\"\n      border\n      show-overflow\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'cell'}\">\n      <vue-xtable-column type=\"checkbox\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 6)
    this.tableData = list
  },

  methods: {
    insertEvent (row) {
      let record = {
        sex: '1'
      }
      this.$refs.xTable.insertAt(record, row)
        .then(({ row }) => this.$refs.xTable.setActiveCell(row, 'sex'))
    },
    getRemoveEvent () {
      let removeRecords = this.$refs.xTable.getRemoveRecords()
      this.$alert({ message: removeRecords.length })
    },
    getSelectionEvent () {
      let removeRecords = this.$refs.xTable.getSelectRecords()
      this.$alert({ message: removeRecords.length })
    }
  }
