export default {"path":"/XtableDemo/edit/Menu","name":"XtableDemo.edit.Menu","head":{"label":"XtableDemo.edit.Menu.label","description":"XtableDemo.edit.Menu.description"},"samples":[{"id":"XtableDemo.edit.Menu.sample1","label":"XtableDemo.edit.Menu.sample1.label","description":"XtableDemo.edit.Menu.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"getInsertEvent\"></vue-button>\n    <vue-button @click=\"getRemoveEvent\"></vue-button>\n    <vue-button @click=\"getUpdateEvent\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  border\n  resizable\n  show-overflow\n  ref=\"xTable\"\n  height=\"400\"\n  :loading=\"loading\"\n  :data=\"tableData\"\n  :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, visibleMethod:visibleMethod}\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n  @context-menu-click=\"contextMenuClickEvent\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      tableData: [],\n      headerMenus: [\n        [\n          {\n            code: 'hideColumn',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'showAllColumn',\n            name: '',\n            disabled: false\n          }\n        ]\n      ],\n      bodyMenus: [\n        [\n          {\n            code: 'copy',\n            name: '',\n            prefixIcon: 'fa fa-copy',\n            disabled: false\n          },\n          {\n            code: 'reload',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'insertAt',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'remove',\n            name: '',\n            disabled: false\n          },\n          {\n            code: 'save',\n            name: '',\n            prefixIcon: 'fa fa-save',\n            disabled: false\n          }\n        ]\n      ],\n\n    }\n  },\n  created () {\n    this.findList()\n  },\n\n  methods: {\n    findList () {\n      this.loading = true\n      this.tableData = []\n      setTimeout(() => {\n        this.tableData = window.MOCK_DATA_LIST.slice(0, 5)\n        this.loading = false\n      }, 500)\n    },\n    insertEvent (row, column) {\n      let xTable = this.$refs.xTable\n      xTable.insertAt(null, row || -1)\n        .then(({ row }) => xTable.setActiveCell(row, column ? column.property : 'name'))\n    },\n    visibleMethod ({ options, column }) {\n      let isDisabled = !column\n      options.forEach(list => {\n        list.forEach(item => {\n          item.disabled = isDisabled\n        })\n      })\n      return true\n    },\n    contextMenuClickEvent ({ menu, row, column }) {\n      let xTable = this.$refs.xTable\n      switch (menu.code) {\n        case 'hideColumn':\n          xTable.hideColumn(column)\n          break\n        case 'showAllColumn':\n          xTable.resetCustoms()\n          break\n        case 'copy':\n          if (VueUtil.clipboard.copy(row[column.property])) {\n            this.$notify.success({ message:''});\n          }\n          break\n        case 'reload':\n          this.findList()\n          break\n        case 'insertAt':\n          this.insertEvent(row, column)\n          break\n        case 'remove':\n          xTable.remove(row)\n          break\n        case 'save':\n          this.$notify.success({ message:''});\n          this.findList()\n          break\n      }\n    },\n    getInsertEvent () {\n      let insertRecords = this.$refs.xTable.getInsertRecords()\n      this.$alert({ message: insertRecords.length })\n    },\n    getRemoveEvent () {\n      let removeRecords = this.$refs.xTable.getRemoveRecords()\n      this.$alert({ message: removeRecords.length })\n    },\n    getUpdateEvent () {\n      let updateRecords = this.$refs.xTable.getUpdateRecords()\n      this.$alert({ message: updateRecords.length })\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"getInsertEvent\"></vue-button>\n        <vue-button @click=\"getRemoveEvent\"></vue-button>\n        <vue-button @click=\"getUpdateEvent\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      show-overflow\n      ref=\"xTable\"\n      height=\"400\"\n      :loading=\"loading\"\n      :data=\"tableData\"\n      :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, visibleMethod:visibleMethod}\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\"\n      @context-menu-click=\"contextMenuClickEvent\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      tableData: [],
      headerMenus: [
        [
          {
            code: 'hideColumn',
            name: '',
            disabled: false
          },
          {
            code: 'showAllColumn',
            name: '',
            disabled: false
          }
        ]
      ],
      bodyMenus: [
        [
          {
            code: 'copy',
            name: '',
            prefixIcon: 'fa fa-copy',
            disabled: false
          },
          {
            code: 'reload',
            name: '',
            disabled: false
          },
          {
            code: 'insertAt',
            name: '',
            disabled: false
          },
          {
            code: 'remove',
            name: '',
            disabled: false
          },
          {
            code: 'save',
            name: '',
            prefixIcon: 'fa fa-save',
            disabled: false
          }
        ]
      ],

    }
  },
  created () {
    this.findList()
  },

  methods: {
    findList () {
      this.loading = true
      this.tableData = []
      setTimeout(() => {
        this.tableData = window.MOCK_DATA_LIST.slice(0, 5)
        this.loading = false
      }, 500)
    },
    insertEvent (row, column) {
      let xTable = this.$refs.xTable
      xTable.insertAt(null, row || -1)
        .then(({ row }) => xTable.setActiveCell(row, column ? column.property : 'name'))
    },
    visibleMethod ({ options, column }) {
      let isDisabled = !column
      options.forEach(list => {
        list.forEach(item => {
          item.disabled = isDisabled
        })
      })
      return true
    },
    contextMenuClickEvent ({ menu, row, column }) {
      let xTable = this.$refs.xTable
      switch (menu.code) {
        case 'hideColumn':
          xTable.hideColumn(column)
          break
        case 'showAllColumn':
          xTable.resetCustoms()
          break
        case 'copy':
          if (VueUtil.clipboard.copy(row[column.property])) {
            this.$notify.success({ message:''});
          }
          break
        case 'reload':
          this.findList()
          break
        case 'insertAt':
          this.insertEvent(row, column)
          break
        case 'remove':
          xTable.remove(row)
          break
        case 'save':
          this.$notify.success({ message:''});
          this.findList()
          break
      }
    },
    getInsertEvent () {
      let insertRecords = this.$refs.xTable.getInsertRecords()
      this.$alert({ message: insertRecords.length })
    },
    getRemoveEvent () {
      let removeRecords = this.$refs.xTable.getRemoveRecords()
      this.$alert({ message: removeRecords.length })
    },
    getUpdateEvent () {
      let updateRecords = this.$refs.xTable.getUpdateRecords()
      this.$alert({ message: updateRecords.length })
    }
  }
}}],"styles":""}