export default {"path":"/XtableDemo/grid/CustomToolbar","name":"XtableDemo.grid.CustomToolbar","head":{"label":"XtableDemo.grid.CustomToolbar.label","description":"XtableDemo.grid.CustomToolbar.description"},"samples":[{"id":"XtableDemo.grid.CustomToolbar.sample1","label":"XtableDemo.grid.CustomToolbar.sample1.label","description":"XtableDemo.grid.CustomToolbar.sample1.description","code":"<vue-xgrid\n  border\n  resizable\n  ref=\"xGrid\"\n  height=\"530\"\n  :pager-config=\"tablePage\"\n  :proxy-config=\"tableProxy\"\n  :columns=\"tableColumn\"\n  :toolbar=\"toolbar\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\">\n  <template v-slot:buttons>\n    <input type=\"text\"/>\n    <button></button>\n    <vue-button @click=\"$refs.xGrid.commitProxy('reload')\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.commitProxy('insert_actived')\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.commitProxy('mark_cancel')\">/</vue-button>\n    <vue-button @click=\"$refs.xGrid.commitProxy('save')\"></vue-button>\n    <vue-button @click=\"$refs.xGrid.exportData()\">.csv</vue-button>\n  </template>\n</vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tablePage: {\n        pageSize: 15\n      },\n      tableProxy: {\n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          // page  { pageSize, currentPage }\n          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),\n          // body  { removeRecords }\n          delete: ({ body }) => Vue.http.post('/api/user/save', body),\n          // body  { insertRecords, updateRecords, removeRecords, pendingRecords }\n          save: ({ body }) => Vue.http.post('/api/user/save', body)\n        }\n      },\n      toolbar: {\n        id: 'toolbar_demo_2',\n        refresh: true,\n        setting: {\n          storage: true\n        }\n      },\n      tableColumn: [\n        { type: 'checkbox', width: 50 },\n        { type: 'index', width: 60 },\n        { field: 'name', title: 'Name', editRender: { name: 'input' } },\n        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },\n        { field: 'role', title: 'Role', editRender: { name: 'input' } },\n        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }\n      ],\n\n    }\n  },\n\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      ref=\"xGrid\"\n      height=\"530\"\n      :pager-config=\"tablePage\"\n      :proxy-config=\"tableProxy\"\n      :columns=\"tableColumn\"\n      :toolbar=\"toolbar\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\">\n      <template v-slot:buttons>\n        <input type=\"text\"/>\n        <button></button>\n        <vue-button @click=\"$refs.xGrid.commitProxy('reload')\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.commitProxy('insert_actived')\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.commitProxy('mark_cancel')\">/</vue-button>\n        <vue-button @click=\"$refs.xGrid.commitProxy('save')\"></vue-button>\n        <vue-button @click=\"$refs.xGrid.exportData()\">.csv</vue-button>\n      </template>\n    </vue-xgrid></div>","parameter": {
  data () {
    return {
      tablePage: {
        pageSize: 15
      },
      tableProxy: {
        props: {
          result: 'data.result',
          total: 'data.page.total'
        },
        ajax: {
          // page  { pageSize, currentPage }
          query: ({ page }) => Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`),
          // body  { removeRecords }
          delete: ({ body }) => Vue.http.post('/api/user/save', body),
          // body  { insertRecords, updateRecords, removeRecords, pendingRecords }
          save: ({ body }) => Vue.http.post('/api/user/save', body)
        }
      },
      toolbar: {
        id: 'toolbar_demo_2',
        refresh: true,
        setting: {
          storage: true
        }
      },
      tableColumn: [
        { type: 'checkbox', width: 50 },
        { type: 'index', width: 60 },
        { field: 'name', title: 'Name', editRender: { name: 'input' } },
        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },
        { field: 'role', title: 'Role', editRender: { name: 'input' } },
        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }
      ],

    }
  },

