export default {"path":"/XtableDemo/base/Style","name":"XtableDemo.base.Style","head":{"label":"XtableDemo.base.Style.label","description":"XtableDemo.base.Style.description"},"samples":[{"id":"XtableDemo.base.Style.sample1","label":"XtableDemo.base.Style.sample1.label","description":"XtableDemo.base.Style.sample1.description","code":"<vue-xtable\n  border\n  class=\"mytable-style\"\n  :header-cell-class-name=\"headerCellClassName\"\n  :row-class-name=\"rowClassName\"\n  :cell-class-name=\"cellClassName\"\n  :data=\"tableData\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n  },\n\n  methods: {\n    headerCellClassName ({ column, columnIndex }) {\n      if (column.property === 'name') {\n        return 'col-blue'\n      }\n    },\n    rowClassName ({ row, rowIndex }) {\n      if (VueUtil.includes([2, 3, 5], rowIndex)) {\n        return 'row-green'\n      }\n    },\n    cellClassName ({ row, rowIndex, column, columnIndex }) {\n      if (column.property === 'sex') {\n        if (row.sex >= '1') {\n          return 'col-red'\n        } else if (row.age === 26) {\n          return 'col-orange'\n        }\n      }\n    },\n  }\n})\n</script>\n\n<style>\n.mytable-style.vue-xtable-table .vue-xtable-body--row.row-green {\n  background-color: #187;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-header--column.col-blue {\n  background-color: #2db7f5;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-body--column.col-red {\n  background-color: red;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-body--column.col-orange {\n  background-color: #f60;\n  color: #fff;\n}\n</style>","template":"<div class=\"source\">    <vue-xtable\n      border\n      class=\"mytable-style\"\n      :header-cell-class-name=\"headerCellClassName\"\n      :row-class-name=\"rowClassName\"\n      :cell-class-name=\"cellClassName\"\n      :data=\"tableData\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
    }
  },
  created () {
    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)
  },

  methods: {
    headerCellClassName ({ column, columnIndex }) {
      if (column.property === 'name') {
        return 'col-blue'
      }
    },
    rowClassName ({ row, rowIndex }) {
      if (VueUtil.includes([2, 3, 5], rowIndex)) {
        return 'row-green'
      }
    },
    cellClassName ({ row, rowIndex, column, columnIndex }) {
      if (column.property === 'sex') {
        if (row.sex >= '1') {
          return 'col-red'
        } else if (row.age === 26) {
          return 'col-orange'
        }
      }
    },
  }
}},{"id":"XtableDemo.base.Style.sample2","label":"XtableDemo.base.Style.sample2.label","description":"XtableDemo.base.Style.sample2.description","code":"<vue-xtable\n  border\n  class=\"mytable-style\"\n  :cell-class-name=\"cellClassName2\"\n  :data=\"tableData\"\n  @cell-click=\"cellClickEvent2\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      selectRow: null,\n      selectColumn: null,\n    }\n  },\n  created () {\n    this.tableData = window.MOCK_DATA_LIST.slice(0, 6)\n  },\n\n  methods: {\n    cellClassName2 ({ row, column }) {\n      if (row === this.selectRow & column === this.selectColumn) {\n        return 'col-orange'\n      }\n    },\n    cellClickEvent2 ({ row, column }) {\n      this.selectRow = row\n      this.selectColumn = column\n    }\n  }\n})\n</script>\n\n<style>\n.mytable-style.vue-xtable-table .vue-xtable-body--row.row-green {\n  background-color: #187;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-header--column.col-blue {\n  background-color: #2db7f5;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-body--column.col-red {\n  background-color: red;\n  color: #fff;\n}\n.mytable-style.vue-xtable-table .vue-xtable-body--column.col-orange {\n  background-color: #f60;\n  color: #fff;\n}\n</style>","template":"<div class=\"source\">    <vue-xtable\n      border\n      class=\"mytable-style\"\n      :cell-class-name=\"cellClassName2\"\n      :data=\"tableData\"\n      @cell-click=\"cellClickEvent2\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\"></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"address\" title=\"Address\" show-overflow></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
