export default {"path":"/XtableDemo/grid/FullEdit","name":"XtableDemo.grid.FullEdit","head":{"label":"XtableDemo.grid.FullEdit.label","description":"XtableDemo.grid.FullEdit.description"},"samples":[{"id":"XtableDemo.grid.FullEdit.sample1","label":"XtableDemo.grid.FullEdit.sample1.label","description":"XtableDemo.grid.FullEdit.sample1.description","code":"<vue-xgrid\n  border\n  resizable\n  highlight-hover-row\n  remote-filter\n  ref=\"xGrid\"\n  height=\"530\"\n  row-id=\"id\"\n  :pager-config=\"{pageSize: 15}\"\n  :toolbar=\"tableToolbar\"\n  :proxy-config=\"tableProxy\"\n  :columns=\"tableColumn\"\n  :checkbox-config=\"{reserve: true}\"\n  :edit-rules=\"validRules\"\n  :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\">\n</vue-xgrid>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      validRules: {\n        name: [\n          { required: true, message: '' },\n          { min: 3, max: 50, message: ' 3  50 ' }\n        ],\n        role: [\n          { required: true, message: '' }\n        ]\n      },\n      tableProxy: {\n        index: true, // \n        sort: true, // \n        filter: true, // \n        props: {\n          result: 'data.result',\n          total: 'data.page.total'\n        },\n        ajax: {\n          //  Promise API fetchjqueryaxiosxe-ajax\n          query: ({ page, sort, filters }) => {\n            // \n            let formData = {\n              sort: sort.property,\n              order: sort.order\n            }\n            // \n            filters.forEach(({ column, property, values }) => {\n              formData[property] = values.join(',')\n            })\n            return Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`, formData)\n          },\n          delete: ({ body }) => Vue.http.post('/api/user/save', body),\n          save: ({ body }) => Vue.http.post('/api/user/save', body)\n        }\n      },\n      tableToolbar: {\n        id: 'full_edit_1',\n        buttons: [\n          { code: 'reload', name: '', disabled: false },\n          { code: 'insert_actived', name: '', disabled: false },\n          {\n            code: 'mark_cancel',\n            name: '',\n            disabled: false,\n            dropdowns: [\n              { code: 'delete_selection', name: '', disabled: false },\n              { code: 'remove_selection', name: '', disabled: false }\n            ]\n          },\n          { code: 'save', name: '', disabled: false },\n          {\n            name: '',\n            disabled: false,\n            dropdowns: [\n              { code: 'reset_custom', name: '', disabled: false }\n            ]\n          },\n          {\n            name: '/1',\n            dropdowns: [\n              { code: 'import', name: '', disabled: false },\n              { code: 'export', name: ' CSV', params: { type: 'csv' }, disabled: false },\n              { code: 'export', name: ' XML', params: { type: 'xml' }, disabled: false },\n              { code: 'export', name: ' HTML', params: { type: 'html' }, disabled: false },\n              { code: 'export', name: ' TXT', params: { type: 'txt' }, disabled: false }\n            ]\n          },\n          {\n            name: '/2',\n            dropdowns: [\n              { code: 'open_import', name: '', disabled: false },\n              { code: 'open_export', name: '', disabled: false }\n            ]\n          },\n        ],\n        refresh: true,\n        import: true,\n        export: true,\n        resizable: {\n          storage: true\n        },\n        setting: {\n          storage: true,\n          checkMethod: this.checkColumnMethod\n        }\n      },\n      tableColumn: [\n        { type: 'checkbox', width: 50 },\n        { type: 'index', title: '', width: 80 },\n        { field: 'name', title: 'Name', remoteSort: true, editRender: { name: 'input' } },\n        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },\n        { field: 'sex', title: 'Sex', editRender: { name: 'select', options: [] } },\n        {\n          field: 'role',\n          title: 'Role',\n          remoteSort: true,\n          width: 200,\n          filters: [\n            { label: '', value: '' },\n            { label: '', value: '' },\n            { label: '', value: '' }\n          ],\n          filterMultiple: false,\n          editRender: { name: 'input' }\n        },\n        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }\n      ],\n\n    }\n  },\n  created () {\n    this.findSexList()\n  },\n\n  methods: {\n    findSexList () {\n      return Vue.http.get('/api/conf/sex/list').then(data => {\n        // \n        let column = this.$refs.xGrid.getColumnByField('sex')\n        column.editRender.options = data.data\n      })\n    },\n    checkColumnMethod ({ column }) {\n      if (VueUtil.includes(['nickname', 'role'], column.property)) {\n        return false\n      }\n      return true\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xgrid\n      border\n      resizable\n      highlight-hover-row\n      remote-filter\n      ref=\"xGrid\"\n      height=\"530\"\n      row-id=\"id\"\n      :pager-config=\"{pageSize: 15}\"\n      :toolbar=\"tableToolbar\"\n      :proxy-config=\"tableProxy\"\n      :columns=\"tableColumn\"\n      :checkbox-config=\"{reserve: true}\"\n      :edit-rules=\"validRules\"\n      :edit-config=\"{trigger: 'click', mode: 'row', showStatus: true}\">\n    </vue-xgrid></div>","parameter": {
  data () {
    return {
      validRules: {
        name: [
          { required: true, message: '' },
          { min: 3, max: 50, message: ' 3  50 ' }
        ],
        role: [
          { required: true, message: '' }
        ]
      },
      tableProxy: {
        index: true, // 
        sort: true, // 
        filter: true, // 
        props: {
          result: 'data.result',
          total: 'data.page.total'
        },
        ajax: {
          //  Promise API fetchjqueryaxiosxe-ajax
          query: ({ page, sort, filters }) => {
            // 
            let formData = {
              sort: sort.property,
              order: sort.order
            }
            // 
            filters.forEach(({ column, property, values }) => {
              formData[property] = values.join(',')
            })
            return Vue.http.get(`/api/user/page/list/${page.pageSize}/${page.currentPage}`, formData)
          },
          delete: ({ body }) => Vue.http.post('/api/user/save', body),
          save: ({ body }) => Vue.http.post('/api/user/save', body)
        }
      },
      tableToolbar: {
        id: 'full_edit_1',
        buttons: [
          { code: 'reload', name: '', disabled: false },
          { code: 'insert_actived', name: '', disabled: false },
          {
            code: 'mark_cancel',
            name: '',
            disabled: false,
            dropdowns: [
              { code: 'delete_selection', name: '', disabled: false },
              { code: 'remove_selection', name: '', disabled: false }
            ]
          },
          { code: 'save', name: '', disabled: false },
          {
            name: '',
            disabled: false,
            dropdowns: [
              { code: 'reset_custom', name: '', disabled: false }
            ]
          },
          {
            name: '/1',
            dropdowns: [
              { code: 'import', name: '', disabled: false },
              { code: 'export', name: ' CSV', params: { type: 'csv' }, disabled: false },
              { code: 'export', name: ' XML', params: { type: 'xml' }, disabled: false },
              { code: 'export', name: ' HTML', params: { type: 'html' }, disabled: false },
              { code: 'export', name: ' TXT', params: { type: 'txt' }, disabled: false }
            ]
          },
          {
            name: '/2',
            dropdowns: [
              { code: 'open_import', name: '', disabled: false },
              { code: 'open_export', name: '', disabled: false }
            ]
          },
        ],
        refresh: true,
        import: true,
        export: true,
        resizable: {
          storage: true
        },
        setting: {
          storage: true,
          checkMethod: this.checkColumnMethod
        }
      },
      tableColumn: [
        { type: 'checkbox', width: 50 },
        { type: 'index', title: '', width: 80 },
        { field: 'name', title: 'Name', remoteSort: true, editRender: { name: 'input' } },
        { field: 'nickname', title: 'Nickname', editRender: { name: 'input' } },
        { field: 'sex', title: 'Sex', editRender: { name: 'select', options: [] } },
        {
          field: 'role',
          title: 'Role',
          remoteSort: true,
          width: 200,
          filters: [
            { label: '', value: '' },
            { label: '', value: '' },
            { label: '', value: '' }
          ],
          filterMultiple: false,
          editRender: { name: 'input' }
        },
        { field: 'describe', title: 'Describe', showOverflow: true, editRender: { name: 'input' } }
      ],

    }
  },
  created () {
    this.findSexList()
  },

  methods: {
    findSexList () {
      return Vue.http.get('/api/conf/sex/list').then(data => {
        // 
        let column = this.$refs.xGrid.getColumnByField('sex')
        column.editRender.options = data.data
      })
    },
    checkColumnMethod ({ column }) {
      if (VueUtil.includes(['nickname', 'role'], column.property)) {
        return false
      }
      return true
    }
  }
}}],"styles":""}