export default {"path":"/XtableDemo/scroll/Tree","name":"XtableDemo.scroll.Tree","head":{"label":"XtableDemo.scroll.Tree.label","description":"XtableDemo.scroll.Tree.description"},"samples":[{"id":"XtableDemo.scroll.Tree.sample1","label":"XtableDemo.scroll.Tree.sample1.label","description":"XtableDemo.scroll.Tree.sample1.description","code":"<vue-toolbar>\n  <template v-slot:buttons>\n    <vue-button @click=\"setAllTreeExpansion(true)\"></vue-button>\n    <vue-button @click=\"setAllTreeExpansion(false)\"></vue-button>\n  </template>\n</vue-toolbar>\n\n<vue-xtable\n  ref=\"xTable\"\n  resizable\n  show-overflow\n  height=\"500\"\n  row-id=\"id\"\n  :loading=\"loading\">\n  <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"\">\n    <template v-slot=\"scope\">\n      <span :class=\"['level-' + scope.row.level]\" :style=\"{paddingLeft: scope.row.level * treeIndent + 'px'}\">\n        <i\n          class=\"virtual-tree-icon\"\n          :class=\"['vue-icon-arrow-' + (scope.row.expand ? 'down' : 'right') , {visible: scope.row.children && scope.row.children.length}]\"\n          @click=\"toggleTreeExpansion(scope.row)\"></i>\n        <span>{{ scope.row.name }}</span>\n      </span>\n    </template>\n  </vue-xtable-column>\n  <vue-xtable-column field=\"id\" title=\"\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      loading: false,\n      treeConfig: {\n        children: 'children'\n      },\n      treeIndent: 16,\n\n    }\n  },\n  created () {\n    this.findCityAll()\n  },\n\n  methods: {\n    findCityAll () {\n      this.loading = true\n      Vue.http.get('/api/conf/city/all').then(data => {\n        const list = this.toVirtualTree(data.data)\n        this.$refs.xTable.reloadData(list)\n        this.loading = false\n      })\n    },\n    setAllTreeExpansion (expand) {\n      let list = this.virtualAllExpand(expand)\n      this.$refs.xTable.reloadData(list)\n    },\n    toggleTreeExpansion (row) {\n      let list = this.virtualExpand(row, !row.expand)\n      this.$refs.xTable.loadData(list)\n    },\n    // -\n    toVirtualTree (treeData) {\n      VueUtil.eachTree(treeData, (item, index, obj, paths, parent, nodes) => {\n        item.expand = false\n        item.level = nodes.length - 1\n      })\n      this.treeData = treeData.slice(0)\n      this.tableData = treeData.slice(0)\n      return treeData\n    },\n    // -/\n    virtualExpand (row, expand) {\n      if (row.expand !== expand) {\n        let children = row.children\n        if (children && children.length) {\n          let tableData = this.tableData\n          if (row.expand) {\n            // \n            let childList = []\n            VueUtil.eachTree(children, item => {\n              childList.push(item)\n            }, this.treeConfig)\n            tableData = tableData.filter(item => childList.indexOf(item) === -1)\n          } else {\n            // \n            let expandList = []\n            let rowIndex = tableData.indexOf(row)\n            if (rowIndex === -1) {\n              throw new Error('')\n            }\n            VueUtil.eachTree(children, (item, index, obj, paths, parent, nodes) => {\n              if (!parent || parent.expand) {\n                expandList.push(item)\n              }\n            }, this.treeConfig)\n            tableData.splice.apply(tableData, [rowIndex + 1, 0].concat(expandList))\n          }\n          row.expand = !row.expand\n          this.tableData = tableData\n        }\n      }\n      return this.tableData\n    },\n    // -/\n    virtualAllExpand (expand) {\n      VueUtil.eachTree(this.treeData, row => {\n        this.virtualExpand(row, expand)\n      }, this.treeConfig)\n      return this.tableData\n    }\n  }\n})\n</script>\n\n<style>\n.virtual-tree-icon {\n  visibility: hidden;\n  cursor: pointer;\n  margin-right: 4px;\n}\n.virtual-tree-icon.visible {\n  visibility: visible;\n}\n</style>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"setAllTreeExpansion(true)\"></vue-button>\n        <vue-button @click=\"setAllTreeExpansion(false)\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      ref=\"xTable\"\n      resizable\n      show-overflow\n      height=\"500\"\n      row-id=\"id\"\n      :loading=\"loading\">\n      <vue-xtable-column type=\"index\" width=\"100\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"\">\n        <template v-slot=\"scope\">\n          <span :class=\"['level-' + scope.row.level]\" :style=\"{paddingLeft: scope.row.level * treeIndent + 'px'}\">\n            <i\n              class=\"virtual-tree-icon\"\n              :class=\"['vue-icon-arrow-' + (scope.row.expand ? 'down' : 'right') , {visible: scope.row.children && scope.row.children.length}]\"\n              @click=\"toggleTreeExpansion(scope.row)\"></i>\n            <span>{{ scope.row.name }}</span>\n          </span>\n        </template>\n      </vue-xtable-column>\n      <vue-xtable-column field=\"id\" title=\"\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      loading: false,
      treeConfig: {
        children: 'children'
      },
      treeIndent: 16,

    }
  },
  created () {
    this.findCityAll()
  },

  methods: {
    findCityAll () {
      this.loading = true
      Vue.http.get('/api/conf/city/all').then(data => {
        const list = this.toVirtualTree(data.data)
        this.$refs.xTable.reloadData(list)
        this.loading = false
      })
    },
    setAllTreeExpansion (expand) {
      let list = this.virtualAllExpand(expand)
      this.$refs.xTable.reloadData(list)
    },
    toggleTreeExpansion (row) {
      let list = this.virtualExpand(row, !row.expand)
      this.$refs.xTable.loadData(list)
    },
    // -
    toVirtualTree (treeData) {
      VueUtil.eachTree(treeData, (item, index, obj, paths, parent, nodes) => {
        item.expand = false
        item.level = nodes.length - 1
      })
      this.treeData = treeData.slice(0)
      this.tableData = treeData.slice(0)
      return treeData
    },
    // -/
    virtualExpand (row, expand) {
      if (row.expand !== expand) {
        let children = row.children
        if (children && children.length) {
          let tableData = this.tableData
          if (row.expand) {
            // 
            let childList = []
            VueUtil.eachTree(children, item => {
              childList.push(item)
            }, this.treeConfig)
            tableData = tableData.filter(item => childList.indexOf(item) === -1)
          } else {
            // 
            let expandList = []
            let rowIndex = tableData.indexOf(row)
            if (rowIndex === -1) {
              throw new Error('')
            }
            VueUtil.eachTree(children, (item, index, obj, paths, parent, nodes) => {
              if (!parent || parent.expand) {
                expandList.push(item)
              }
            }, this.treeConfig)
            tableData.splice.apply(tableData, [rowIndex + 1, 0].concat(expandList))
          }
          row.expand = !row.expand
          this.tableData = tableData
        }
      }
      return this.tableData
    },
    // -/
    virtualAllExpand (expand) {
      VueUtil.eachTree(this.treeData, row => {
        this.virtualExpand(row, expand)
      }, this.treeConfig)
      return this.tableData
    }
  }
}}],"styles":".virtual-tree-icon {\n  visibility: hidden;\n  cursor: pointer;\n  margin-right: 4px;\n}\n.virtual-tree-icon.visible {\n  visibility: visible;\n}"}