eList()\n    })\n  },\n\n  methods: {\n    insertEvent () {\n      let record = {\n\n      }\n      this.$refs.xTable.insert(record)\n    },\n    updateRoleList () {\n      // \n      let { fullData } = this.$refs.xTable.getTableData()\n      this.roleList.forEach(item => {\n        if (item.value) {\n          // \n          item.disabled = fullData.some(row => row.role1 === item.value)\n        }\n      })\n    },\n    roleChangeEvent ({ row }, evnt) {\n      //  select  v-model \n      row.role1 = evnt.target.value\n      this.updateRoleList()\n    },\n    editActivedEvent ({ row }) {\n      // \n      this.updateRoleList()\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-toolbar>\n      <template v-slot:buttons>\n        <vue-button @click=\"insertEvent()\"></vue-button>\n      </template>\n    </vue-toolbar>\n\n    <vue-xtable\n      border\n      resizable\n      ref=\"xTable\"\n      max-height=\"400\"\n      :data=\"tableData\"\n      :edit-config=\"{trigger: 'click', mode: 'row'}\"\n      @edit-actived=\"editActivedEvent\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" :edit-render=\"{name: 'input'}\"></vue-xtable-column>\n      <vue-xtable-column field=\"role1\" title=\"Role\" :edit-render=\"{name: 'select', options: roleList, events: {change: roleChangeEvent}}\"></vue-xtable-column>\n      <vue-xtable-column field=\"date12\" title=\"Date\" :edit-render=\"{name: 'input', attrs: {type: 'date'}}\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
