export default {"path":"/XtableDemo/advanced/MenuPrivilege","name":"XtableDemo.advanced.MenuPrivilege","head":{"label":"XtableDemo.advanced.MenuPrivilege.label","description":"XtableDemo.advanced.MenuPrivilege.description"},"samples":[{"id":"XtableDemo.advanced.MenuPrivilege.sample1","label":"XtableDemo.advanced.MenuPrivilege.sample1.label","description":"XtableDemo.advanced.MenuPrivilege.sample1.description","code":"<vue-xtable\n  border\n  show-footer\n  highlight-current-row\n  highlight-current-column\n  ref=\"xTable\"\n  :footer-method=\"footerMethod\"\n  :data=\"tableData\"\n  :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, footer: {options: footerMenus}, visibleMethod: visibleMethod}\"\n  @header-cell-context-menu=\"headerCellContextMenuEvent\"\n  @cell-context-menu=\"cellContextMenuEvent\"\n  @context-menu-click=\"contextMenuClickEvent\">\n  <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n  <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n  <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n  <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n  <vue-xtable-column field=\"time\" title=\"Time\"></vue-xtable-column>\n</vue-xtable>\n\n<script>\nnew Vue({\n  data () {\n    return {\n      tableData: [],\n      headerMenus: [\n        [\n          {\n            code: 'exportAll',\n            name: '.csv',\n            visible: true,\n            disabled: false\n          }\n        ]\n      ],\n      bodyMenus: [\n        [\n          {\n            code: 'copy',\n            name: '',\n            prefixIcon: 'fa fa-copy',\n            visible: true,\n            disabled: false\n          },\n          {\n            code: 'clear',\n            name: '',\n            prefixIcon: 'fa fa-copy',\n            visible: true,\n            disabled: false\n          }\n        ],\n        [\n          {\n            code: 'remove',\n            name: '',\n            visible: true,\n            disabled: false\n          },\n          {\n            code: 'filter',\n            name: '',\n            visible: true,\n            disabled: false,\n            children: [\n              {\n                code: 'clearFilter',\n                name: '',\n                visible: true,\n                disabled: false\n              },\n              {\n                code: 'filterSelect',\n                name: '',\n                visible: true,\n                disabled: false\n              }\n            ]\n          },\n          {\n            code: 'sort',\n            name: '',\n            visible: true,\n            disabled: false,\n            children: [\n              {\n                code: 'clearSort',\n                name: '',\n                visible: true,\n                disabled: false\n              },\n              {\n                code: 'sortAsc',\n                name: '',\n                visible: true,\n                disabled: false\n              },\n              {\n                code: 'sortDesc',\n                name: '',\n                visible: true,\n                disabled: false\n              }\n            ]\n          },\n          {\n            code: 'print',\n            name: '',\n            disabled: true\n          }\n        ]\n      ],\n      footerMenus: [\n        [\n          {\n            code: 'clearAll',\n            name: '',\n            visible: true,\n            disabled: false\n          }\n        ]\n      ],\n\n    }\n  },\n  created () {\n    let list = window.MOCK_DATA_LIST.slice(0, 6)\n    this.tableData = list\n  },\n\n  methods: {\n    headerCellContextMenuEvent ({ column }) {\n      this.$refs.xTable.setCurrentColumn(column)\n    },\n    cellContextMenuEvent ({ row }) {\n      this.$refs.xTable.setCurrentRow(row)\n    },\n    visibleMethod ({ type, options, column }) {\n      //  name  age \n      // \n      let isDisabled = !column || column.property !== 'name'\n      let isVisible = column && column.property === 'age'\n      options.forEach(list => {\n        list.forEach(item => {\n          if (VueUtil.includes(['copy', 'remove'], item.code)) {\n            item.disabled = isDisabled\n          }\n          if (VueUtil.includes(['clear', 'filter'], item.code)) {\n            item.visible = isVisible\n          }\n        })\n      })\n      return true\n    },\n    contextMenuClickEvent ({ menu, row, column }) {\n      switch (menu.code) {\n        case 'copy':\n          // \n          if (row && column) {\n            if (VueUtil.clipboard.copy(row[column.property])) {\n              this.$notify.success({ message:''});\n            }\n          }\n          break\n        default:\n          this.$notify({message: ` ${menu.name} `})\n      }\n    },\n    footerMethod ({ columns, data }) {\n      return [\n        columns.map((column, columnIndex) => {\n          if (columnIndex === 0) {\n            return ''\n          }\n          if (VueUtil.includes(['age', 'rate'], column.property)) {\n            return VueUtil.mean(data.map(data => data[column.property]))\n          }\n          return null\n        })\n      ]\n    }\n  }\n})\n</script>","template":"<div class=\"source\">    <vue-xtable\n      border\n      show-footer\n      highlight-current-row\n      highlight-current-column\n      ref=\"xTable\"\n      :footer-method=\"footerMethod\"\n      :data=\"tableData\"\n      :context-menu=\"{header: {options: headerMenus}, body: {options: bodyMenus}, footer: {options: footerMenus}, visibleMethod: visibleMethod}\"\n      @header-cell-context-menu=\"headerCellContextMenuEvent\"\n      @cell-context-menu=\"cellContextMenuEvent\"\n      @context-menu-click=\"contextMenuClickEvent\">\n      <vue-xtable-column type=\"index\" width=\"60\"></vue-xtable-column>\n      <vue-xtable-column field=\"name\" title=\"Name\" sortable></vue-xtable-column>\n      <vue-xtable-column field=\"sex\" title=\"Sex\"></vue-xtable-column>\n      <vue-xtable-column field=\"age\" title=\"Age\"></vue-xtable-column>\n      <vue-xtable-column field=\"time\" title=\"Time\"></vue-xtable-column>\n    </vue-xtable></div>","parameter": {
  data () {
    return {
      tableData: [],
      headerMenus: [
        [
          {
            code: 'exportAll',
            name: '.csv',
            visible: true,
            disabled: false
          }
        ]
      ],
      bodyMenus: [
        [
          {
            code: 'copy',
            name: '',
            prefixIcon: 'fa fa-copy',
            visible: true,
            disabled: false
          },
          {
            code: 'clear',
            name: '',
            prefixIcon: 'fa fa-copy',
            visible: true,
            disabled: false
          }
        ],
        [
          {
            code: 'remove',
            name: '',
            visible: true,
            disabled: false
          },
          {
            code: 'filter',
            name: '',
            visible: true,
            disabled: false,
            children: [
              {
                code: 'clearFilter',
                name: '',
                visible: true,
                disabled: false
              },
              {
                code: 'filterSelect',
                name: '',
                visible: true,
                disabled: false
              }
            ]
          },
          {
            code: 'sort',
            name: '',
            visible: true,
            disabled: false,
            children: [
              {
                code: 'clearSort',
                name: '',
                visible: true,
                disabled: false
              },
              {
                code: 'sortAsc',
                name: '',
                visible: true,
                disabled: false
              },
              {
                code: 'sortDesc',
                name: '',
                visible: true,
                disabled: false
              }
            ]
          },
          {
            code: 'print',
            name: '',
            disabled: true
          }
        ]
      ],
      footerMenus: [
        [
          {
            code: 'clearAll',
            name: '',
            visible: true,
            disabled: false
          }
        ]
      ],

    }
  },
  created () {
    let list = window.MOCK_DATA_LIST.slice(0, 6)
    this.tableData = list
  },

  methods: {
    headerCellContextMenuEvent ({ column }) {
      this.$refs.xTable.setCurrentColumn(column)
    },
    cellContextMenuEvent ({ row }) {
      this.$refs.xTable.setCurrentRow(row)
    },
    visibleMethod ({ type, options, column }) {
      //  name  age 
      // 
      let isDisabled = !column || column.property !== 'name'
      let isVisible = column && column.property === 'age'
      options.forEach(list => {
        list.forEach(item => {
          if (VueUtil.includes(['copy', 'remove'], item.code)) {
            item.disabled = isDisabled
          }
          if (VueUtil.includes(['clear', 'filter'], item.code)) {
            item.visible = isVisible
          }
        })
      })
      return true
    },
    contextMenuClickEvent ({ menu, row, column }) {
      switch (menu.code) {
        case 'copy':
          // 
          if (row && column) {
            if (VueUtil.clipboard.copy(row[column.property])) {
              this.$notify.success({ message:''});
            }
          }
          break
        default:
          this.$notify({message: ` ${menu.name} `})
      }
    },
    footerMethod ({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return ''
          }
          if (VueUtil.includes(['age', 'rate'], column.property)) {
            return VueUtil.mean(data.map(data => data[column.property]))
          }
          return null
        })
      ]
    }
  }
}}],"styles":""}